import BaseService from './base'


class ProviderService extends BaseService {
  get entity () {
    return 'provider'
  }
  
  create(data ) {
    const provider = this.request({ auth: true }).post(`/${this.entity}/create`, data)
    return provider
  }

  get(){
    const providers = this.request({ auth: true }).get(`/${this.entity}`)
    return providers
  }

  update({data, id}) {
    const providers = this.request({ auth: true }).put(`/${this.entity}/action/${id}`, data)
    return providers
  }

  delete(id) {
    const providers = this.request({ auth: true }).delete(`/${this.entity}/action/${id}`)
    return providers
  }

}
  
export default new ProviderService()
