<template>
  <div class="main-content d-flex justify-content-center">
    <div class="position-absolute sticky-top mt-2 mybcrumb">
      <b-breadcrumb :items="items"></b-breadcrumb>
    </div>
    <div class="table-responsive p-3 col-md-7">
      <h2 class="card-header text-white bg-info text-left py-4">
        Create New Provider
      </h2>
      <div class="text-danger pb-2 pl-4 pt-2 text-center" v-show="error.length > 0">
        <span>{{ error }}</span>
      </div>
      <form id="id_create_form" class="bg-white p-4 text-info" method="POST">
        <div class="form-group row">
          <label for="inputemailteam" class="col-sm-2 col-form-label">Name<sup class="text-danger"> *</sup></label>
          <div class="col-sm-9">
            <input id="id_input_email" v-model="name" @change="clearError(name)" type="text" class="form-control"
                   placeholder="Name" />
          </div>
        </div>
        <div class="form-group row">
          <label for="inputnameteam" class="col-sm-2 col-form-label">Phone<sup class="text-danger"> *</sup></label>
          <div class="col-sm-9">
            <input id="id_input_team_name" v-model="phone" @change="clearError(phone)" type="number"
                   class="form-control" placeholder="Phone Number" />
          </div>
        </div>
        <div class="form-group row">
          <label for="inputnameteam" class="col-sm-2 col-form-label">Address<sup class="text-danger"> *</sup></label>
          <div class="col-sm-9">
            <input id="id_input_team_name" v-model="address" @change="clearError(address)" type="text"
                   class="form-control" placeholder="Address" />
          </div>
        </div>
        <div class="form-group row">
          <label for="inputnameteam" class="col-sm-2 col-form-label">Description<sup class="text-danger">
            *</sup></label>
          <div class="col-sm-9">
            <input id="id_input_team_name" v-model="description" @change="clearError(description)" type="text"
                   class="form-control" placeholder="Description" />
          </div>
        </div>
        <div class="form-group row">
          <label for="inputnameteam" class="col-sm-2 col-form-label">Link Web<sup class="text-danger"> *</sup></label>
          <div class="col-sm-9">
            <input id="id_input_team_name" v-model="link" @change="clearError(link)" type="text" class="form-control"
                   placeholder="Link" />
          </div>
        </div>
        <div class="form-group row">
          <label for="inputnameteam" class="col-sm-2 col-form-label">Budget<sup class="text-danger"> *</sup></label>
          <div class="col-sm-9">
            <input id="id_input_team_name" v-model="budget" @change="clearError(budget)" type="number"
                   class="form-control" placeholder="000" />
          </div>
        </div>
        <div class="form-group row">
          <label for="inputnameteam" class="col-sm-2 col-form-label">Vegetarian<sup class="text-danger"> *</sup></label>
          <label class="container row font-weight-bold col-5 pr-0 justify-content-between">
            <font-awesome-icon :icon="['fas', 'leaf']" class="text-success fa-fw" />
            <input v-model="vegetarian" type="checkbox" class="form-check-input" />
            <span id="veggiecheckmark" class="checkmark" />
          </label>
        </div>
        <br />
        <div class="form-group row">
          <div class="col-sm-11 d-flex flex-row-reverse">
            <button class="btn btn-info" @click.prevent="submit">Create</button>
          </div>
        </div>
      </form>
    </div>
  </div>
</template>
<script>
import axios from 'axios'
import ProviderService from '../../services/provider'

export default {
  name: 'CreateProvider',
  middleware: 'authen',
  data: () => ({
    error: '',
    name: '',
    address: '',
    phone: '',
    budget: '',
    vegetarian: false,
    description: '',
    link: '',
    items: [
      {
        text: 'Homepage',
        to: { name: 'Index' }
      },
      {
        text: 'Provider',
        to: { name: 'manage-provider' }
      }
    ]
  }),

  head() {
    return {
      title: 'Create new provider'
    }
  },
  methods: {

    async submit() {
      if (this.checkForm() === true) {
        const data = {
          name: this.name,
          phone: this.phone,
          address: this.address,
          link: this.link,
          description: this.description,
          budget: this.budget,
          has_vegetarian: this.vegetarian
        }
        const provider = await ProviderService.create(data)
        if (provider && provider.data) {
          const content = 'Provider have just created successfully!'
          this.$router.push({ path: '/manage-provider' })
          this.$toast.success(content, { position: 'top-right' })
        }
      }
    },

    checkForm() {
      this.error = ''
      if (this.name === '') {
        this.error = 'Name is required'
        return false
      }

      if (this.phone === '') {
        this.error = 'Phone is required'
        return false
      }
      if (this.address === '') {
        this.error = 'Address is required'
        return false
      }
      if (this.description === '') {
        this.error = 'description is required'
        return false
      }
      if (this.link === '') {
        this.error = 'link is required'
        return false
      }
      if (this.budget === '') {
        this.error = 'budget is required'
        return false
      }

      return true
    },
    clearError(input_data) {
      if (input_data.length !== 0) {
        this.error = []
        return this.error
      } else {
        return true
      }
    }
  }
}
</script>
<style lang="scss" scoped>
@import '../../assets/scss/create-provider.scss'
</style>
