<template>
  <div class="main-content">
    <div class="position-absolute sticky-top mt-2 mybcrumb">
      <b-breadcrumb :items="items"></b-breadcrumb>
    </div>
    <v-app>
      <div>
        <div class="col-8 mx-auto">
          <router-link to="/holidays" class="btn btn-info float-right text-uppercase font-weight-bold text-light">
            <font-awesome-icon :icon="['fas', 'chart-bar']" />
            Holidays
          </router-link>
        </div>
      </div>
      <div class=" row p-3">
        <div class="col-md-8 mx-auto">
          <h5 class="card-header text-white bg-info text-left py-4">
            <strong>Requirements</strong>
            <font-awesome-icon :icon="['fas', 'plus']" class="fa-fw d-inline float-right mr-4"
                               @click="show_dialog_add" />
            <v-dialog
              v-model="dialog"
              width="500">
              <v-card>
                <v-card-title class="headline bg-info text-light mb-3">
                  {{ title_dialog }}
                </v-card-title>
                <v-card-text>
                  <label class="float-left font-weight-bold">Choose type:</label>
                  <select class="form-control" v-model="type">
                    <option v-for="(type, index) in types" v-bind:value="index">{{ type }}</option>
                  </select>
                  <label class="float-left mt-2 font-weight-bold">Title:</label>
                  <textarea class="form-control" id="typeid" rows="2" v-model="title"></textarea>

                  <div class="row" v-if="type === 0">
                    <div class="col-4">
                      <label class="font-weight-bold">Salary paid</label>
                    </div>
                    <div class="col-8">
                      <label class="radio-inline" v-model="is_salary" @click="click_salary(true)">
                        <input type="radio" name="salary" v-bind:checked="is_salary"> Yes
                      </label>
                      <label class="radio-inline ml-3" v-model="is_salary" @click="click_salary(false)">
                        <input type="radio" name="salary" v-bind:checked="is_salary === false"> No
                      </label>
                    </div>
                  </div>

                  <div class="row" v-if="type === 0">
                    <div class="col-4">
                      <label class="font-weight-bold">Leave days counted</label>
                    </div>
                    <div class="col-8">
                      <label class="radio-inline" v-model="is_absence" @click="click_absence(1)">
                        <input type="radio" name="absence" v-bind:checked="is_absence"> Yes
                      </label>
                      <label class="radio-inline ml-3" v-model="is_absence" @click="click_absence(0)">
                        <input type="radio" name="absence" v-bind:checked="is_absence === 0"> No
                      </label>
                    </div>
                  </div>

                  <div class="row" v-if="type === 0">
                    <div class="col-4">
                      <label class="font-weight-bold">Leave days types</label>
                    </div>
                    <div class="col-8">
                      <label class="radio-inline" v-model="is_weekend" @click="click_weekend(0)">
                        <input type="radio" name="weekend" v-bind:checked="is_weekend===0"> Business day
                      </label>
                      <label class="radio-inline ml-3" v-model="is_weekend" @click="click_weekend(1)">
                        <input type="radio" name="weekend" v-bind:checked="is_weekend === 1"> Weekend
                      </label>
                      <label class="radio-inline ml-3" v-model="is_weekend" @click="click_weekend(2)">
                        <input type="radio" name="weekend" v-bind:checked="is_weekend === 2"> All week
                      </label>
                    </div>
                  </div>
                </v-card-text>
                <v-card-actions class="mr-6">
                  <v-spacer></v-spacer>
                  <v-btn
                    color="dark"
                    depressed
                    text
                    @click="dialog = false"
                  >
                    Close
                  </v-btn>
                  <v-btn
                    depressed
                    color="#43c9d0"
                    dark
                    @click="submit(id_type!==''? id_type: 0)">
                    {{ button_submit }}
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
          </h5>
          <table class="w-100 table table-borderless">
            <tr>
              <th class="type_stt"></th>
              <th class="type_title">Title</th>
              <th class="type_salary">Salary paid</th>
              <th class="type_weekend">Leave days types</th>
              <th class="type_absence">Leave days counted</th>
              <th class="type_action">Edit/Active</th>
            </tr>
            <tbody>
            <tr v-for="(type, index) in list_company_title">
              <td :rowspan="list_company_title.length" v-if="index ===0 ">{{ types[COMPANY] }}</td>
              <td v-if="type.is_active===false"><s>{{ type.title }}</s></td>
              <td v-else>{{ type.title }}</td>
              <td>{{ type.is_paid_salary === true ? 'Yes' : 'No' }}</td>
              <td>{{ type.is_ot_days === 0 ? 'Business day' : type.is_ot_days === 1 ? 'Weekend' : 'All day' }}</td>
              <td>{{ type.add_sub_day_off === 1 ? 'Yes' : 'No' }}</td>
              <td>

                <div class="d-flex">
                  <button class="btn" title="Edit type here">
                    <v-icon @click="show_dialog_edit(type)">mdi-pencil</v-icon>
                  </button>
                  <v-switch
                    color="#43c9d0"
                    v-model="type.is_active"
                    class="ml-2"
                    @click="switch_active(type)"
                  ></v-switch>
                </div>
              </td>
            </tr>
            <tr v-for="(type, index) in list_insurance_title">
              <td :rowspan="list_insurance_title.length" v-if="index ===0 ">{{ types[INSURANCE] }}</td>
              <td v-if="type.is_active===false"><s>{{ type.title }}</s></td>
              <td v-else>{{ type.title }}</td>
              <td>No</td>
              <td>{{ type.is_ot_days === 0 ? 'Business day' : type.is_ot_days === 1 ? 'Weekend' : 'All day' }}</td>
              <td>No</td>
              <td>
                <div class="d-flex">
                  <button class="btn" title="Edit type here">
                    <v-icon @click="show_dialog_edit(type)">mdi-pencil</v-icon>
                  </button>
                  <v-switch
                    color="#43c9d0"
                    v-model="type.is_active"
                    class="ml-2"
                    @click="switch_active(type)"
                  ></v-switch>
                </div>

              </td>
            </tr>
            </tbody>
          </table>
        </div>
      </div>
    </v-app>
  </div>
</template>
<script>
import flatPickr from 'vue-flatpickr-component'
import 'flatpickr/dist/flatpickr.css'
import { TypeOffServices } from '../../services'
import { mapMutations, mapGetters } from 'vuex'

export default {
  name: 'TypeOff',
  middleware: 'authen',
  components: {
    flatPickr
  },
  computed: mapGetters({
    showNoti: 'sidebar/showNoti'
  }),
  created: function() {
    TypeOffServices.getTypeOff().then(res => {
      console.log(res.data)
      this.list_company_title = [...res.data].filter(i => i.type === this.COMPANY).sort((a, b) => this.func_sort(a, b))
      this.list_insurance_title = [...res.data].filter(i => i.type === this.INSURANCE).sort((a, b) => this.func_sort(a, b))
    })
  },
  data() {
    return {
      COMPANY: 0,
      INSURANCE: 1,
      EDIT_TYPE: title => `Edit '${title}' type`,
      BUTTON_SUBMIT: title => `${title}`,
      ADD_TYPE: 'Add requirements',
      button_submit: 'Add more',
      title_dialog: '',
      dialog: false,
      id_type: '',
      title: '',
      type: 0,
      is_salary: null,
      is_weekend: null,
      is_absence: null,
      list_company_title: [],
      list_insurance_title: [],
      types: [
        'Company pay',
        'Insurance pay'
      ],
      items: [
        {
          text: 'Homepage',
          to: { name: 'Index' }
        },
        {
          text: 'Requirements',
          active: true
        }
      ]
    }
  },
  head() {
    return {
      title: 'Type off'
    }
  },
  methods: {
    show_dialog_edit(obj) {
      console.log(obj)
      this.title = obj.title
      this.type = obj.type
      this.id_type = obj.id
      this.is_salary = obj.is_paid_salary
      this.is_absence = obj.add_sub_day_off
      this.is_weekend = obj.is_ot_days
      this.title_dialog = this.EDIT_TYPE(obj.title)
      this.button_submit = this.BUTTON_SUBMIT('Edit now')
      this.dialog = true
    },
    show_dialog_add() {
      this.title = ''
      this.is_salary = null
      this.is_absence = null
      this.is_weekend = null
      this.title_dialog = this.ADD_TYPE
      this.button_submit = this.BUTTON_SUBMIT('Add more')
      this.dialog = true
    },
    submit(pk) {
      let data = null
      if (this.type === this.INSURANCE) {
        data = {
          'title': this.title,
          'type': this.type,
          'is_pay_salary': false,
          'add_sub_day_off': 0,
          'is_ot_days': 0,
          'descriptions': 'No'
        }
      } else if (this.title !== '' && this.is_salary !== null && this.is_weekend !== null && this.is_absence !== null) {
        data = {
          'title': this.title,
          'type': this.type,
          'is_paid_salary': this.is_salary,
          'add_sub_day_off': this.is_absence,
          'is_ot_days': this.is_weekend,
          'descriptions': 'No'
        }
      }
      if (data) {
        if (pk === 0) {
          TypeOffServices.addTypeOff(data)
            .then(res => {
              if (res.status === 201) {
                this.dialog = false
                if (data.type === this.COMPANY) {
                  this.list_company_title = [...this.list_company_title, res.data].sort((a, b) => this.func_sort(a, b))
                } else {
                  this.list_insurance_title = [...this.list_insurance_title, res.data].sort((a, b) => this.func_sort(a, b))
                }
                this.$toast.success('Add successfully', { position: 'top-right' })
                this.title = ''
                this.is_salary = null
                this.is_absence = null
                this.is_weekend = null
              }
            }).catch(res => {
            this.$toast.error('An error occurred', { position: 'top-right' })
          })
        } else {
          TypeOffServices.editTypeOff(pk, data)
            .then(res => {
              if (res.status === 200) {
                this.dialog = false
                this.$toast.success('Edit successfully', { position: 'top-right' })
                if (data.type === this.COMPANY) {
                  const index = this.list_company_title.indexOf(this.list_company_title.find(item => item.id === pk))
                  this.list_company_title = [...this.list_company_title.slice(0, index), res.data, ...this.list_company_title.slice(index + 1)]
                } else {
                  const index = this.list_insurance_title.indexOf(this.list_insurance_title.find(item => item.id === pk))
                  this.list_insurance_title = [...this.list_insurance_title.slice(0, index), res.data, ...this.list_insurance_title.slice(index + 1)]
                }
                this.title = ''
                this.is_salary = null
                this.is_absence = null
                this.is_weekend = null
              }
            }).catch(res => {
            this.$toast.error('An error occurred', { position: 'top-right' })
          })
        }
      }
    },
    switch_active(obj) {
      TypeOffServices.handleActiveTypeoff(obj.id)
        .then(res => {
          if (res.status === 200) {
            this.$toast.success(`${res.data.is_active ? 'Active' : 'Hided'} successfully`, { position: 'top-right' })
          } else {
            this.$toast.error('An error occurred', { position: 'top-right' })
          }
        })
    },
    formatDate(date) {
      var d = new Date(date),
        month = '' + (d.getMonth() + 1),
        day = '' + d.getDate(),
        year = d.getFullYear()
      if (month.length < 2) {
        month = '0' + month
      }
      if (day.length < 2) {
        day = '0' + day
      }
      return [year, month, day].join('-')
    },
    func_sort(a, b) {
      return new Date(a.created_at) - new Date(b.created_at)
    },
    click_salary(value) {
      this.is_salary = value
    },
    click_weekend(value) {
      this.is_weekend = value
    },
    click_absence(value) {
      this.is_absence = value
    }
  }
}
</script>
<style lang="scss" scoped>
@import "./style.scss";
</style>
