import BaseService from '../base'


class InsuranceService extends BaseService {
    get entity() {
        return 'insurance'
    }

    create(id, data) {
        const insurance = this.request({ auth: true }).post(`user/${this.entity}/${id}/add`, data)
        return insurance
    }

    get(id) {
        const insurance = this.request({ auth: true }).get(`user/${this.entity}/${id}`)
        return insurance
    }

    update(id, data) {
        const insurance = this.request({ auth: true }).put(`user/${this.entity}/${id}`, data)
        return insurance
    }

    delete(id) {
        return this.request({ auth: true }).delete(`user/${this.entity}/${id}`)
    }

}

export default new InsuranceService()
