import axios from 'axios'

export async function getManagementRequestOff(page = 1, pageSize = 10, year = '', month = '', day = '', status = '', search = '') {
    try {
        const request = await axios.get(`/workday/request/management`, {
          params: {
            year: year,
            month: month,
            day: day,
            status: status,
            search: search,
            page: page,
            page_size: pageSize
          }
        })
        return request
    } catch (error) {
        return []
    }
}

export async function getRequestByAdmin(page = 1, pageSize = 10, year = '', month = '', day = '', status = '', search = '') {
  try {
    const request = await axios.get(`/workday/request/admin`, {
      params: {
        year: year,
        month: month,
        day: day,
        status: status,
        search: search,
        page: page,
        page_size: pageSize
      }
    })
    return request
  } catch (error) {
    return []
  }
}

export async function getMyRequest(year = '', month = '', day = '', status = '') {
    try {
        const request = await axios.get(`/workday/request/user?year=${year}&month=${month}&day=${day}&status=${status}`)
        return request
    } catch (error) {
        return []

    }
}

export async function actionRequest(form_data) {
    try {
        let data = await axios.post('/workday/action', form_data)
        return data
    } catch (error) {
        return []
    }
}


export async function countRequest() {
    try {
        let response = await axios.get('/workday/request/management/count')
        if(response && response.status===200 && response.data) {
            return response.data
        }
        return 0
    }
    catch (error){
        return 0
    }
}
