<template>
  <div class=" d-flex justify-content-center">
    <div class="main-content position-absolute sticky-top mt-2 mybcrumb">
      <b-breadcrumb :items="items"></b-breadcrumb>
    </div>
    <v-app>
      <v-container>
        <div class="">
          <h2 class="card-header text-white bg-info text-left py-4">
            Invite user
          </h2>
          <div align="center" class="bg-white mt-0">
            <validation-observer
              ref="observer"
              v-slot=""
              align="center"
            >
              <v-row class="ml-5 mr-2 pt-3">
                <v-col cols="12" sm="11" class="mt-2">
                  <v-row>
                    <v-col cols="12" sm="6">
                      <validation-provider
                        v-slot="{ errors }"
                        name="Name"
                        rules="required|max:50"
                      >
                        <v-text-field
                          v-model="invite.name"
                          :counter="50"
                          :error-messages="errors"
                          label="Name"
                          ref="nameA"
                          outlined
                          dense
                          required
                        ></v-text-field>
                      </validation-provider>

                    </v-col>
                    <v-col cols="12" sm="6">
                      <validation-provider
                        v-slot="{ errors }"
                        name="Email"
                        rules="required"
                      >
                        <v-text-field
                          @keyup.enter="add"
                          v-model="invite.email"
                          :error-messages="errors"
                          label="E-mail"
                          outlined
                          dense
                          required
                          suffix="@paradox.ai"
                        ></v-text-field>
                      </validation-provider>
                    </v-col>
                  </v-row>
                </v-col>
                <v-col cols="12" sm="1" class="mt-5">
                  <v-icon color="#43C9D0" @click="add" large>mdi-plus-thick</v-icon>
                </v-col>
              </v-row>
              <v-row>
                <v-col cols="12" sm="12">
                  <v-card class="mx-3" outlined>
                    <v-simple-table class="table-striped" fixed-header height="400px">
                      <template v-slot:default>

                        <thead class="py-2">
                        <tr>
                          <th scope="row" class="text-uppercase ">Name</th>
                          <th scope="row" class="text-uppercase">Company email</th>
                          <th scope="row" v-show="!showResult" class="text-uppercase">Action</th>
                          <th scope="row" v-show="showResult" class="text-uppercase">Status</th>
                        </tr>
                        </thead>
                        <tbody>
                        <tr v-for="item in listInvite" :key="item.name">
                          <td align="left" scope="row">{{ item.name }}</td>
                          <td align="left" scope="row">{{ item.email }}</td>
                          <td align="left" scope="row">
                            <button class="btn btn-danger btn-sm" @click="remove(item)">
                              <font-awesome-icon
                                class="text-white fa-fw"
                                :icon="['fas', 'times']"
                              />

                            </button>
                          </td>
                        </tr>
                        <tr v-for="item in invalidUser" :key="item.name" class="text-danger">
                          <td align="left" scope="row">{{ item.name }}</td>
                          <td align="left" scope="row">{{ item.email }}</td>
                          <td align="left" scope="row">
                            {{ item.status }}
                          </td>
                        </tr>
                        <tr v-for="item in validUser" :key="item.name" class="text-success">
                          <td align="left" scope="row">{{ item.name }}</td>
                          <td align="left" scope="row">{{ item.email }}</td>
                          <td align="left" scope="row">
                            {{ item.status }}
                          </td>
                        </tr>
                        </tbody>
                      </template>

                    </v-simple-table>
                  </v-card>
                </v-col>
              </v-row>
              <v-row>
                <v-col cols="12" sm="6">
                  <v-col v-show="showResult" align="left" class="float-left">
                    <div class="pt-2">
                      <div class="text-success ml-3">
                        Success: {{ validUser.length }}
                      </div>
                      <div class="text-danger ml-3" @click="sendList">
                        Failure: {{ invalidUser.length }}
                      </div>
                    </div>
                  </v-col>
                </v-col>
                <v-col align="right" cols="12" sm="6">
                  <v-btn dark text class="btn btn-secondary mr-2 btn-width" @click="clearTable">
                    clear
                  </v-btn>
                  <v-btn
                    dark
                    text
                    class="mr-4 btn btn-info btn-width"
                    @click="sendList"
                  >
                    Send invitations
                  </v-btn>
                </v-col>
              </v-row>
            </validation-observer>
          </div>
        </div>
      </v-container>
    </v-app>
  </div>
</template>
<script>
import axios from 'axios'
import InviteService from '../../services/invite/invite'
import { required, email, max } from 'vee-validate/dist/rules'
import { extend, ValidationObserver, ValidationProvider, setInteractionMode } from 'vee-validate'

setInteractionMode('eager')

extend('required', {
  ...required,
  message: '{_field_} can not be empty'
})

extend('max', {
  ...max,
  message: '{_field_} may not be greater than {length} characters'
})

extend('email', {
  ...email,
  message: 'Email must be valid'
})
export default {
  name: 'id_invite_form',
  middleware: 'authen',
  components: {
    ValidationProvider,
    ValidationObserver
  },
  data() {
    return {
      showResult: false,
      errors: '',
      listInvite: [],
      invalidUser: [],
      validUser: [],
      invite: {
        name: '',
        email: ''
      },
      defaultInvite: {
        name: '',
        email: ''
      },
      check: null,
      showbutton: true,
      character: '',
      items: [
        {
          text: 'Homepage',
          to: { name: 'Index' }
        },
        {
          text: 'Employee List',
          to: { name: 'EmployeeList' }
        },
        {
          text: 'Invite',
          active: true
        }
      ]
    }
  },
  methods: {
    async sendList() {
      if (!this.listInvite.length) {
        return
      }
      this.resetForm()
      let response = await InviteService.sendList(this.listInvite)
      if (response.status === 201 && response.data) {
        this.listInvite = []
        this.invalidUser = response.data.invalid_user
        this.validUser = response.data.valid_user
        if (this.invalidUser.length) {
          this.$toast.warning(`Failure: ${this.invalidUser.length}`)
        } else {
          this.$toast.success('Success')
        }
        this.showResult = true
      } else {
        this.$toast.error('Error')
      }
    },
    add() {
      if (this.invite.name && this.invite.email) {
        this.invite.email += '@paradox.ai'
        this.listInvite.unshift(this.invite)
        this.resetForm()
        this.$refs.nameA.focus()
      }

    },
    clearTable() {
      this.validUser = []
      this.listInvite = []
      this.invalidUser = []
      this.showResult = false
      this.resetForm()
    },
    resetForm() {
      this.invite = Object.assign({}, this.defaultInvite)
      this.$refs.observer.reset()
    },
    remove(item) {
      let index = this.listInvite.indexOf(item)
      this.listInvite.splice(index, 1)
    }
  },
  computed: {
    noneData() {
      return !this.listInvite.length && !this.invalidUser.length && !this.validUser.length
    }
  }
}
</script>
<style lang="scss" scoped>
@import "invite";

</style>
