<template>
  <div id="id_profile" class="main-profile row p-3 m-4">
    <table
      class="table table-bordered table-rounded table-fixed"
    >
      <caption>
      </caption>
      <thead class="bg-info">
      <tr>
        <th scope="col" class="family-name">
          Name
        </th>
        <th scope="col" class="family-birth">
          Date of birth
        </th>
        <th scope="col" class="family-relation">
          Relationship
        </th>
        <th scope="col" class="add text-center">
          <font-awesome-icon :icon="['fas', 'plus']" class="fa-fw" @click="toggleEditing()" />
        </th>
      </tr>
      </thead>
      <tbody>
      <tr v-for="(familyMember, i) in userFamilyMembers" :key="i">
        <td class="text-dark">
          {{ familyMember.name }}
        </td>
        <td class="text-dark">
          {{ familyMember.date_of_birth }}
        </td>
        <td class="text-capitalize text-dark">
          {{ familyMember.relationship.toLowerCase() }}
        </td>
        <td class="w-6 text-center text-dark">
            <span
              class="mr-3"
              v-on:click="toggleEditing(familyMember)"
            >
              <font-awesome-icon :icon="['fas', 'edit']" />
            </span>
          <span
            v-on:click="toggleConfirmDelete(familyMember)"
          >
              <font-awesome-icon :icon="['fas', 'trash']" />
            </span>
        </td>
      </tr>
      </tbody>
    </table>
    <b-modal
      ref="create_family_modal"
      @ok="submitModal()"
      :ok-disabled="this.$v.$invalid"
      @hidden="resetModal()"
      ok-title="Confirm"
      ok-variant="info"
    >
      <slot>
        <h5 class="text-center">
          <b-badge class="p-3 w-100">
            Input member information
          </b-badge>
        </h5>
        <div class="col-sm-12 mx-auto row my-4">
          <form class="bg-white p-4 text-info">
            <div
              class="form-group row"
              :class="{
                'form-group--error': formData.name.$error
              }"
            >
              <label class="col-sm-5 col-form-label"
              >Name<sup class="text-danger"> *</sup></label
              >
              <div class="col-sm-7 pt-2">
                <input
                  v-model="formData.name"
                  type="text"
                  class="form-control row"
                />
                <div class="error" v-if="!$v.formData.name.required">
                  Field is required
                </div>
              </div>
            </div>
            <div class="form-group row">
              <label class="col-sm-5 col-form-label"
              >Relationship<sup class="text-danger"> *</sup></label
              >
              <div class="col-sm-7 pt-2 inner-addon right-addon row">
                <select class="form-control" v-model="formData.relationship">
                  <option
                    v-for="option in this.RELATIONSHIPS"
                    v-bind:value="option.value"
                  >
                    {{ option.text }}
                  </option>
                </select>
              </div>
            </div>
            <div class="form-group row">
              <label class="col-sm-5 col-form-label"
              >Date of birth<sup class="text-danger"> *</sup></label
              >
              <div class="col-sm-7 pt-2">
                <input
                  v-model="formData.date_of_birth"
                  type="date"
                  class="form-control row"
                />
              </div>
            </div>
          </form>
        </div>
      </slot>
    </b-modal>
    <b-modal
      ref="delete_member_modal"
      @ok="deleteMember()"
      ok-title="Confirm"
      ok-variant="danger"
    >
      <slot>
        <h5 class="text-center">
          <p class="p-3 w-100 text-dark">
            Do you want to remove?
          </p>
        </h5>
        <div class="col-sm-12 mx-auto row my-4"></div>
      </slot>
    </b-modal>
  </div>
</template>

<script>
import { RELATIONSHIPS } from '../../../const/familyRelationships.js'
import { required } from 'vuelidate/lib/validators'
import FamilyService from '../../../services/profile/family.js'

export default {
  data: () => ({
    memberToDelete: '',
    selected: '',
    formData: {
      id: '',
      name: '',
      relationship: '',
      date_of_birth: null
    },
    userId: '',
    admin: localStorage.getItem('admin'),
    userFamilyMembers: []
  }),
  validations: {
    formData: {
      name: {
        required
      }
    }
  },
  computed: {
    RELATIONSHIPS: function() {
      return RELATIONSHIPS
    }
  },
  methods: {
    submitModal() {
      !this.formData.id ? this.createFamilyMember() : this.updateFamilyMember()
    },
    toggleEditing(userFamilyMember) {
      if (userFamilyMember) this.formData = { ...userFamilyMember }
      this.showForm()
    },
    toggleConfirmDelete(userFamilyMember) {
      this.memberToDelete = userFamilyMember
      this.$refs.delete_member_modal.show()
    },
    resetModal() {
      this.formData = {
        id: '',
        name: '',
        relationship: '',
        date_of_birth: null
      }
    },
    showForm() {
      this.$refs.create_family_modal.show()
    },
    async getData() {
      this.userFamilyMembers = (await FamilyService.get(this.userId)).data
    },
    async createFamilyMember() {
      try {
        this.userFamilyMembers.push(
          (await FamilyService.create(this.userId, this.formData)).data
        )
      } catch (error) {
        if (error.response && error.response.data) {
          this.$emit('openSnack', error.response.data, 'error')
        }
      }
    },
    async updateFamilyMember() {
      try {
        FamilyService.update(this.userId, this.formData).then(() => {
          for (let i in this.userFamilyMembers) {
            if (this.userFamilyMembers[i].id === this.formData.id) {
              this.userFamilyMembers[i] = { ...this.formData }
              break
            }
          }
        })
      } catch (error) {
        if (error.response && error.response.data) {
          this.$emit('openSnack', error.response.data, 'error')
        }
      }
    },
    async deleteMember() {
      try {
        await FamilyService.delete(this.userId, this.memberToDelete.id)
        this.userFamilyMembers.splice(
          this.userFamilyMembers.indexOf(this.memberToDelete),
          1
        )
        this.memberToDelete = {}
      } catch (error) {
        if (error.response && error.response.data) {
          this.$emit('openSnack', error.response.data, 'error')
        }
      }
    }
  },
  mounted() {
    this.userId = this.$route.params.id
    this.getData()
  }
}
</script>
<style lang="scss">
@import "family";
</style>
