<template>
  <div class="m-5">
    <h5 class="card-header text-white bg-info text-left py-4">
      <strong>Employee Identity</strong>
      <span class="float-right">
        <font-awesome-icon v-if="userIdentity.identity_number"
                           :icon="['fas', 'edit']"
                           v-on:click="toggleEditing"/>
        <font-awesome-icon v-else
                           :icon="['fas', 'plus']"
                           v-on:click="toggleEditing"/>
      </span>
    </h5>
    <form class="bg-white p-4 text-info">
      <div v-if="userIdentity.identity_number">
        <div class="row">
          <label class="col-sm-3">Identity number</label>
          <div class="col-sm-8 ml-2">
            <p class="text-dark">
              {{ userIdentity.identity_number }}
            </p>
            <v-text-field
              v-model="formData.identity_number"
              :error-messages="identityErrors"
              outlined
              dense
              required
              @input="$v.formData.identity_number.$touch()"
              @blur="$v.formData.identity_number.$touch()"
            ></v-text-field>
          </div>
        </div>
        <div class="form-group row mt-3">
          <label class="col-sm-3">Issue date</label>
          <div class="col-sm-8 ml-2">
            <p class="text-dark">
              {{ userIdentity.issue_date }}
            </p>
              <flat-pickr
                v-model="formData.issue_date"
                :config="{
                altInput: true,
                altFormat: 'd-m-Y',
                dateFormat: 'Y-m-d',
                locale: { firstDayOfWeek: 1 },
            }"
                class="form-control bg-white"
                name="date"
                required
              />
          </div>
        </div>
        <div class="form-group row">
          <label class="col-sm-3">Issue place</label>
          <div class="col-sm-8 ml-2">
            <p class="text-dark">
              {{ userIdentity.issue_place }}
            </p>
            <v-text-field
              v-model="formData.issue_place"
              :error-messages="issuePlaceErrors"
              outlined
              dense
              required
              @input="$v.formData.issue_place.$touch()"
              @blur="$v.formData.issue_place.$touch()"
            ></v-text-field>
          </div>
        </div>
        <div class="row">
          <label class="col-sm-3">Place of birth</label>
          <div class="col-sm-8 ml-2">
            <p class="text-dark">
              {{ userIdentity.place_of_birth }}
            </p>
            <v-text-field
              :error-messages="pobErrors"
              v-model="formData.place_of_birth"
              outlined
              dense
              required
              @input="$v.formData.place_of_birth.$touch()"
              @blur="$v.formData.place_of_birth.$touch()"
            ></v-text-field>
          </div>
        </div>
        <div class="form-group row" v-show="editting">
          <label class="col-sm-3"></label>
          <div class="col-sm-8 ml-2">
            <button class="btn btn-primary mr-3" @click="submitForm" :disabled="this.$v.$invalid">Save</button>
            <button class="btn btn-light" @click="toggleEditing">Cancel</button>
          </div>
        </div>
      </div>
    </form>
    <b-modal
      ref="create_identity_modal"
      @ok="saveIdentity()"
      :ok-disabled="this.$v.$invalid"
      ok-title="Confirm"
      ok-variant="info"
    >
      <slot>
        <h5 class="text-center">
          <b-badge class="p-3 w-100">
            Input your identity account
          </b-badge>
        </h5>
        <div class="col-sm-12 mx-auto row my-4">
          <form class="bg-white p-4 text-info">
            <div
              class="form-group row"
              :class="{
                'form-group--error': formData.identity_number.$error
              }"
            >
              <label class="col-sm-5 col-form-label"
              >Identity number<sup class="text-danger"> *</sup></label
              >
              <div class="col-sm-7 pt-2">
                <input
                  v-model="formData.identity_number"
                  type="text"
                  class="form-control row"
                />
                <div class="error" v-if="!$v.formData.identity_number.required">
                  Field is required
                </div>
                <div class="error" v-if="!$v.formData.identity_number.integer">
                  Must contain only numbers
                </div>
                <div
                  class="error"
                  v-if="!$v.formData.identity_number.minLength"
                >
                  Required at least
                  {{ $v.formData.identity_number.$params.minLength.min }}
                  digits.
                </div>
              </div>
            </div>
            <div class="form-group row">
              <label class="col-sm-5 col-form-label"
              >Issue Date<sup class="text-danger"> *</sup></label
              >
              <div class="col-sm-7 pt-2">
                <input
                  v-model="formData.issue_date"
                  type="date"
                  class="form-control row"
                />
              </div>
            </div>
            <div
              class="form-group row"
              :class="{
                'form-group--error': formData.issue_place.$error
              }"
            >
              <label class="col-sm-5 col-form-label">Issue place<sup class="text-danger"> *</sup></label>
              <div class="col-sm-7 pt-2 inner-addon right-addon row">
                <input
                  v-model="formData.issue_place"
                  class="form-control"
                  type="text"
                />
                <div class="error" v-if="!$v.formData.issue_place.required">
                  Field is required
                </div>
              </div>
            </div>
            <div class="form-group row">
              <label class="col-sm-5 col-form-label"
              >Place of birth</label
              >
              <div class="col-sm-7 pt-2 inner-addon right-addon row">
                <input
                  v-model="formData.place_of_birth"
                  class="form-control"
                  type="text"
                />
              </div>
            </div>
          </form>
        </div>
      </slot>
    </b-modal>
  </div>
    </v-app>
</template>
<script>
import IdentityService from '../../../services/profile/identity.js'
import { validationMixin } from 'vuelidate'
import { required, minLength, integer } from 'vuelidate/lib/validators'
import '../../../assets/scss/main.scss'
import flatPickr from 'vue-flatpickr-component'
import 'flatpickr/dist/flatpickr.css'

export default {
  data: () => ({
    selected: '',
    userId: '',
    admin: localStorage.getItem('admin'),
    formData: {
      identity_number: '',
      issue_date: null,
      issue_place: '',
      place_of_birth: ''
    },
    keysToDelete: ['user', 'id', 'create_at', 'delete_at'],
    userIdentity: {}
  }),
  validations: {
    formData: {
      identity_number: {
        required,
        integer,
        minLength: minLength(9)
      },
      issue_place: {
        required
      },
      place_of_birth: {
        required
      }
    }
  },
  created() {
    this.userId = this.$route.params.id
    this.getData()
  },
  methods: {
    submitModal() {
      !this.userIdentity.identity_number
        ? this.createIdentity()
        : this.updateIdentity()
    },
    toggleEditing() {
      this.showForm()
    },
    showForm() {
      this.$refs.create_identity_modal.show()
    },
    async getData() {
      this.userIdentity = (await IdentityService.get(this.userId)).data
      this.formData = { ...this.userIdentity }
      this.keysToDelete.forEach((key) => delete this.formData[key])
    },
    async saveIdentity() {
      try {
        this.userIdentity = !this.userIdentity.identity_number
          ? (await IdentityService.create(this.userId, this.formData)).data
          : (await IdentityService.update(this.userId, this.formData)).data
        this.$toast.success('Update successfully')
      } catch (error) {
        if (error.response && error.response.data){
          const errList = error.response.data
          const err = Object.keys(errList)
          console.log(error.response.data[err[0]])
          this.$toast.error(`${err[0]}: ${errList[err[0]]}`)
        }
      }
    }
  },
}
</script>
