<template>
  <v-app>
  <div class="main-content d-flex justify-content-center">

    <div class="table-responsive p-3 col-md-9">
      <h2 class="card-header text-white bg-info text-left py-4">
        Create New Lunch
      </h2>
      <div class="text-danger pb-2 pl-4 pt-2 text-center" v-show="error.length > 0">
        <span>{{ error }}</span>
      </div>
      <div id="id_create_form" class="bg-white p-4 text-info">
        <v-row class=" d-flex justify-content-between align-items-center">
          <v-col cols="10" class="m-auto d-flex align-items-center">
            <v-row>
              <v-col cols="3" class="m-auto d-flex align-items-center">
                <v-text-field v-model="start" type="date" label="From"></v-text-field>
              </v-col>
              <v-col cols="3" class="m-auto d-flex align-items-center">
                <v-text-field v-model="end" type="date" label="To"></v-text-field>
              </v-col>
              <v-col cols="3" class="m-auto d-flex align-items-center">
                <b-dropdown class="w-90" :text=" text ? text: 'Select provider'" variant="outline-info">
                  <b-dropdown-item v-for="item in providers" @click="provider = item.id; text=item.name"> {{ item.name
                    }}
                  </b-dropdown-item>
                </b-dropdown>
              </v-col>
              <v-col cols="1" class="m-auto d-flex align-items-center">
                <input v-model="vegetarian" type="checkbox" class="mr-2" />
                <font-awesome-icon :icon="['fas', 'leaf']" class="text-success fa-fw mr-1" />
              </v-col>
            </v-row>
          </v-col>

          <v-col cols="2" class="text-center">
            <button @click="addLunch" class="btn btn-info">
              <font-awesome-icon :icon="['fas', 'plus']" class="text-light" />
            </button>

          </v-col>
        </v-row>
        <v-row>
          <v-col cols="12" sm="12">
            <v-card class="mx-3" outlined>
              <v-simple-table class="table-striped" fixed-header height="400px">
                <template v-slot:default>
                  <thead class="py-2">
                  <tr>
                    <th scope="row" class="text-uppercase">From</th>
                    <th scope="row" class="text-uppercase">To</th>
                    <th scope="row" class="text-uppercase">Provider</th>
                    <th scope="row" class="text-uppercase">Has Veggie</th>
                    <th scope="row" class="text-uppercase">Action</th>
                  </tr>
                  </thead>
                  <tbody>
                  <tr v-for="item in initLunch" :key="item.from">
                    <td align="left" scope="row">{{ item.from }}</td>
                    <td align="left" scope="row">{{ item.to }}</td>
                    <td align="left" scope="row">{{ item.provider_name }}</td>
                    <td align="left" scope="row">{{ item.has_veggie ? 'Yes' : 'No' }}</td>
                    <td align="left" scope="row">
                      <font-awesome-icon title="Remove this" type="button" @click="deleteEvent(item)" :icon="['fas', 'trash']" class="icon-trash"/>
                    </td>
                  </tr>
                  </tbody>
                </template>
              </v-simple-table>
            </v-card>
          </v-col>
        </v-row>
        <br />
        <div class="form-group row">
          <div class="col-sm-11 d-flex flex-row-reverse">
            <button class="btn btn-info" @click.prevent="createMany">Create</button>
          </div>
        </div>
      </div>
    </div>
  </div>
    </v-app>

</template>
<script>
import ProviderService from '../../services/provider'
import LunchService from '../../services/lunch'
import moment from 'moment'
export default {
  name: 'CreateLunch',
  middleware: 'authen',
  data: () => ({
    error: '',
    text: '',
    provider: '',
    date: new Date().toISOString().substr(0, 10),
    note: '',
    start: new Date().toISOString().substr(0, 10),
    end: null,
    providers: [],
    initLunch: [],
    dialog: false,
    vegetarian: false,
    from: null,
    to: null,

  }),
  head() {
    return {
      title: 'Create new lunch'
    }
  },
  created() {
    this.getProviders()
  },
  methods: {
    async submit() {
      if (this.checkForm() === true) {
        const data = {
          provider: this.provider,
          note: this.note,
          date: this.date,
          has_vegetarian: this.vegetarian
        }
        const lunch = await LunchService.create(data)
        if (lunch && lunch.data.msg) {
          const content = lunch.data.msg
          this.$toast.error(content, { position: 'top-right' })
        }
        if (lunch && lunch.data.note) {
          const content = 'Lunch have just created successfully!'
          this.$router.push({ path: '/manage-lunch' })
          this.$toast.success(content, { position: 'top-right' })
        }
      }
    },
    deleteEvent(item) {
        const index = this.initLunch.indexOf(item)
        this.initLunch.splice(index, 1)
    },

    addLunch() {
      if (this.start === '' || this.end === '' || this.provider === '') {
        return this.$toast.error('Start date or end date or provider are empty', { position: 'top-right' })
      }
      if (this.start > this.end) {
        return this.$toast.error('End day must be greater than start day', { position: 'top-right' })
      }

      const input = {
        list_dates: this.getDatesInRange(this.start, this.end),
        provider_name: this.providers.find(provider => provider.id === this.provider).name,
        provider: this.provider,
        has_veggie: this.vegetarian,
        note: this.note,
        from: this.start,
        to: this.end
      }
      this.initLunch.push(input)
      this.start = ''
      this.end = ''
      this.vegetarian = false
    },
    getDatesInRange(startDate, stopDate) {
      var dateArray = []
      var currentDate = moment(startDate)
      var stopDate = moment(stopDate)
      while (currentDate <= stopDate) {
        dateArray.push(moment(currentDate).format('YYYY-MM-DD'))
        currentDate = moment(currentDate).add(1, 'days')
      }
      return dateArray
    },
    async createMany() {
      if (this.checkForm() === true) {
        if (this.initLunch.length > 0) {
          const data = {
            list_lunches: this.initLunch
          }
          const userLunches = await LunchService.createMany(data)
          this.$router.push({ path: '/manage-lunch' })

        }
      } else {
        alert('You must enter start and end time')
      }
    },
    async getProviders() {
      const listProviders = await ProviderService.get()
      this.providers = listProviders.data
    },
    checkForm() {
      this.error = ''
      if (this.date === '') {
        this.error = 'date is required'
        return false
      }
      if (this.provider === '') {
        this.error = 'provider is required'
        return false
      }
      return true
    },
    clearError(input_data) {
      if (input_data.length !== 0) {
        this.error = []
        return this.error
      } else {
        return true
      }
    }
  }
}
</script>
<style>
@import "index.scss";
</style>
