<template>
  <div id="app" class="ml-5 mr-5">
    <v-app id="inspire">
      <v-card outlined class="m-4">
        <v-data-table
          :headers="headers"
          :items="desserts"
          hide-default-footer
          :page.sync="page"
          class="table-striped"
          :items-per-page="itemsPerPage"
          :search="search"
          @page-count="pageCount = $event"
        >
          <template v-slot:top>
            <v-toolbar flat color="white" class="m-3">
              <h2>Manage Provider</h2>
              <v-spacer></v-spacer>
              <v-col cols="3">
                <v-text-field
                  v-model="search"
                  append-icon="mdi-magnify"
                  label="Search"
                  single-line
                  hide-details
                  placeholder="Search"
                ></v-text-field>
              </v-col>
              <v-dialog v-model="dialog" max-width="650px">
                <template v-slot:activator="{ on, attrs }">
                  <v-btn
                    color="#43c9d0"
                    dark
                    depressed
                    class="mb-2"
                    v-bind="attrs"
                    v-on="on"
                  >Add New Provider
                  </v-btn>
                </template>
                <v-card>
                  <v-card-title>
                    <span class="headline">{{ formTitle }}</span>
                  </v-card-title>
                  <v-card-text>
                    <v-container>
                      <validation-observer
                        ref="observer"
                        v-slot=""
                        align="center"
                      >
                        <validation-provider
                          v-slot="{ errors }"
                          name="Name"
                          rules="required"
                        >
                          <v-row>

                            <v-text-field v-model="editedItem.name" :error-messages="errors" required
                                          label="Name*"></v-text-field>
                          </v-row>
                        </validation-provider>
                        <validation-provider
                          v-slot="{ errors }"
                          name="Phone"
                          rules="required"
                        >
                        <v-row>
                            <v-text-field v-model="editedItem.phone" type="number" :error-messages="errors" label="Phone*"></v-text-field>
                          </v-row>
                        </validation-provider>
                        <validation-provider
                          v-slot="{ errors }"
                          name="Budget"
                          rules="required"
                        >
                          <v-row>
                            <v-text-field v-model="editedItem.budget" type="number" :error-messages="errors" label="Budget*"></v-text-field>
                          </v-row>
                        </validation-provider>
                        <validation-provider
                          v-slot="{ errors }"
                          name="Address"
                          rules="required"
                        >
                          <v-row>
                            <v-text-field v-model="editedItem.address" :error-messages="errors" label="Address*"></v-text-field>
                          </v-row>
                        </validation-provider>
                        <validation-provider
                          v-slot="{ errors }"
                          name="Link"
                          rules="required"
                        >
                          <v-row>
                            <v-text-field v-model="editedItem.link" :error-messages="errors" label="Link*"></v-text-field>
                          </v-row>
                        </validation-provider>

                        <v-row>
                          <input v-model="editedItem.has_vegetarian" type="checkbox" class="mr-4" />
                          <label class="font-weight-bold">
                            <font-awesome-icon :icon="['fas', 'leaf']" class="text-success fa-fw" />
                            Has Veggie
                          </label>
                        </v-row>

                      </validation-observer>
                    </v-container>
                  </v-card-text>
                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn depressed color="danger" text @click="close">Cancel</v-btn>
                    <v-btn depressed color="#43c9d0" dark @click="save()">Save</v-btn>
                  </v-card-actions>
                </v-card>
              </v-dialog>
            </v-toolbar>
          </template>
          <template v-slot:item.has_vegetarian="{ item }">
            <td v-if="item.has_vegetarian">Yes</td>
            <td v-else>No</td>
          </template>
          <template v-slot:item.actions="{ item }">
            <v-icon
              title="Edit this"
              small
              color="green darken-2"
              class="mr-2"
              @click="editItem(item)"
            >
              mdi-pencil
            </v-icon>
            <v-icon
              title="Remove this"
              small
              color="red darken-2"
              @click="deleteItem(item)"
            >
              mdi-delete
            </v-icon>
          </template>
          <template v-slot:item.lunch="{ item }">
            <v-icon
              title="Remove this"
              small
              @click="deleteItem(item)"
              color="teal darken-2"
            >
              mdi-plus
            </v-icon>
          </template>
          <template v-slot:item.budget="{ item }">
            <v-chip :color="getColor(item.budget)" dark>{{ item.budget }}</v-chip>
          </template>
        </v-data-table>
      </v-card>
      <v-pagination v-model="page" :length="pageCount" color="#43c9d0"></v-pagination>
    </v-app>
  </div>
</template>
<script>
import ProviderService from '../../services/provider'
import { required, email, max } from 'vee-validate/dist/rules'
import { extend, ValidationObserver, ValidationProvider, setInteractionMode } from 'vee-validate'

setInteractionMode('eager')

extend('required', {
  ...required,
  message: '{_field_} can not be empty'
})

extend('max', {
  ...max,
  message: '{_field_} may not be greater than {length} characters'
})

extend('email', {
  ...email,
  message: 'Email must be valid'
})
export default {
  middleware: 'authen',
  data: () => ({
    dialog: false,
    invite: {
      name: '',
      email: ''
    },
    headers: [
      {
        text: 'Name',
        align: 'start',
        sortable: false,
        value: 'name'
      },
      {
        text: 'Phone',
        value: 'phone'
      },
      {
        text: 'Budget',
        value: 'budget'
      },
      {
        text: 'Address',
        value: 'address'
      },
      {
        text: 'Has Veggie',
        value: 'has_vegetarian'
      },
      {
        text: 'Link',
        value: 'link'
      },
      {
        text: 'Actions',
        value: 'actions',
        sortable: false
      }
    ],
    search: '',
    page: 1,
    pageCount: 0,
    itemsPerPage: 10,
    desserts: [],
    editedIndex: -1,
    editedItem: {
      name: '',
      phone: 0,
      budget: 0,
      address: '',
      link: ''
    },
    defaultItem: {
      name: '',
      phone: 0,
      budget: 0,
      address: '',
      link: ''
    }
  }),
  components: {
    ValidationProvider,
    ValidationObserver
  },

  computed: {
    formTitle() {
      return this.editedIndex === -1 ? 'New Provider' : 'Edit Provider'
    }
  },

  watch: {
    dialog(val) {
      val || this.close()
    }
  },

  created() {
    this.getProviders()
  },

  methods: {
    async getProviders() {
      const providers = await ProviderService.get()
      this.desserts = providers.data
      this.pageCount = Math.ceil(this.desserts.length / 10)
    },

    editItem(item) {
      this.editedIndex = this.desserts.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.dialog = true
    },
    async createProvider(data) {
      const provider = await ProviderService.create(data)
    },

    async deleteItem(item) {
      const index = this.desserts.indexOf(item)
      confirm('Are you sure you want to delete this item?') && this.desserts.splice(index, 1) &&
      await ProviderService.delete(item.id)
    },

    close() {
      this.dialog = false
      this.$refs.observer.reset()
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
    },

    async save() {
      let check = false
      await this.$refs.observer.validate().then(valid => {
        check = valid
      })
      if(!check) {
        return
      }
      if (this.editedIndex > -1) {
        Object.assign(this.desserts[this.editedIndex], this.editedItem)
        const provider = await ProviderService.update({
          id: this.editedItem.id,
          data: this.editedItem
        })
        if (provider && provider.data.msg) {
          const content = provider.data.msg
          this.$toast.error(content, { position: 'top-right' })
        }
        if (provider && provider.data.name) {
          const content = 'Updated successfully!'
          this.$toast.success(content, { position: 'top-right' })
        }
      } else {
        await this.createProvider(this.editedItem)
        await this.getProviders()

      }
      this.close()
    },
    getColor(budget) {
      if (budget > 400) {
        return 'purple'
      } else if (budget > 200) {
        return 'orange'
      } else {
        return 'teal'
      }
    }
  }
}
</script>
<style lang="scss" scoped>
