<template>
  <div class="row mb-2 ml-1 d-flex align-items-center m-auto">
    <div class="col-md-2 inner-addon right-addon mt-1">
      <v-text-field
        class="pt-0"
        append-icon="mdi-magnify"
        label="Search"
        single-line
        hide-details
        v-model="searchData.name"
        placeholder="Enter Name or Email"
        v-on:keyup.enter="search(1)">
      </v-text-field>
    </div>
    <div class="col-md-2 inner-addon right-addon mt-2">
      <b-form-select v-model="searchData.birthdayMonth" :options="this.MONTHS" @change="search(1)">
        <template v-slot:first>
          <b-form-select-option :value="null" disabled>Search by birthday</b-form-select-option>
        </template>
      </b-form-select>
    </div>
    <div class="col-md-2 inner-addon right-addon mt-2">
      <flat-pickr
        v-model="searchData.joinDate"
        :config="this.flatpickrConfig"
        class="form-control bg-white"
        placeholder="Search by join date"
        name="date"
      />
    </div>
    <div class="col-md-1 inner-addon right-addon mt-2">
      <b-form-select v-model="searchData.gender" :options="GENDERS" @change="search(1)">
        <template v-slot:first>
          <b-form-select-option :value="null" disabled>Gender</b-form-select-option>
        </template>
      </b-form-select>
    </div>
    <div class="col-md-2 inner-addon right-addon mt-2">
      <b-form-select v-model="searchData.title" :options="optionsTitle" @change="search(1)">
        <template v-slot:first>
          <b-form-select-option :value="null" disabled class="">Title</b-form-select-option>
        </template>
      </b-form-select>
    </div>
    <div class="col-md-2 inner-addon right-addon mt-2">
      <b-form-select v-model="searchData.team" :options="optionsTeam" @change="search(1)">
        <template v-slot:first>
          <b-form-select-option :value="null" disabled>Team</b-form-select-option>
        </template>
      </b-form-select>
    </div>
    <div class="col-md-1 inner-addon right-addon mt-2 text-center">
      <label class="switch my-auto mr-3" title="Toggle active user">
        <input type="checkbox" v-model="searchData.active">
        <span class="slider round" />
      </label>
    </div>
  </div>
</template>

<script>
import SearchUserService from '@/services/user/searchUser'
import { MONTHS } from '@/const/months'
import { GENDERS } from '@/const/genders'
import DatePicker from 'vue2-datepicker'
import 'vue2-datepicker/index.css'
import moment from 'moment'
import flatPickr from 'vue-flatpickr-component'
import monthSelectPlugin from 'flatpickr/dist/plugins/monthSelect/index'
import 'flatpickr/dist/plugins/monthSelect/style.css'

export default {
  name: 'FilterBox',
  components: {
    DatePicker,
    flatPickr
  },
  props: ['page_size'],
  data() {
    return {
      flatpickrConfig: {
        plugins: [
          new monthSelectPlugin({
            shorthand: true //defaults to false
          })
        ]
      },
      searchData: {
        name: '',
        email: '',
        birthdayMonth: null,
        joinDate: '',
        gender: null,
        team: null,
        title: null,
        active: true
      },
      optionsTeam: [],
      optionsTitle: []
    }
  },
  watch: {
    'searchData.joinDate': function() {
      this.search(1)
    },
    'searchData.active': function() {
      this.search(1)
    }
  },
  created() {
    this.setOptionTitle()
    this.setOptionTeam()
    if (GENDERS.length < 4) {
      GENDERS.push({
        text: 'All',
        value: ''
      })
    }
  },
  computed: {
    MONTHS: function() {
      return MONTHS
    },
    GENDERS: function() {
      return GENDERS
    }
  },
  methods: {
    getSearchData() {
      return this.searchData
    },
    async setOptionTitle() {
      let data = await SearchUserService.getTitleOption()
      if (data && data.data) {
        data.data.data.forEach(row => {
          if(row.title) {
            this.optionsTitle.push(row.title)
          }
          })
        this.optionsTitle.push('All')
      }
    },
    async setOptionTeam() {
      let data = await SearchUserService.getTeamOption()
      if (data && data.data) {
        data.data.data.forEach(row => this.optionsTeam.push(row.team_name))
        this.optionsTeam.push('All')
      }
    },
    validateEmail(email) {
      const re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
      return re.test(String(email).toLowerCase())
    },
    async search(page = 1) {
      if (this.validateEmail(this.searchData.name)) {
        this.searchData.email = this.searchData.name
        this.searchData.name = ''
      }
      const joindate = this.format_date(this.searchData.joinDate)
      const responseData = await SearchUserService.get(this.searchData.name, this.searchData.email,
        this.searchData.birthdayMonth, joindate,
        this.searchData.gender, this.searchData.title, this.searchData.team, this.searchData.active, page, this.page_size)
      if (responseData && responseData.data) {
        this.$emit('loadData', responseData, true)
      }

    },
    async clearFilter() {
      this.searchData = {
        name: '',
        email: '',
        birthdayMonth: null,
        joinDate: '',
        gender: null,
        team: null,
        title: null,
        active: true
      },
        this.$emit('getData')
    },
    format_date(value) {
      if (value) {
        return moment(String(value)).format('YYYY-MM')
      }
    }
  }
}
</script>
<style scoped>
@import "filter.scss";
</style>
