<template>
  <div id="app" class="ml-5 mr-5">
    <v-app id="inspire">
      <v-card outlined class="m-4">
        <v-data-table
          :headers="headers"
          class="table-striped"
          :items="desserts"
          :hide-default-footer="true"
          :search="search"
        >
          <template v-slot:top>
            <v-toolbar flat color="white" class="m-3">
              <h2>Statistic User Lunch</h2>

            </v-toolbar>
            <v-toolbar flat color="white">
              <div class="my-2 d-flex">
                <flat-pickr
                  v-model="date"
                  :config="{
                altInput: true,
                altFormat: 'd-m-Y',
                dateFormat: 'Y-m-d',
                locale: { firstDayOfWeek: 1 },
            }"
                  class="form-control bg-white"
                  name="date"
                  required
                  @on-change="doSomethingOnChange"
                />
                <v-btn class="ml-3" color="#43c9d0" depressed dark @click="filterByWeek()">
                  Filter by week
                </v-btn>
                <v-btn class="ml-3" color="#5390D9" depressed dark @click="filterByMonth()">
                  Filter by month
                </v-btn>
              </div>
              <v-spacer></v-spacer>
              <v-col cols="3">
                <v-text-field
                  v-model="search"
                  append-icon="mdi-magnify"
                  label="Search"
                  placeholder="Search provider name"
                  single-line
                  hide-details
                ></v-text-field>
              </v-col>
            </v-toolbar>
          </template>
          <template v-slot:item.total_budgets="{ item }">
            <v-chip :color="getColor(item.total_budgets)" dark>{{ item.total_budgets }}</v-chip>
          </template>
        </v-data-table>
      </v-card>
    </v-app>
  </div>
</template>
<script>
import ProviderService from '../../services/provider'
import flatPickr from 'vue-flatpickr-component'
import 'flatpickr/dist/flatpickr.css'
import UserLunchService from '../../services/user-lunch'

export default {
  middleware: 'authen',
  components: {
    flatPickr
  },
  data: () => ({
    dialog: false,
    search: '',
    isMonth: null,
    startday: new Date().toISOString().substr(0, 10),
    date: new Date().toISOString().substr(0, 10),
    headers: [
      {
        text: 'Provider Name',
        align: 'start',
        sortable: false,
        value: 'provider.name'
      },
      {
        text: 'Provider Address',
        value: 'provider.address'
      },
      {
        text: 'Total budgets',
        value: 'total_budgets'
      },
      {
        text: 'Total lunches',
        value: 'total_lunches'
      },
      {
        text: 'Total set meals',
        value: 'total_user_lunches'
      },
      {
        text: 'Total normal lunches',
        value: 'total_normal_lunch'
      },
      {
        text: 'Total veggie lunches',
        value: 'total_veggie_lunch'
      }

    ],
    desserts: [],
    editedIndex: -1,
    editedItem: {
      has_veggie: false,
      date: new Date().toISOString().substr(0, 10),
      is_hidden: true
    },
    defaultItem: {
      has_veggie: false,
      date: new Date().toISOString().substr(0, 10),
      is_hidden: true
    },
    dataArray: []
  }),

  watch: {
    dialog(val) {
      val || this.close()
    }
  },

  methods: {
    async statistic() {
      const data = {
        is_month: this.isMonth,
        date: this.date
      }
      const statistic = await UserLunchService.statistic(data)

      this.desserts = statistic.data.data
      return statistic
    },

    getColor(budget) {
      if (budget > 400) {
        return 'purple'
      } else if (budget > 200) {
        return 'orange'
      } else {
        return 'teal'
      }
    },

    async filterByMonth() {
      this.isMonth = true
      await this.statistic()

    },

    async filterByWeek() {
      this.isMonth = false
      await this.statistic()

    },

    async doSomethingOnChange() {
      await this.statistic()
    }

  },
  async mounted() {
    await this.doSomethingOnChange()
  }
}
</script>
