import BaseService from '../base'


class IdentityService extends BaseService {
    get entity() {
        return 'identity'
    }

    create(id, data) {
        const identity = this.request({ auth: true }).post(`user/${this.entity}/${id}/add`, data)
        return identity
    }

    get(id) {
        const identity = this.request({ auth: true }).get(`user/${this.entity}/${id}`)
        return identity
    }

    update(id, data) {
        const identity = this.request({ auth: true }).put(`user/${this.entity}/${id}`, data)
        return identity
    }

    delete(id) {
        return this.request({ auth: true }).delete(`user/${this.entity}/${id}`)
    }

}

export default new IdentityService()
