<template>
  <v-app class="mr-3">
    <v-row class="fill-height">
      <v-col>
        <div class="d-flex mb-10 mr-4 ml-4">
          <div class="d-flex wd-div">
            <b-dropdown text="SET LUNCH" variant="info" class="m-2 w-100">
              <v-btn color="#43c9d0" dark @click.stop="dialog = true" class="mb-2 w-100 ">
                Set Range Lunch
              </v-btn>
              <v-btn color="#43c9d0" dark @click="setToday" class="mb-2 w-100">
                Set Lunch Today
              </v-btn>
              <v-btn dark v-if="!is_set_veggie" color="#43c9d0" class="mb-2 w-100" @click="setVeggieLunch()">
                Set veggie lunch
              </v-btn>
            </b-dropdown>
            <b-dropdown text="CANCEL LUNCH" variant="danger" class="m-2 w-100">
              <v-btn v-if="is_set_veggie" color="#e02d1a" dark @click="cancelSetVeggieLunch()" class="mb-2 w-100">
                Cancel set veggie lunch
              </v-btn>
              <v-btn dark color="#e02d1a" @click="deleteMany()" class="mb-2 w-100">
                Cancel Setted Lunch
              </v-btn>
            </b-dropdown>
          </div>
          <div class="ml-auto d-flex">
            <v-btn fab text small @click="prev">
              <v-icon small>mdi-chevron-left</v-icon>
            </v-btn>
            <v-btn fab text small @click="next">
              <v-icon small>mdi-chevron-right</v-icon>
            </v-btn>
            <v-btn outlined>
              {{ title }}
            </v-btn>
          </div>
        </div>
        <v-dialog v-model="dialog" max-width="500">
          <v-card>
            <v-container>
              <v-form @submit.prevent="addEvent">
                <label><strong>Start:</strong></label>
                <v-text-field v-model="startDate" type="date"></v-text-field>
                <label><strong>End:</strong></label>
                <v-text-field v-model="endDate" type="date"></v-text-field>
                <label class="container row font-weight-bold col-5 pr-0 justify-content-between ml-1">
                  Has Veggie
                  <font-awesome-icon :icon="['fas', 'leaf']" class="text-success fa-fw" />
                  <input v-model="has_veggie" type="checkbox" class="form-check-input" />
                  <span class="checkmark" />
                </label>
              </v-form>
              <v-row>
                <v-spacer></v-spacer>
                <v-btn dark type="submit" color="#43c9d0" @click.stop="addEvent" class="mr-auto">
                  Create Lunch
                </v-btn>
              </v-row>

            </v-container>
          </v-card>
        </v-dialog>
        <v-sheet height="600" class="mr-3 ml-3">
          <v-calendar
            ref="calendar"
            v-model="focus"
            color="#20B2AA"
            :events="events"
            :event-color="getEventColor"
            :event-margin-bottom="0"
            :now="today"
            :type="type"
            @click:event="showEvent"
            @change="updateRange"
          ></v-calendar>
          <v-menu
            v-model="selectedOpen"
            :close-on-content-click="false"
            :activator="selectedElement"
          >
            <v-card color="grey lighten-4" min-width="350px" flat>
              <v-toolbar :color="selectedEvent.color" dark>
                <v-btn @click="deleteEvent(selectedEvent)" icon>
                  <v-icon>mdi-delete</v-icon>
                </v-btn>
                <v-toolbar-title v-html="selectedEvent.name"></v-toolbar-title>
                <div class="flex-grow-1"></div>
              </v-toolbar>
              <v-card-text>
                <form>
                  <label class="container row font-weight-bold col-5 pr-0 justify-content-between">
                    Has Veggie
                    <input v-model="selectedEvent.has_veggie" type="checkbox" class="form-check-input" />
                    <span id="veggiecheckmark" class="checkmark" />
                    <font-awesome-icon :icon="['fas', 'leaf']" class="text-success fa-fw" />
                  </label>
                </form>
              </v-card-text>
              <v-card-actions>
                <v-btn text color="secondary" @click="selectedOpen = false">
                  close
                </v-btn>
                <v-btn text type="submit" @click="updateEvent(selectedEvent)">
                  Update
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-menu>
        </v-sheet>
      </v-col>
    </v-row>
  </v-app>
</template>
<script>
import UserLunchService from '../../services/user-lunch'
import moment from 'moment'
import { mapState } from 'vuex'
import { VALID_TIME } from '../../.env'

export default {
  data: () => ({
    today: new Date().toISOString().substr(0, 10),
    focus: new Date().toISOString().substr(0, 10),
    type: 'month',
    typeToLabel: {
      month: 'Month',
      week: 'Week',
      day: 'Day',
      '4day': '4 Days'
    },
    name: 'Lunch',
    details: null,
    start: null,
    is_set_veggie: false,
    has_veggie: false,
    end: null,
    color: '#20B2AA', // default event color
    currentlyEditing: null,
    selectedEvent: {},
    selectedElement: null,
    selectedOpen: false,
    events: [],
    dialog: false,
    dialogDate: false,
    startDate: new Date().toISOString().substr(0, 10),
    endDate: null
  }),
  async mounted() {
    await this.getEvents()
  },

  computed: {
    title() {
      const { start, end } = this
      if (!start || !end) {
        return ''
      }
      const startMonth = this.monthFormatter(start)
      const endMonth = this.monthFormatter(end)
      const suffixMonth = startMonth === endMonth ? '' : endMonth
      const startYear = start.year
      const endYear = end.year
      const suffixYear = startYear === endYear ? '' : endYear
      const startDay = start.day + this.nth(start.day)
      const endDay = end.day + this.nth(end.day)
      switch (this.type) {
        case 'month':
          return `${startMonth} ${startYear}`
        case 'week':
        case '4day':
          return `${startMonth} ${startDay} ${startYear} - ${suffixMonth} ${endDay} ${suffixYear}`
        case 'day':
          return `${startMonth} ${startDay} ${startYear}`
      }
      return ''
    },
    monthFormatter() {
      return this.$refs.calendar.getFormatter({
        timeZone: 'UTC',
        month: 'long'
      })
    }
  },
  methods: {
    async getEvents() {
      this.events = await this.$store.dispatch('event/getUserLunches')
      this.events.map(e => {
        if (e.has_veggie === true) {
          this.is_set_veggie = true
        }
      })
    },

    checkValidTime() {
      const date = new Date()
      const hours = date.getHours()
      if (hours > VALID_TIME) {
        return this.$toast.error('Out of time to set lunch', { position: 'top-right' })
      }
    },
    async deleteMany() {
      const data = {
        date: this.today
      }
      const userLunch = await UserLunchService.deleteMany(data)
      await this.getEvents()
      if (userLunch && userLunch.data.msg) {
        const content = userLunch.data.msg
        this.$toast.success(content, { position: 'top-right' })
        this.is_set_veggie = false

      }
    },
    async setVeggieLunch() {
      const date = new Date()
      const hours = date.getHours()
      if (hours > VALID_TIME) {
        return this.$toast.error('Out of time to set lunch', { position: 'top-right' })
      }
      const data = {
        date: this.today
      }
      const userLunch = await UserLunchService.setVeggieMonth(data)
      const contentRes = 'Not found lunar day'

      await this.getEvents()
      if (userLunch && userLunch.data.msg !== contentRes) {
        const content = userLunch.data.msg
        this.$toast.success(content, { position: 'top-right' })
        this.is_set_veggie = true
      }
      if (userLunch && userLunch.data.msg === contentRes) {
        this.$toast.error(contentRes, { position: 'top-right' })
      }
    },
    async cancelSetVeggieLunch() {
      const date = new Date()
      const hours = date.getHours()
      if (hours > VALID_TIME) {
        return this.$toast.error('Out of time to set lunch', { position: 'top-right' })
      }
      const data = {
        date: this.today
      }
      const userLunch = await UserLunchService.cancelSetVeggieMonth(data)
      await this.getEvents()
      if (userLunch && userLunch.data.msg) {
        const content = userLunch.data.msg
        this.$toast.success(content, { position: 'top-right' })
      }
      this.is_set_veggie = false
    },
    viewDay({ date }) {
      this.focus = date
      this.type = 'day'
    },
    getEventColor(event) {
      return event.color
    },
    async setToday() {
      const date = new Date()
      const hours = date.getHours()
      if (hours > VALID_TIME) {
        return this.$toast.error('Out of time to set lunch', { position: 'top-right' })
      }
      this.focus = this.today
      const data = {
        name: this.name,
        has_veggie: this.has_veggie || false,
        date: this.today
      }
      const userLunch = await UserLunchService.create(data)
      await this.getEvents()
      if (userLunch && userLunch.data.date) {
        this.$toast.success('You have setted lunch for today', { position: 'top-right' })
      }
      if (userLunch && userLunch.data.error_msg) {
        this.$toast.error(userLunch.data.error_msg, { position: 'top-right' })
      }
    },
    async addEvent() {
      this.dialog = false
      const date = new Date()
      const hours = date.getHours()
      var today = moment();
      if (this.startDate > this.endDate) {
        return this.$toast.error('End day must be greater than start day', { position: 'top-right' })
      }
      if (this.startDate === this.today && hours > VALID_TIME) {
        this.startDate = moment(today).add(1, 'days');
      }
      if (this.start && this.end) {
        const data = {
          name: this.name,
          has_veggie: this.has_veggie,
          list_dates: this.getDatesUnRange(this.startDate , this.endDate)
        }

        const userLunches = await UserLunchService.createMany(data)
        await this.getEvents()
        if (userLunches && userLunches.data.msg) {
          const content = userLunches.data.msg
          this.$toast.success(content, { position: 'top-right' })
        }
      } else {
        alert('You must enter start and end time')
      }
    },
    editEvent(ev) {
      this.currentlyEditing = ev.id
    },

    async updateEvent(ev) {
      const date = new Date()
      const hours = date.getHours()
      if (hours > VALID_TIME) {
        return this.$toast.error('Out of time to set lunch', { position: 'top-right' })
      }
      const data = {
        has_veggie: ev.has_veggie
      }
      const userLunch = await UserLunchService.update({
        data,
        id: ev.id
      })
      this.selectedOpen = false
      this.currentlyEditing = null
      this.getEvents()
    },

    async deleteEvent(ev) {
      const date = new Date()
      const hours = date.getHours()
      if (hours > VALID_TIME && ev.start <= this.today) {
        return this.$toast.error('Out of time to set lunch', { position: 'top-right' })
      }
      const userLunch = await UserLunchService.delete(ev.id)
      this.getEvents()
      if (userLunch && userLunch.data.msg) {
        const content = userLunch.data.msg
        this.$toast.success(content, { position: 'top-right' })
      }
      this.selectedOpen = false
    },

    showEvent({ nativeEvent, event }) {
      const open = () => {
        this.selectedEvent = event
        this.selectedElement = nativeEvent.target
        setTimeout(() => this.selectedOpen = true, 10)
      }

      if (this.selectedOpen) {
        this.selectedOpen = false
        setTimeout(open, 10)
      } else {
        open()
      }

      nativeEvent.stopPropagation()
    },
    updateRange({ start, end }) {
      this.start = start
      this.end = end
    },
    getDatesUnRange(startDate, stopDate) {
      var dateArray = []
      var currentDate = moment(startDate)
      var stopDate = moment(stopDate)
      while (currentDate <= stopDate) {
        dateArray.push(moment(currentDate).format('YYYY-MM-DD'))
        currentDate = moment(currentDate).add(1, 'days')
      }
      return dateArray
    },
    nth(d) {
      return d > 3 && d < 21
        ? 'th'
        : ['th', 'st', 'nd', 'rd', 'th', 'th', 'th', 'th', 'th', 'th'][d % 10]
    }
  }
}
</script>
<style>
@import "index.scss";
</style>
