<template>
  <v-app>
    <div class="main-content">
      <div id="id_profile" class="main-profile row p-3">
        <div class="col-md-6 mx-auto">
          <h5 class="card-header text-white bg-info text-left py-4">
            <strong>General profile</strong>
          </h5>
          <form id="profile" class="bg-white p-4 text-info">
            <div :class="msg[0]">
              {{ msg[1] }}
            </div>
            <div class="form-group row my-3">
              <div class="form-group row">
                <label class="col-sm-5 col-form-label"></label>
                <div class="col-sm-6 ml-2">
                  <img alt="" aria-hidden="true" class="p-edit_profile__photo" :src="uploadFileUrl" height="100px" />
                </div>
              </div>
            </div>
            <div class="form-group row">
              <label class="col-sm-5 col-form-label"></label>
              <div class="col-sm-6 ml-2">
                <button class="btn btn-info" variant="outline-danger" @click.prevent="onPickFile()">
                  Upload file
                </button>
                <input ref="fileInput" type="file" accept="image/*" style="display:none;" @change="onFilePicked" />
              </div>
            </div>
            <div class="form-group row">
              <label class="col-sm-3 col-form-label mt-3">Email</label>
              <div class="col-sm-8 ml-2">
                <v-text-field
                  v-model="formData.email"
                  :error-messages="emailErrors"
                  outlined
                  dense
                  :disabled="admin !== 'true'"
                  required
                  @input="$v.formData.email.$touch()"
                  @blur="$v.formData.email.$touch()"
                ></v-text-field>
              </div>
            </div>
            <div class="form-group row">
              <label class="col-sm-3 col-form-label mt-3">Name</label>
              <div class="col-sm-8 ml-2">
                <v-text-field
                  v-model="formData.name"
                  :error-messages="nameErrors"
                  outlined
                  dense
                  required
                  :disabled="admin !== 'true'"
                  @input="$v.formData.name.$touch()"
                  @blur="$v.formData.name.$touch()"
                ></v-text-field>
              </div>
            </div>
            <div class="form-group row">
              <label class="col-sm-3 col-form-label mt-3">Personal Email</label>
              <div class="col-sm-8 ml-2">
                <v-text-field
                  v-model="formData.personal_email"
                  outlined
                  dense
                  class="border-secondary"
                ></v-text-field>
              </div>
            </div>
            <div class="form-group row">
              <label class="col-sm-3 col-form-label mt-3">Phone</label>
              <div class="col-sm-8 ml-2">
                <v-text-field
                  v-model="formData.phone"
                  outlined
                  dense
                  class="border-secondary"
                ></v-text-field>
              </div>
            </div>
            <div class="form-group row">
              <label class="col-sm-3 col-form-label mt-3">Slack ID</label>
              <div class="col-sm-8 ml-2">
                <v-text-field
                  v-model="formData.slack_id"
                  outlined
                  dense
                  class="border-secondary"
                ></v-text-field>
              </div>
            </div>
            <div class="form-group row my-3">
              <label class="col-sm-3 col-form-label mt-3">Gender</label>
              <div class="col-sm-8 ml-2">
                <select v-model="formData.gender" class="form-control border-secondary" value="0">
                  <option v-for="option in this.GENDERS" :value="option.value">
                    {{ option.text }}
                  </option>
                </select>
              </div>
            </div>
            <div class="form-group row my-3">
              <label class="col-sm-3 col-form-label mt-3">Birth day</label>
              <div class="col-sm-8 ml-2">
                <div class="md-form">
                  <flat-pickr
                    v-model="formData.birth_day"
                    :config="{
                                        altInput: true,
                                        altFormat: 'd-m-Y',
                                        dateFormat: 'Y-m-d',
                                        locale: { firstDayOfWeek: 1 }
                                    }"
                    class="form-control bg-white border-secondary"
                    placeholder="Select date"
                    name="date"
                    required
                  />
                </div>
              </div>
            </div>
            <div class="form-group row my-3" v-show="admin === 'true'">
              <label class="col-sm-3 col-form-label mt-3">Join date</label>
              <div class="col-sm-8 ml-2">
                <div class="md-form">
                  <flat-pickr
                    v-model="formData.join_date"
                    :config="{
                      altInput: true,
                      altFormat: 'd-m-Y',
                      dateFormat: 'Y-m-d',
                      locale: { firstDayOfWeek: 1 }
                    }"
                    class="form-control bg-white border-secondary"
                    placeholder="Select date"
                    name="date"
                    required
                  />
                </div>
              </div>
            </div>
            <div v-show="admin === 'true'" class="form-group row my-3">
              <label class="col-sm-3 col-form-label mt-3">Title</label>
              <div class="col-sm-8 ml-2">
                <select v-model="formData.title" class="form-control border-secondary">
                  <option v-for="option in this.roleList" :value="option.title">
                    {{ option.title }}
                  </option>
                </select>
              </div>
            </div>
            <div v-show="admin === 'true'" class="form-group row my-3">
              <label class="col-sm-3 col-form-label mt-3"></label>
              <div class="col-sm-8 ml-2">
                <input type="checkbox" v-model="formData.admin" />
                <span class="mt-1 ml-3 text-dark"> Set Admin</span>
              </div>
            </div>
            <div class="text-center">
              <v-btn dark color="#43c9d0" depressed @click="submit()">
                Save
              </v-btn>
            </div>
          </form>
        </div>
      </div>
      <b-modal ref="my-modal" hide-footer title="Create profile picture!">
        <div>
          <vue-croppie
            ref="croppieRef"
            :enable-orientation="true"
            :enable-exif="false"
            :mouse-wheel-zoom="true"
            :show-zoomer="true"
            :boundary="{ width: 300, height: 300 }"
            :enable-resize="true"
            :viewport="{ width: 150, height: 150, type: 'circle' }"
            @update="update"
            @result="result"
          >
            >
          </vue-croppie>
          <div class="d-flex justify-content-between mb-3 mt-3">
            <b-button variant="outline-success" @click="rotate(-90)">
              Rotate Left
            </b-button>
            <b-button variant="outline-success" @click="rotate(90)">
              Rotate Right
            </b-button>
          </div>
        </div>
        <b-button class="mt-3" variant="outline-danger" block @click="crop()">
          Save
        </b-button>
      </b-modal>
    </div>
  </v-app>
</template>
<script>
import 'croppie/croppie.css'
import UserService from '../../../services/user/user'
import logo from '../../../static/images/icon.png'
import { GENDERS } from '../../../const/genders.js'
import flatPickr from 'vue-flatpickr-component'
import 'flatpickr/dist/flatpickr.css'
import { validationMixin } from 'vuelidate'
import { required, maxLength, email } from 'vuelidate/lib/validators'
import axios from 'axios'

export default {
  name: 'ListTable',
  middleware: 'authen',
  components: {
    flatPickr
  },
  mixins: [validationMixin],

  validations: {
    formData: {
      name: {
        required
      },
      email: {
        required,
        email
      }
    }
  },

  props: {
    content: true
  },
  data: () => ({
    name: '',
    email: '',
    select: null,
    checkbox: false,

    photos: [],
    userid: '',
    uploadfile: '',
    uploadFileUrl: '',
    formData: {
      email: '',
      name: '',
      slack_id: '',
      join_date: '',
      personal_email: '',
      phone: '',
      birth_day: '',
      title: '',
      gender: 0,
      admin: 0,
      user: ''
    },
    user: '',
    files: '',
    file_name: '',
    profile: '',
    id: '',
    profileid: '',
    msg: [],
    roleList: [],
    current_user: localStorage.getItem('user_id'),
    admin: localStorage.getItem('is_admin')
  }),
  computed: {
    nameErrors() {
      const errors = []
      if (!this.$v.formData.name.$dirty) return errors
      !this.$v.formData.name.required && errors.push('Name is required.')
      return errors
    },
    emailErrors () {
      const errors = []
      if (!this.$v.formData.email.$dirty) return errors
      !this.$v.formData.email.email && errors.push('Must be valid e-mail')
      !this.$v.formData.email.required && errors.push('E-mail is required')
      return errors
    },
    GENDERS: function() {
      return GENDERS
    }
  },
  created() {
    this.asyncData(this.$route.params)
  },
  methods: {
    clear() {
      this.$v.$reset()
      this.name = ''
      this.email = ''
      this.select = null
      this.checkbox = false
    },
    async asyncData(params) {
      this.roleList = (await UserService.getAllRoles()).data
      return axios
        .get(`/user/${params.id}`)
        .then((res) => {
          this.user = res.data
          this.profile = res.data.profile
          this.photos = res.data.profile.photo
          this.uploadFileUrl = res.data.profile.image ? res.data.profile.image : logo
          this.formData = {
            email: this.user.email,
            name: this.profile.name,
            personal_email: this.profile.personal_email,
            phone: this.profile.phone,
            join_date: this.profile.join_date,
            gender: this.profile.gender,
            slack_id: this.profile.slack_id,
            birth_day: this.profile.birth_day ? this.profile.birth_day : new Date().toLocaleDateString(),
            title: this.user.title,
            user: this.user.id,
            admin: this.user.staff
          }
          this.currentUserId = res.data.profile.user
        })
        .catch((e) => {
          error({
            statusCode: 404,
            message: 'Post not found'
          })
        })
    },
    showModal() {
      this.$refs['my-modal'].show()
    },
    hideModal() {
      this.$refs['my-modal'].hide()
    },
    onPickFile() {
      this.$refs.fileInput.click()
    },
    onFilePicked(event) {
      this.selectedFile = event.target.files[0]
      const fileReader = new FileReader()
      fileReader.readAsDataURL(this.selectedFile)
      fileReader.addEventListener('load', () => {
        this.$refs.croppieRef.bind({
          url: fileReader.result
        })
      })
      this.showModal()
    },
    result(output) {
      return output
    },
    update(val) {
    },
    crop() {
      const options = {
        type: 'blob',
        format: 'png',
        size: {
          width: 100,
          height: 100
        },
        circle: true
      }
      this.$refs.croppieRef.result(options).then((output) => {
        const newfile = new File([output], 'myfile.jpg')
        this.uploadfile = newfile
        this.uploadFileUrl = URL.createObjectURL(newfile)
        this.hideModal()
      })
    },
    rotate(rotationAngle) {
      this.$refs.croppieRef.rotate(rotationAngle)
    },
    handleFileUpload: function(event) {
      this.files = event.target.files[0]
      if (event.target.files[0] !== undefined) {
        this.file_name = event.target.files[0].name
      } else {
        this.file_name = ''
      }
      const fName = this.$refs.fName
      fName.textContent = this.file_name
    },
    submit: function() {
      this.$v.$touch()
      if (this.$v.$invalid) {
        return
      }
      const formdata = new FormData()
      formdata.append('image', this.uploadfile)
      for (const key in this.formData) {
        if (key === 'gender' || this.formData[key]) {
          formdata.append(key, this.formData[key])
        }
      }
      axios
        .put(`user/profile/${this.profile.id}`, formdata)
        .then((res) => {
          const imageurl = res.data.image
          localStorage.setItem('imageurl', imageurl)
          this.$toast.success('Update profile successfully')
        })
        .catch((e) => {
          this.$toast.error('Input data is invalid')
        })
    }
  },
  head() {
    return {
      title: 'Edit Profile'
    }
  }
}
</script>
