              :title="'Click to make a call to : ' + row.phone"
            >{{ row.phone }}</a
            >
          </td>
          <td class="text-center">
            <div class="d-flex flex-column">
              <div v-if="row.user === team.team_leader" class="text-white  bg-success badge badge-pill h4 p-2"
                   style="font-size: 1em">
                Leader
              </div>
              <div v-if="row.user === team.project_manager" class="text-white btn-primary badge  badge-pill h4 p-2"
                   style="font-size: 1em">
                Project Manager
              </div>
              <div v-if="row.user !== team.team_leader && row.user !== team.project_manager" class="mt-2"
                   style="font-size: 1em">
                Member
              </div>
            </div>
          </td>
          <td v-show="admin === 'true' || team.project_manager === user_id" scope="row">

            <div class="mt-2">
              <button
                class="btn m-1 btn-sm"
                :class="{
                    'd-none':
                      row.user === team.team_leader
                  }"
                @click="showModalLead(row)"
                title="Click to assign this member to leader"
              >
                <font-awesome-icon class="fa-fw text-success" :icon="['fas', 'crown']" />
              </button>
              <button
                class="btn m-1 btn-sm"
                @click="showMoveTeamModal(row)"
                v-show="row.title !== 'Leader'"
                title="Click to move this member to another team"
              >
                <font-awesome-icon
                  class="fa-fw text-info"
                  :icon="['fas', 'exchange-alt']"
                />
              </button>
              <button class="btn m-1 btn-sm" @click="showModal(row)" v-show="row.title !== 'Leader'"
                      title="Click to remove this member">
                <font-awesome-icon
                  class="fa-fw text-danger"
                  :icon="['fas', 'user-times']"
                />
              </button>
            </div>
          </td>
        </tr>
        </tbody>
      </table>
    </div>
    <div class="text-center col-sm-12" v-show="members.length === 0">
      <label
        class="col-sm-12 col-form-label alert alert-danger"
      >This team has no member</label
      >
    </div>

    <div class="text-center">
      <v-dialog v-model="dialog" width="500">
        <v-container class="fixed-top">
          <v-card class="mx-auto" max-width="500">
            <v-card-title
              class="headline d-block lighten-2 text-center text-danger"
            >
              <h3>Removing member</h3>
              <hr />
            </v-card-title>

            <v-card-text>
              <div class="d-block text-center mb-4">
                Do you want to remove
                <p class="text-danger d-inline">{{ currentUser.name }}</p>
                from team?
              </div>
            </v-card-text>

            <v-card-actions>
              <v-btn
                depressed
                outlined
                class="ml-3 float-left col-3 btn-secondary text-light"
                @click="closeDialog()"
              >
                Cancel
              </v-btn>
              <v-spacer></v-spacer>
              <v-btn color="#43c9d0" depressed dark class="mr-3 col-3" @click="del()">
                Confirm
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-container>
      </v-dialog>
    </div>

    <div class="text-center">
      <v-dialog v-model="dialogMove" width="500">
        <v-container class="fixed-top">
          <v-card class="mx-auto" max-width="500">
            <v-card-title
              class="headline d-block lighten-2 text-center text-info"
            >
              <h3>Moving member</h3>
              <hr />
            </v-card-title>

            <v-card-text>
              <div class="col-sm-12 ml-2">
                <div class="text-center m-2">Please choose new team:</div>
                <select
                  v-model="new_team"
                  type="text"
                  class="form-control mb-2 mt-2"
                >
                  <option
                    v-for="team in new_teams"
                    :key="team.id"
                    placeholder="Teams"
                    :value="team.id"
                  >
                    {{ team.team_name }}
                  </option>
                </select>
              </div>
            </v-card-text>
            <v-card-actions>
              <v-btn
                depressed
                outlined
                class="btn ml-3 btn-secondary col-4 text-light"
                @click="closeDialogMove()"
              >
                Cancel
              </v-btn>

              <v-spacer></v-spacer>
              <v-btn
                color="#43c9d0" depressed dark
                class=" mr-3 col-4"
                @click="move()"
              >
                Confirm
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-container>
      </v-dialog>
    </div>

    <div class="text-center">
      <v-dialog v-model="dialogLead" width="500">
        <v-container class="fixed-top">
          <v-card class="mx-auto" max-width="500">
