              class="headline d-block lighten-2 text-center text-info"
            >
              <h3>Set leader</h3>
              <hr />
            </v-card-title>

            <v-card-text>
              <div class="d-block text-center mb-4">
                Do you want to set
                <p class="text-danger d-inline">{{ currentUser.name }}</p>
                as leader?
              </div>
            </v-card-text>
            <v-card-actions>
              <v-btn
                depressed
                outlined
                class="btn ml-3 btn-secondary col-4 text-light"
                @click="hideModalLead()"
              >
                Cancel
              </v-btn>

              <v-spacer></v-spacer>
              <v-btn
                color="#43c9d0" depressed dark
                class=" mr-3 col-4"
                @click="lead()"
              >
                Confirm
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-container>
      </v-dialog>
    </div>

    <div class="text-center">
      <v-dialog v-model="dialogManger" width="500">
        <v-container class="fixed-top">
          <v-card class="mx-auto" max-width="500">
            <v-card-title
              class="headline d-block lighten-2 text-center text-info"
            >
              <h3>Add project manager</h3>
              <hr />
            </v-card-title>

            <v-card-text>
              <div class="col-sm-12 ml-2">
                <div class="text-center m-2">Please choose a project manager:</div>
                <select
                  id="id_selected_bank_name"
                  v-model="currentPM"
                  type="text"
                  class="form-control mb-2 mt-2"
                >
                  <option
                    v-for="pm in pms"
                    :key="pm.id"
                    placeholder="Teams"
                    :value="pm.email"
                  >
                    {{ pm.profile.name + ' (' + pm.email + ')' }}
                  </option>
