<script>
import axios from 'axios'
import TeamService from '../../../services/team/team.js'
import UserService from '../../../services/user/user.js'

export default {
  name: 'id_team_table',
  middleware: 'authen',
  data() {
    return {
      dialog: false,
      dialogLead: false,
      dialogMove: false,
      dialogManger: false,
      date: new Date(),
      show: true,
      namesort: false,
      namecount: 0,
      emailsort: false,
      emailcount: 0,
      subject: '',
      content: '',
      member_email: '',
      member_rows: [],
      team_id: '',
      rows: [],
      team_name: '',
      team_name_update: '',
      currentUser: '',
      admin: localStorage.getItem('is_admin'),
      user_id: localStorage.getItem('user_id'),
      items: [
        {
          text: 'Homepage',
          to: { name: 'Index' }
        },
        {
          text: 'Team',
          to: { name: 'Team' }
        },
        {
          text: 'Team Info',
          active: true
        }
      ],
      reciever: null,
      borderradius: {
        borderRadius: '1.25rem',
        textAlign: 'center'
      },
      color: {
        color: '#25c9d0',
        fontSize: '40px',
        marginLeft: '16px'
      },
      text: {
        fontSize: '20px'
      },
      errors: '',
      team_leader: '',
      // team_leader: 0,
      new_teams: [],
      new_team: '',

      //my code
      team: {},
      members: [],
      pms: [],
      currentPM: {}
    }
  },
  methods: {
    async getData() {
      this.team = (await TeamService.get(`${this.$route.params.id}`)).data
      this.members = this.team.employee_list
      this.team_name = this.team.team_name
    },
    sortFunction(arr) {
      if (this.emailsort) {
        if (this.emailcount % 2 === 0) {
          return arr.slice().sort(function(a, b) {
            return a.email.localeCompare(b.email)
          })
        } else {
          return arr.slice().sort(function(a, b) {
            return b.email.localeCompare(a.email)
          })
        }
      } else if (this.namesort) {
        if (this.namecount % 2 === 0) {
          return arr.slice().sort(function(a, b) {
            return a.name.localeCompare(b.name)
          })
        } else {
          return arr.slice().sort(function(a, b) {
            return b.name.localeCompare(a.name)
          })
        }
      } else {
        return arr.slice().sort(function(a, b) {
          return a.title.localeCompare(b.title)
        })
      }
    },
    nameSort: function() {
      this.teamsort = false
      this.emailsort = false
      this.namecount++
      return (this.namesort = true)
    },
    emailSort: function() {
      this.namesort = false
      this.teamsort = false
      this.emailcount++
      return (this.emailsort = true)
    },
    updateTeamName: function() {
      if (this.team_name_update.length !== 0) {
        const formdata = new FormData()
        formdata.append('team_name', this.team_name_update)
        return axios
          .put(`/team/${this.team_id}/`, formdata)
          .then((res) => {
            this.show = !this.show
            this.errors = ''
            return axios
              .get(`/team/${this.team_id}/`)
              .then((res) => {
                this.team_name = res.data.team.team_name
                this.rows = res.data.team.employee_list
                this.member_rows = res.data.non_members
                this.team_id = params.id
              })
              .catch((e) => {
              })
          })
          .catch(function(error) {
          })
      } else {
        this.team_name_update = this.team_name
        this.errors = 'please enter your team name'
      }
    },
    showEdit: function() {
      this.team_name_update = this.team_name
      this.show = !this.show
      this.errors = ''
    },
    cancel: function() {
      this.errors = ''
      this.show = !this.show
    },
    showModal(user) {
      this.currentUser = user
      this.dialog = true
      this.errors = ''
    },
    closeDialog() {
      this.currentUser = ''
      this.dialog = false
    },
    async showModalManager() {
      let response = await UserService.getAllProjectManager()
      this.pms = response.data
      this.errors = ''
      this.dialogManger = true
    },

    hideModalManager() {
      this.currentUser = ''
      this.dialogManger = false
    },

    async setManager() {
      let formData = new FormData()
      formData.append('email', this.currentPM)
      let response = await TeamService.setManager(
        this.$route.params.id,
        formData
      )
      if (response.data && response.data.Success) {
        this.$toast.success('Success')
        let index = this.members.indexOf(this.currentUser)
        this.$set(this.members, index, this.currentUser)
      } else {
        this.$toast.error('Error')
      }
      this.getData()
      this.hideModalManager()
    },
    async showMoveTeamModal(user) {
      this.dialogMove = true
      let response = await TeamService.getNewTeams(user.user)
      this.new_teams = response.data
      this.errors = ''
      this.currentUser = user
    },
    hideModal() {
      this.$refs.mymodal.hide()
    },
    submit: function() {
      const formdata = new FormData()
      const teamid = this.team_id
      formdata.append('email', this.member_email)
      axios
        .put(`/team/${teamid}/add_member/`, formdata)
        .then((res) => {
          return axios
            .get(`/team/${teamid}/`)
            .then((res) => {
              this.rows = res.data.team.employee_list
            })
            .catch((e) => {
            })
        })
        .catch(function(error) {
          alert(error)
        })
    },
    showModalLead(user) {
      this.currentUser = user
      this.dialogLead = true
    },
    hideModalLead() {
      this.currentUser = false
      this.dialogLead = false
    },
    async lead() {
      const formData = new FormData()
      const teamId = this.$route.params.id
      this.show = true
      this.errors = ''
      formData.append('email', this.currentUser.email)
      let response = await TeamService.setLeader(teamId, formData)
      if (response.data && response.data.Success) {
        this.$toast.success('Success')
        await this.getData()
      } else {
        this.$toast.error('Error')
      }
      this.currentUser = ''
      this.dialogLead = false
    },
    async del() {
      const formdata = new FormData()
      this.errors = ''
      formdata.append('email', this.currentUser.email)
      let response = await TeamService.removeMember(
        this.$route.params.id,
        formdata
      )
      if (response.data && response.data.Success) {
        let index = this.members.indexOf(this.currentUser)
        this.members.splice(index, 1)
        this.$toast.success('Success')
      } else {
        this.$toast.error('Error')
      }
      this.currentUser = ''
      this.dialog = false
    },
    async move() {
      const formData = new FormData()
      formData.append('current_team_id', this.$route.params.id)
      formData.append('user_id', this.currentUser.user)
      formData.append('new_team_id', this.new_team)
      let response = await TeamService.moveTeam(formData)
      if (response.data && response.data.Success) {
        let index = this.members.indexOf(this.currentUser)
        this.members.splice(index, 1)
        this.$toast.success('Success')
      } else {
        this.$toast.error('Error')
      }
      this.dialogMove = false
    },
    closeDialogMove() {
      this.dialogMove = false
      this.currentUser = ''
    }
  },
  computed: {
    filter: function() {
      return this.rows.filter((row) => {
        return row.email.match(this.mailTo)
      })
    }
  },
  async created() {
    await this.getData()
  }
}
</script>
