import axios from 'axios'

export const StatisticUser = {
    getStatisticUser
}

function getStatisticUser() {
    return axios.get('workday/calendar/user')
        .then(res => handleData(res.data))
}

function handleData(data) {
    return data.map(item => {
        if (item.type === 'All day') {
            return {
                'id': item.id,
                'title': item.request_off.type_off.title,
                'start': item.date,
                'reason': item.request_off.reason,
                'type': item.type,
                'lunch': item.lunch ? 'Yes' : 'No'
            }
        } else {
            return {
                'id': item.id,
                'title': item.request_off.type_off.title,
                'start': item.type === 'Morning' ? item.date + 'T08:30:00' : item.date + 'T13:30:00',
                'end': item.type === 'Morning' ? item.date + 'T12:00:00' : item.date + 'T17:30:00',
                'reason': item.request_off.reason,
                'type': item.type,
                'lunch': item.lunch ? 'Yes' : 'No'
            }
        }
    })
}
