<template>
    <div  class="m-5">
        <h5 class="card-header text-white bg-info text-left py-4">
            <strong>Bank Account</strong>
            <span class="float-right">
                <font-awesome-icon v-if="userBank.account_number" :icon="['fas', 'edit']" @click="toggleEditing()" />
                <font-awesome-icon v-else :icon="['fas', 'plus']" @click="toggleEditing()" />
            </span>
        </h5>
        <form class="bg-white p-4 text-info">
            <div v-if="userBank.account_number">
                <div class="row">
                    <label class="col-sm-3">Account number</label>
                    <div class="col-sm-8 ml-2">
                        <p class="text-dark">
                            {{ userBank.account_number }}
                        </p>
                    </div>
                </div>
                <div class="row">
                    <label class="col-sm-3">Bank name</label>
                    <div class="col-sm-8 ml-2">
                        <p class="text-dark">
                            {{ userBank.bank_name }}
                        </p>
                    </div>
                </div>
                <div class="row">
                    <label class="col-sm-3">Bank branch</label>
                    <div class="col-sm-8 ml-2">
                        <p class="text-dark">
                            {{ userBank.branch }}
                        </p>
                    </div>
                </div>
            </div>
        </form>
        <b-modal ref="create_bank_modal" :ok-disabled="this.$v.$invalid" ok-title="Confirm" ok-variant="info" @ok="saveBank()">
            <slot>
                <h5 class="text-center">
                    <b-badge class="p-3 w-100">
                        Input your bank account
                    </b-badge>
                </h5>
                <div class="col-sm-12 mx-auto row my-4">
                    <form id="id_invite_form" class="bg-white p-4 text-info">
                        <div
                            class="form-group row"
                            :class="{
                                'form-group--error': formData.account_number.$error
                            }"
                        >
                            <label class="col-sm-5 col-form-label">
                                Account number
                                <sup class="text-danger">*</sup>
                            </label>
                            <div class="col-sm-7 pt-2">
                                <input v-model="formData.account_number" type="text" class="form-control row" />
                                <div v-if="!$v.formData.account_number.required" class="error">
                                    Field is required
                                </div>
                            </div>
                        </div>
                        <div class="form-group row">
                            <label class="col-sm-5 col-form-label">
                                Bank
                                <sup class="text-danger">*</sup>
                            </label>
                            <div class="col-sm-7 pt-2 inner-addon right-addon row">
                                <select v-model="formData.bank" class="form-control">
                                    <option v-for="option in this.bankList" :value="option.id">
                                        {{ option.name }}
                                    </option>
                                </select>
                            </div>
                        </div>
                        <div class="form-group row">
                            <label class="col-sm-5 col-form-label">
                                Bank branch
                            </label>
                            <div class="col-sm-7 pt-2 inner-addon right-addon row">
                                <input v-model="formData.branch" class="form-control" type="text" />
                            </div>
                        </div>
                    </form>
                </div>
            </slot>
        </b-modal>
    </div>
</template>

<script>
import { required } from 'vuelidate/lib/validators'
import BankService from '../../../services/profile/bank.js'
import '../../../assets/scss/main.scss'
export default {
    data: () => ({
        userId: '',
        admin: localStorage.getItem('is_admin'),
        selected: '',
        formData: {
            account_number: '',
            bank: null,
            branch: ''
        },
        keysToDelete: ['user', 'id', 'create_at', 'delete_at'],
        userBank: {},
        bankList: []
    }),
    validations: {
        formData: {
            account_number: {
                required
            }
        }
    },
    created() {
        this.userId = this.$route.params.id
        this.getData()
    },
    methods: {
        toggleEditing() {
            this.showForm()
        },
        showForm() {
            this.$refs.create_bank_modal.show()
        },
        async getData() {
            this.bankList = (await BankService.getBankList()).data
            this.userBank = (await BankService.get(this.userId)).data
            this.formData = { ...this.userBank }
            this.keysToDelete.forEach((key) => delete this.formData[key])
        },
        async saveBank() {
            try {
                this.userBank = !this.userBank.account_number
                    ? (await BankService.create(this.userId, this.formData)).data
                    : (await BankService.update(this.userId, this.formData)).data
            } catch (error) {
                if (error.response && error.response.data) {
                    this.$emit('openSnack', error.response.data, 'error')
                }
            }
        }
    }
}
</script>
