import BaseService from '../base'


class BankService extends BaseService {
    get entity() {
        return 'bank'
    }

    create(id, data) {
        const bank = this.request({ auth: true }).post(`user/${this.entity}/${id}/add`, data)
        return bank
    }

    get(id) {
        const bank = this.request({ auth: true }).get(`user/${this.entity}/${id}`).catch(err => console.log(err))
        return bank
    }

    update(id, data) {
        const bank = this.request({ auth: true }).put(`user/${this.entity}/${id}`, data)
        return bank
    }

    delete(id) {
        return this.request({ auth: true }).delete(`user/${this.entity}/${id}`)
    }

    getBankList() {
        const banks = this.request({ auth: true }).get(`user/bank_list`)
        return banks
    }

}

export default new BankService()
