<template>
  <div class="main-content">
    <v-app class="mr-3 bg-light">
      <v-row class="m-4">
        <v-col cols="2">
          <v-row style="height: 60px;">
          </v-row>
          <v-row>
            <v-list class="bg-light">
              <v-list-group :value="true">
                <template v-slot:activator>
                  <v-list-item-content>
                    <v-list-item-title>My Calendar</v-list-item-title>
                  </v-list-item-content>
                </template>
                <v-list-item>
                  <v-checkbox
                    v-model="lunch"
                    color="#F2CD5E"
                    label="My Lunch"
                    @click="checkBox"
                    class="m-0 p-0"
                  >My Lunch
                  </v-checkbox>
                </v-list-item>
                <v-list-item>
                  <v-checkbox
                    color="info"
                    v-model="leave"
                    label="My Leave"
                    @click="checkBox"
                    class="m-0 p-0"
                  ></v-checkbox>
                </v-list-item>
              </v-list-group>
            </v-list>
          </v-row>
          <v-row>
            <div class="m-3">
              <strong>Descriptions</strong>
            </div>
            <div class="text-center rounded text-light color-bg m-2 w-100">
              <small>Lunar Day</small>
            </div>
            <div class="text-center rounded text-dark color-bg-2 m-2 w-100">
              <small>Current Day</small>
            </div>
          </v-row>
        </v-col>
        <v-col cols="10">
          <FullCalendar ref="fullCalendar" :options="calendarOptions" />
        </v-col>
      </v-row>
      <v-dialog v-model="dialogDetail" max-width="400">
        <v-card v-if="selectedEvent.has_veggie != null">
          <v-card-title class="headline">
            {{ selectedEvent.title }}
          </v-card-title>
          <v-card-text v-for="(item, index) in selectedEvent.detail" :key="index">
            {{ index + 1 }} - {{ item.profile.name }}
          </v-card-text>
        </v-card>
        <v-card v-else>
          <v-card-title class="headline">
            {{ selectedEvent.title }}
          </v-card-title>
          <v-card-text v-for="(item, index) in selectedEvent.detail" :key="index">
            {{ index + 1 }} - {{ item.name }} - {{item.typeOff}} - {{item.timeType}}
          </v-card-text>
        </v-card>
      </v-dialog>
    </v-app>
  </div>
</template>
<script>
import 'vue-cal/dist/vuecal.css'
import { CalendarAdminServices } from '../../services/ company_calendar.services'
import UserLunchService from '@/services/user-lunch'
import FullCalendar from '@fullcalendar/vue'
import dayGridPlugin from '@fullcalendar/daygrid'
import timeGridPlugin from '@fullcalendar/timegrid'
import interactionPlugin from '@fullcalendar/interaction'
import moment from 'moment'

export default {
  name: 'datepicker',
  components: {
    FullCalendar
  },
  middleware: 'authen',
  data() {
    return {
      selectedEvent: {},
      title: '',
      content: '',
      dialogDetail: false,
      lunch: true,
      leave: true,
      items: [
        {
          text: 'Homepage',
          to: { name: 'Index' }
        },
        {
          text: 'Company Calendar',
          active: true
        }
      ],
      showDialog: false,
      calendarOptions: {
        themeSystem: 'bootstrap',
        plugins: [
          dayGridPlugin,
          timeGridPlugin,
          interactionPlugin // needed for dateClick
        ],
        headerToolbar: {
          left: 'prev,next today',
          center: 'title',
          right: 'dayGridMonth,timeGridWeek,timeGridDay'
        },
        contentHeight: 800,
        events: [],
        eventClick: this.handleEventClick
      },
      leaveEvents: [],
      lunchEvents: [],
      lunarDay: []
    }
  },
  head() {
    return {
      title: 'Company Calendar'
    }
  },
  async mounted() {
    // this.lunarDays()
    await this.getLeave()
    await this.getUserLunches()
  },
  methods: {
    lunarDays() {
      let calendarApi = this.$refs.fullCalendar.getApi()
      let dateCalendar = calendarApi.getDate()
      let year = dateCalendar.getFullYear()
      let month = dateCalendar.getMonth()
      for (let day = 0; day < 32; day++) {
        let lunarDate = moment().year(year).month(month).date(day).lunar().format('DD')
        if (lunarDate == 15 || lunarDate == 1) {
          let date = `${year}-${month + 1}-${day}`
          let nextDate = `${year}-${month + 1}-${day + 1}`
          if (month < 9) {
            date = `${year}-0${month + 1}-${day}`
            nextDate = `${year}-0${month + 1}-${day + 1}`
            if (day == 31) {
              nextDate = `${year}-0${month + 2}-01`
            }
            if (day < 10) {
              date = `${year}-0${month + 1}-0${day}`
              nextDate = `${year}-0${month + 1}-0${day + 1}`
            }
          } else {
            if (day == 31) {
              nextDate = `${year}-${month + 2}-01`
            }
            if (day < 10) {
              date = `${year}-${month + 1}-0${day}`
              nextDate = `${year}-${month + 1}-0${day + 1}`
            }
          }
          let dataDay = {
            start: date,
            end: nextDate,
            display: 'background',
            backgroundColor: '#45BCF3'
          }
          this.lunarDay.push(dataDay)
        }
      }
    },
    async getLeave() {
      await CalendarAdminServices.getCalendar()
        .then(res => {
          let ot = []
          let remote = []
          let off = []
          for (let i = 0; i < res.length; i++) {
            if (res[i].is_ot_days === 1) {
              res[i].color = '#F9844A'
              ot.push(res[i])
            } else if (res[i].is_paid_salary === true && res[i].type === 0 && res[i].add_sub_day_off === 0) {
              res[i].color = '#43AA8B'
              remote.push(res[i])
            } else {
              res[i].title = 'Days off'
              res[i].color = '#277DA1'
              off.push(res[i])
            }
          }
          this.leaveEvents = this.changeFormatLeave(off).concat(this.changeFormatLeave(ot), this.changeFormatLeave(remote))
        })
    },
    checkBox() {
      if (this.leave === false && this.lunch === true) {
        this.calendarOptions.events = this.lunchEvents.concat(this.lunarDay)
      } else if (this.leave === false && this.lunch === false) {
        this.calendarOptions.events = this.lunarDay
      } else if (this.leave === true && this.lunch === false) {
        this.calendarOptions.events = this.leaveEvents.concat(this.lunarDay)
      } else {
        this.calendarOptions.events = this.leaveEvents.concat(this.lunchEvents, this.lunarDay)
      }
    },
    handleEventClick(e) {
      this.selectedEvent = this.calendarOptions.events.find(item => item.id === e.event.id)
      this.dialogDetail = true
    },
    changeFormatLunch(arr) {
      let dataArr = arr.map(item => {
        return [item.date, item]
      })
      let mapArr = new Map(dataArr)
      let result = [...mapArr.values()]
      let newResult = result.map(item => {
        let len = []
        for (const element of arr) {
          if (item.date === element.date) {
            len.push(element)
          }
        }
        return {
          title: item.has_veggie ? `Veggie Lunch : ${len.length}` : `Lunch: ${len.length}`,
          id: item.id,
          detail: len,
          start: item.date,
          end: item.date,
          has_veggie: item.has_veggie,
          color: item.has_veggie ? '#90BE6D' : '#F9C74F'
        }
      })
      return newResult
    },
    changeFormatLeave(arr) {
      let dataArr = arr.map(item => {
        return [item.start, item]
      })
      let mapArr = new Map(dataArr)
      let result = [...mapArr.values()]
      let newResult = result.map(item => {
        let len = []
        for (const element of arr) {
          if (item.start === element.start) {
            len.push(element)
          }
        }
        return {
          title: item.title + ': ' + len.length,
          id: item.id,
          detail: len,
          start: item.start,
          end: item.end,
          color: item.color
        }
      })
      return newResult
    },
    async getUserLunches() {
      const userLunches = await UserLunchService.getAll()
      let notVeg = userLunches.data.filter(item => item.has_veggie === false)
      let hasVeg = userLunches.data.filter(item => item.has_veggie === true)
      let newNotVeg = this.changeFormatLunch(notVeg)
      let newHasVeg = this.changeFormatLunch(hasVeg)
      this.lunchEvents = newNotVeg.concat(newHasVeg)
      this.calendarOptions.events = this.leaveEvents.concat(this.lunchEvents, this.lunarDay)
    },
    hideModal() {
      this.$refs.mymodal.hide()
    }
  }
}
</script>

<style lang="scss">
@import "./css.scss";
</style>
