<template>
  <div class="m-5">
    <h5 class="card-header text-white bg-info text-left py-4">
      <strong>Contact</strong>
      <span class="float-right">
        <font-awesome-icon v-if="userContact.permanent_address"
                           v-on:click="toggleEditing"
                           :icon="['fas', 'edit']"/>
        <font-awesome-icon v-else :icon="['fas', 'plus']"
                           v-on:click="toggleEditing"/>
      </span>
    </h5>
    <form class="bg-white p-4 text-info">
      <div v-if="userContact.permanent_address">
        <div class="row">
          <label class="col-sm-3">Permanent address</label>
          <div class="col-sm-7 ml-2">
            <p class="text-dark">
              {{ userContact.permanent_address }}
            </p>
          </div>
        </div>
        <div class="row">
          <label class="col-sm-3">Temporary address</label>
          <div class="col-sm-8 ml-2">
            <p class="text-dark">
              {{ userContact.temporary_address }}
            </p>
          </div>
        </div>
        <div class="row">
          <label class="col-sm-3">Household registration number</label>
          <div class="col-sm-8 ml-2">
            <p class="text-dark">
              {{ userContact.household_registration_number }}
            </p>
          </div>
        </div>
        <div class="row">
          <label class="col-sm-3">Phone emergency</label>
          <div class="col-sm-8 ml-2">
            <p class="text-dark">
              {{ userContact.contact_emergency }}
            </p>
          </div>
        </div>
      </div>
    </form>
    <b-modal
      ref="create_contact_modal"
      @ok="saveContact()"
      :ok-disabled="this.$v.$invalid"
      ok-title="Confirm"
      ok-variant="info"
    >
      <slot>
        <h5 class="text-center">
          <b-badge class="p-3 w-100">
            Input your contact information
          </b-badge>
        </h5>
        <div class="col-sm-12 mx-auto row my-4">
          <form id="id_invite_form" class="bg-white p-4 text-info">
            <div
              class="form-group row"
              :class="{
                'form-group--error': formData.permanent_address.$error
              }"
            >
              <label class="col-sm-5 col-form-label"
                >Permanent address<sup class="text-danger"> *</sup></label
              >
              <div class="col-sm-7 pt-2">
                <input
                  v-model="formData.permanent_address"
                  type="text"
                  class="form-control row form__input"
                />
                <div
                  class="error"
                  v-if="!$v.formData.permanent_address.required"
                >
                  Field is required
                </div>
              </div>
            </div>
            <div
              class="form-group row"
              :class="{
                'form-group--error': formData.temporary_address.$error
              }"
            >
              <label class="col-sm-5 col-form-label"
                >Temporary address<sup class="text-danger"> *</sup></label
              >
              <div class="col-sm-7 pt-2">
                <input
                  v-model="formData.temporary_address"
                  type="text"
                  class="form-control row form__input"
                />
                <div
                  class="error"
                  v-if="!$v.formData.temporary_address.required"
                >
                  Field is required.
                </div>
              </div>
            </div>
            <div
              class="form-group row"
              :class="{
                'form-group--error':
                  formData.household_registration_number.$error
              }"
            >
              <label class="col-sm-5 col-form-label"
                >Household registration number<sup class="text-danger">
                  *</sup
                ></label
              >
              <div class="col-sm-7 pt-2">
                <input
                  v-model="formData.household_registration_number"
                  type="text"
                  class="form-control row"
                />
                <div
                  class="error"
                  v-if="!$v.formData.household_registration_number.required"
                >
                  Field is required.
                </div>
                <div
                  class="error"
                  v-if="!$v.formData.household_registration_number.minLength"
                >
                  Required at least
                  {{
                    $v.formData.household_registration_number.$params.minLength
                      .min
                  }}
                  characters.
                </div>
              </div>
            </div>
            <div
              class="form-group row"
              :class="{
                'form-group--error':
                  formData.household_registration_number.$error
              }"
            >
              <label class="col-sm-5 col-form-label"
                >Phone emergency<sup class="text-danger"> *</sup></label
              >
              <div class="col-sm-7 pt-2">
                <input
                  v-model="formData.contact_emergency"
                  type="text"
                  class="form-control row"
                />
                <div
                  class="error"
                  v-if="!$v.formData.contact_emergency.required"
                >
                  Field is required.
                </div>
                <div
                  class="error"
                  v-if="!$v.formData.contact_emergency.integer"
                >
                  Contact must be number
                </div>
                <div
                  class="error"
                  v-if="!$v.formData.contact_emergency.minLength"
                >
                  Required at least
                  {{ $v.formData.contact_emergency.$params.minLength.min }}
                  digits.
                </div>
              </div>
            </div>
          </form>
        </div>
      </slot>
    </b-modal>
  </div>
</template>

<script>
import ContactService from '../../../services/profile/contact.js'
import { required, minLength, integer } from 'vuelidate/lib/validators'
import '../../../assets/scss/main.scss'
export default {
  data: () => ({
    userId: '',
    admin: localStorage.getItem('is_admin'),
    selected: '',
    formData: {
      permanent_address: '',
      temporary_address: '',
      household_registration_number: '',
      contact_emergency: ''
    },
    keysToDelete: ['user', 'id', 'create_at', 'delete_at'],
    userContact: {}
  }),
  validations: {
    formData: {
      permanent_address: {
        required
      },
      temporary_address: {
        required
      },
      household_registration_number: {
        required,
        minLength: minLength(5)
      },
      contact_emergency: {
        required,
        integer,
        minLength: minLength(9)
      }
    }
  },
  created() {
    this.userId = this.$route.params.id
    this.getData()
  },
  methods: {
    toggleEditing() {
      this.showForm()
    },
    showForm() {
      this.$refs.create_contact_modal.show()
    },
    async getData() {
      this.userContact = (await ContactService.get(this.userId)).data
      this.formData = { ...this.userContact }
      this.keysToDelete.forEach((key) => delete this.formData[key])
    },
    async saveContact() {
      try {
        this.userContact = !this.userContact.permanent_address
          ? (await ContactService.create(this.userId, this.formData)).data
          : (await ContactService.update(this.userId, this.formData)).data
      } catch (error) {
        if (error.response && error.response.data)
          this.$emit('openSnack', error.response.data, 'error')
      }
    }
  },
}
</script>
