import BaseService from '../base'
import axios from 'axios'
import { API_URL } from '../../.env'


class FamilyService extends BaseService {
    get entity() {
        return 'family'
    }

    create(id, data) {
        const familyMembers = this.request({ auth: true }).post(`user/${this.entity}/${id}/add`, data)
        return familyMembers
    }

    get(id) {
        const familyMembers = this.request({ auth: true }).get(`user/${this.entity}/${id}`)
        return familyMembers
    }

    update(id, data) {
        return this.request({ auth: true }).put(`user/${this.entity}/${id}`, data)
    }

    delete(id, family_id) {
        return axios({
            method: 'delete',
            headers: {
                Authorization: `${localStorage.getItem('token')}`
            },
            url: API_URL + `/user/${this.entity}/${id}`,
            data: {
                id: family_id
            }
        });
    }

}

export default new FamilyService()
