import BaseService from '../base'
import axios from 'axios'

class TeamService extends BaseService {
    get entity() {
        return 'team'
    }

    create(data) {
        const team = this.request({ auth: true }).post(`${this.entity}`, data)
        return team
    }

    getAll() {
        const teams = this.request({ auth: true }).get(`${this.entity}`)
        return teams
    }

    get(id) {
        const team = this.request({ auth: true }).get(`${this.entity}/${id}`)
        return team
    }

    update(id, data) {
        return this.request({ auth: true }).put(`${this.entity}/${id}`, data)
    }

    delete(id) {
        return this.request({ auth: true }).delete(`${this.entity}/${id}`)
    }

    addMember(id, data) {
        const response = this.request({ auth: true }).put(`${this.entity}/${id}/add_member`, data)
        return response
    }

    removeMember(id, data) {
        const response = this.request({ auth: true }).put(`${this.entity}/${id}/remove_member`, data)
        return response
    }

    getNewTeams(userId) {
        const response = this.request({ auth: true }).get(`${this.entity}/${userId}/get_new_teams`)
        return response
    }

    moveTeam(data) {
        const response = this.request({ auth: true }).put(`${this.entity}/move_team`, data)
        return response
    }

    setLeader(teamId, data) {
        const response = this.request({ auth: true }).put(`${this.entity}/${teamId}/set_leader`, data)
        return response
    }

    setManager(teamId, data) {
        const response = this.request({ auth: true }).put(`${this.entity}/${teamId}/set_project_manager`, data)
        return response
    }

    removeTeam(teamId) {
        const response = this.request({ auth: true }).delete(`${this.entity}/${teamId}`)
        return response
    }

}

export default new TeamService()
