<template>
  <div id="id_profile" class="main-profile row p-3 m-4">
    <table
      class="table table-bordered table-rounded table-fixed table-responsive"
    >
      <thead class="bg-info">
      <tr>
        <th scope="col" class="school">
          <button
            class="btn dropdown-toggle text-white font-weight-bold"
          >
            School name
          </button>
        </th>
        <th scope="col" class="degree">
          <button
            class="btn dropdown-toggle text-white font-weight-bold"
          >
            Degree
          </button>
        </th>
        <th scope="col" class="study">
          <button
            class="btn dropdown-toggle text-white font-weight-bold"
          >
            Major
          </button>
        </th>
        <th scope="col" class="year">
            <span class="btn text-white font-weight-bold">
              Year of graduation
            </span>
        </th>
        <th scope="col" class="notes">
          <button
            class="btn dropdown-toggle text-white  font-weight-bold"
          >
            Notes
          </button>
        </th>
        <th scope="col" class="add text-center">
          <font-awesome-icon :icon="['fas', 'plus']" class="fa-fw" @click="toggleEditing()" />
        </th>
      </tr>
      </thead>
      <tbody>
      <tr v-for="(userEducation, i) in userEducations" :key="i">
        <td class="text-dark">
          {{ userEducation.school }}
        </td>
        <td class="text-dark">
          {{ userEducation.degree }}
        </td>
        <td class="text-dark">
          {{ userEducation.field_of_study }}
        </td>
        <td class="text-dark">
          {{ userEducation.graduated_year }}
        </td>
        <td class="text-dark">
          {{ userEducation.additional_notes }}
        </td>
        <td class="w-6 text-center text-dark">
            <span
              class="mr-3"
              v-on:click="toggleEditing(userEducation)"
            >
              <font-awesome-icon :icon="['fas', 'edit']" />
            </span>
          <span

            v-on:click="toggleConfirmDelete(userEducation)"
          >
              <font-awesome-icon :icon="['fas', 'trash']" />
            </span>
        </td>
      </tr>
      </tbody>
    </table>
    <b-modal
      ref="create_education_modal"
      @ok="submitModal()"
      :ok-disabled="this.$v.$invalid"
      @hidden="resetModal"
      ok-title="Confirm"
      ok-variant="info"
    >
      <slot>
        <h5 class="text-center">
          <b-badge class="p-3 w-100">
            Input your education information
          </b-badge>
        </h5>
        <div class="col-sm-12 mx-auto row my-4">
          <form class="bg-white p-4 text-info">
            <div
              class="form-group row"
              :class="{
                'form-group--error': formData.school.$error
              }"
            >
              <label class="col-sm-5 col-form-label"
              >School name<sup class="text-danger"> *</sup></label
              >
              <div class="col-sm-7 pt-2">
                <input
                  v-model="formData.school"
                  type="text"
                  class="form-control row"
                />
                <div class="error" v-if="!$v.formData.school.required">
                  Field is required
                </div>
              </div>
            </div>
            <div
              class="form-group row"
              :class="{
                'form-group--error': formData.degree.$error
              }"
            >
              <label class="col-sm-5 col-form-label"
              >Degree<sup class="text-danger"> *</sup></label
              >
              <div class="col-sm-7 pt-2">
                <input
                  v-model="formData.degree"
                  type="text"
                  class="form-control row"
                />
                <div class="error" v-if="!$v.formData.degree.required">
                  Field is required
                </div>
              </div>
            </div>
            <div class="form-group row">
              <label class="col-sm-5 col-form-label">Year of graduation</label>
              <div class="col-sm-7 pt-2">
                <input
                  v-model="formData.graduated_year"
                  type="number"
                  min="2000"
                  max="3000"
                  class="form-control row"
                />
              </div>
            </div>
            <div
              class="form-group row"
              :class="{
                'form-group--error': formData.field_of_study.$error
              }"
            >
              <label class="col-sm-5 col-form-label"
              >Major<sup class="text-danger"> *</sup></label
              >
              <div class="col-sm-7 pt-2">
                <input
                  v-model="formData.field_of_study"
                  type="text"
                  class="form-control row"
                />
                <div class="error" v-if="!$v.formData.field_of_study.required">
                  Field is required
                </div>
              </div>
            </div>
            <div class="form-group row">
              <label class="col-sm-5 col-form-label">Notes</label>
              <div class="col-sm-7 pt-2">
                <input
                  v-model="formData.additional_notes"
                  type="text"
                  class="form-control row"
                />
              </div>
            </div>
          </form>
        </div>
      </slot>
    </b-modal>
    <b-modal
      ref="delete_education_modal"
      @ok="deleteEducation()"
      ok-title="Confirm"
      ok-variant="danger"
    >
      <slot>
        <h5 class="text-center">
          <p class="p-3 w-100 text-dark">
            Do you want to remove?
          </p>
        </h5>
        <div class="col-sm-12 mx-auto row my-4"></div>
      </slot>
    </b-modal>
  </div>
</template>

<script>
import EducationService from '../../../services/profile/education.js'
import { required } from 'vuelidate/lib/validators'
import '../../../assets/scss/main.scss'
import './school.scss'

export default {
  data: () => ({
    educationToDelete: '',
    selected: '',
    formData: {
      education_id: '',
      school: '',
      degree: '',
      field_of_study: '',
      graduated_year: null,
      additional_notes: null
    },
    userId: '',
    admin: localStorage.getItem('admin'),
    userEducations: []
  }),
  validations: {
    formData: {
      school: {
        required
      },
      degree: {
        required
      },
      field_of_study: {
        required
      }
    }
  },
  methods: {
    submitModal() {
      !this.formData.id ? this.createEducation() : this.updateEducation()
    },
    toggleEditing(userEducation) {
      if (userEducation) this.formData = { ...userEducation }
      this.showForm()
    },
    toggleConfirmDelete(userEducation) {
      this.educationToDelete = userEducation
      this.$refs.delete_education_modal.show()
    },
    resetModal() {
      this.formData = {
        id: '',
        school: '',
        degree: '',
        field_of_study: '',
        graduated_year: null,
        additional_notes: null
      }
    },
    showForm() {
      this.$refs.create_education_modal.show()
    },
    async getData() {
      this.userEducations = (await EducationService.get(this.userId)).data
    },
    async createEducation() {
      try {
        this.userEducations.push(
          (await EducationService.create(this.userId, this.formData)).data
        )
      } catch (error) {
        if (error.response && error.response.data) {
          this.$emit('openSnack', error.response.data, 'error')
        }
      }
    },
    async updateEducation() {
      try {
        let temp_education = (
          await EducationService.update(this.userId, this.formData)
        ).data
        for (let i in this.userEducations) {
          if (this.userEducations[i].id === temp_education.id) {
            this.userEducations[i] = { ...temp_education }
            break
          }
        }
      } catch (error) {
        if (error.response && error.response.data) {
          this.$emit('openSnack', error.response.data, 'error')
        }
      }
    },
    async deleteEducation() {
      try {
        await EducationService.delete(this.userId, this.educationToDelete.id)
        this.userEducations.splice(
          this.userEducations.indexOf(this.educationToDelete),
          1
        )
        this.educationToDelete = {}
      } catch (error) {
        if (error.response && error.response.data) {
          this.$emit('openSnack', error.response.data, 'error')
        }
      }
    }
  },
  mounted() {
    this.userId = this.$route.params.id
    this.getData()
  }
}
</script>
