<template>
  <div class="d-flex justify-content-center">
    <div class="position-absolute sticky-top mt-2 mybcrumb">
      <b-breadcrumb :items="items"></b-breadcrumb>
    </div>
    <div class="table-responsive p-3 col-md-7">
      <h2 class="card-header text-white bg-info text-left py-4">
        Create New Team
      </h2>
      <div
        class="text-danger pb-2 pl-4 pt-2 text-center"
        v-show="error.length > 0"
      >
        <span>{{ error }}</span>
      </div>
      <form class="bg-white p-4 text-info pl-5">
        <div
          class="form-group row"
          :class="{
            'form-group--error': formData.team_name.$error
          }"
        >
          <label class="col-sm-2 col-form-label mt-3"
          >Team Name<sup class="text-danger"> *</sup></label
          >
          <div class="col-sm-9">
            <input
              v-model="formData.team_name"
              type="text"
              class="form-control"
              placeholder="Team Name"
            />
            <div class="error" v-if="!$v.formData.team_name.required">
              Field is required
            </div>
          </div>
        </div>
        <div
          class="form-group row d-flex align-items-center"
          :class="{
            'form-group--error': formData.team_email.$error
          }"
        >
          <label class="col-sm-2 col-form-label">
            Team Email
          </label>
          <div class="col-sm-9">
            <input
              v-model="formData.team_email"
              type="email"
              class="form-control"
              placeholder="Email"
            />
            <div class="error" v-if="!$v.formData.team_email.email">
              Not a valid email
            </div>
          </div>
        </div>
        <div class="form-group row d-flex align-items-center">
          <label class="col-sm-2 col-form-label"
          >Team Leader<sup class="text-danger"> *</sup></label
          >
          <div class="col-sm-9">
            <div class="custom-file">
              <input
                class="btn mr-2 border-dark"
                list="names"
                @keyup="filterUsers"
                @focus="filterUsers"
                v-model="formData.team_leader"
              />
              <datalist id="names">
                <option v-for="user in listUsers" :value-data="user.name">
                  {{ String(user.name).concat(` (${String(user.email)})`) }}
                </option>
              </datalist>
            </div>
          </div>
        </div>
        <br />
        <div class="form-group row ">
          <div class="col-sm-11 d-flex">
            <b-button class="btn btn-info ml-auto" @click="submit()">Create</b-button>
          </div>
        </div>
      </form>
    </div>
  </div>
</template>
<script>
import axios from 'axios'
import { required, email } from 'vuelidate/lib/validators'

export default {
  name: 'CreateTeam',
  middleware: 'authen',
  data() {
    return {
      error: '',
      team_email: '',
      team_name: '',
      team_leaders: '',
      team_leader_email: '',
      items: [
        {
          text: 'Homepage',
          to: { name: 'Index' }
        },
        {
          text: 'Team',
          to: { name: 'team' }
        },
        {
          text: 'Create',
          active: true
        }
      ],
      formData: {
        team_name: '',
        team_email: '',
        team_leader: ''
      },
      managerEmail: '',
      managerId: '',
      listUsers: []
    }
  },
  validations: {
    formData: {
      team_name: {
        required
      },
      team_email: {
        email
      }
    }
  },
  head() {
    return {
      title: 'Create new teams'
    }
  },
  methods: {
    async filterUsers(event) {
      return axios.get(`/team/send_leader?name=${event.target.value}`).then((res) => {
        this.listUsers = []
        res.data.forEach((data) => {
          this.listUsers.push({
            email: data.email,
            name: data.name,
            id: data.id
          })
        })
      })
    },
    sortFunction(arr) {
      return arr.slice().sort(function(a, b) {
        return a.name
          .split(' ')
          [a.name.split(' ').length - 1].localeCompare(
          b.name.split(' ')[b.name.split(' ').length - 1]
        )
      })
    },
    async submit() {
      const index = this.formData.team_leader.indexOf('(')
      this.formData.team_leader = this.formData.team_leader.substring(index + 1, this.formData.team_leader.length - 1)
      return axios({
        method: 'POST',
        url: `team/`,
        data: this.formData

      })
        .then((res) => {
          this.$router.push('/team')
        })
        .catch(() => {
          this.error = 'Input is not valid'
        })
    }
  }
}
</script>
