<template>
  <div class="main-content">
    <div class="row position-absolute sticky-top mt-2 mybcrumb">
      <b-breadcrumb :items="items"></b-breadcrumb>
    </div>
    <div id="id_profile" class="main row col-8 mx-auto">
      <table class="table table-bordered table-striped text-center">
        <thead class>
        <tr class>
          <th scope="col" class="w-15">
            #
          </th>
          <th scope="col" class="w-35">
            Date
          </th>
          <th scope="col" class="w-50">
            Description
          </th>
          <th>
            <font-awesome-icon class="fa-fw" :icon="['fas', 'plus']" v-on:click="add_holiday" />
          </th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="(item, index) in list_holiday">
          <td>
            {{ index + 1 }}
          </td>
          <td>
            {{ formatDate(item.date) }}
          </td>
          <td>
            {{ item.descriptions }}
          </td>
          <td>
            <font-awesome-icon class="fa-fw" :icon="['fas', 'trash']" v-on:click="remove_holiday(item.id)" />
          </td>
        </tr>
        </tbody>
      </table>
    </div>
    <v-app>
      <v-dialog v-model="dialog" max-width="600px">
        <v-card>
          <v-card-title class="headline grey lighten-2">
            Add holidays
          </v-card-title>
          <v-card-text>
            <div class="md-form col-md-6 row d-flex justify-content-between ml-3">
              <label class="in">Choose: </label>
              <flat-pickr
                v-model="date"
                :config="{
                    altInput: true,
                    altFormat: 'd-m-Y',
                    dateFormat: 'Y-m-d',
                    disable:[datePicker],
                }"
                class="form-control bg-white"
                placeholder="Select date"
                name="date"
                required
              />
            </div>
            <div class="ml-6">
              <label>Description: </label>
              <textarea class="form-control" v-model="description" rows="3"></textarea>
            </div>

          </v-card-text>
          <v-card-actions class="mr-5">
            <v-spacer></v-spacer>
            <v-btn
              color="dark"
              text
              @click="dialog = false"
            >
              Close
            </v-btn>
            <v-btn
              color="#43c9d0"
              dark
              @click="add_more"
            >
              Add more
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-app>
  </div>
</template>
<script>
import flatPickr from 'vue-flatpickr-component'
import 'flatpickr/dist/flatpickr.css'
import { HolidayServices } from '../../services'
import { mapMutations, mapGetters } from 'vuex'
import Swal from 'sweetalert2'

export default {
  name: 'Holiday',
  middleware: 'authen',
  components: {
    flatPickr
  },
  computed: mapGetters({
    showNoti: 'sidebar/showNoti'
  }),
  created: function() {
    HolidayServices.getHolidays().then(res => {
      this.list_holiday = [...res.data].sort((a, b) => this.func_sort(a, b))
    })
  },
  data() {
    return {
      list_holiday: [],
      dialog: false,
      date: null,
      description: '',
      items: [
        {
          text: 'Homepage',
          to: { name: 'Index' }
        },
        {
          text: 'Types of Leaves',
          to: { name: 'type_off' }
        },
        {
          text: 'Holidays',
          active: true
        }
      ]
    }
  },
  head() {
    return {
      title: 'Holidays'
    }
  },
  methods: {
    add_more() {
      this.dialog = false
      if (this.date && this.description) {
        console.log(this.description)
        HolidayServices.addHoliday({
          'date': this.date,
          'descriptions': this.description
        })
          .then(res => {
            if (res.status === 200) {
              this.list_holiday = [...this.list_holiday, res.data].sort((a, b) => this.func_sort(a, b))
              this.$toast.success('Add successfully', { position: 'top-right' })
              this.date = null
              this.description = ''
            }
          })
          .catch(res => {
            this.$toast.error('An error occurred', { position: 'top-right' })
          })
      }
    },
    func_sort(a, b) {
      return new Date(a.date) - new Date(b.date)
    },
    add_holiday() {
      this.dialog = true
    },
    async remove_holiday(id) {
      const obj = this.list_holiday.find(item => item.id === id)
      const title = `Do you want to delete the date' ${obj.date}' ?`
      this.confirm(title)
        .then(result => {
          if (result.isConfirmed) {
            HolidayServices.removeHoliday(id)
              .then(res => {
                if (res.status === 204) {
                  this.$toast.success('Deleted successfully', { position: 'top-right' })
                  const index = this.list_holiday.indexOf(obj)
                  this.list_holiday = [...this.list_holiday.slice(0, index), ...this.list_holiday.slice(index + 1)].sort((a, b) => this.func_sort(a, b))
                } else {
                  this.$toast.error('An error occurred', { position: 'top-right' })
                }
              })
          }
        })
    },
    datePicker: function(date) {
      return (date.getFullYear() === new Date().getFullYear() + 1)
    },
    formatDate(date) {
      var d = new Date(date),
        month = '' + (d.getMonth() + 1),
        day = '' + d.getDate(),
        year = d.getFullYear()
      if (month.length < 2) {
        month = '0' + month
      }
      if (day.length < 2) {
        day = '0' + day
      }
      return [year, month, day].join('-')
    },
    confirm(title) {
      return Swal.fire({
        title: title,
        showCancelButton: true,
        confirmButtonColor: '#43c9d0',
        cancelButtonColor: 'dark',
        confirmButtonText: 'Yes, delete it!'
      })
    }
  }
}
</script>
<style lang="scss" scoped>
@import "./style.scss";
</style>
