        this.filterDate()
      }
    },
    dateMyRequest() {
      if (this.dateMyRequest && this.typeFilterMyRequest) {
        this.filterDateMyRequest()
      }
    },
    page() {
      this.filter()
    }
  },
  head() {
    return {
      title: 'Leave Management'
    }
  },
  methods: {
    async asyncDataMyRequest(page) {
      this.loadingMyRequest = true
      let myRequest = await getMyRequest(page)
      if (myRequest.status === 200 && myRequest.data) {
        this.pageCountRequest = Math.ceil(myRequest.data.length / 10) ? Math.ceil(myRequest.data.length / 10) : 1
        this.dessertsRequest = myRequest.data
      }
      this.loadingMyRequest = false
    },
    async action() {
      let form_data = new FormData()

      form_data.append('comment', this.comment)
      form_data.append('request_off_id', this.dataDialog.id)
      form_data.append('action', this.dataDialog.action)
      this.dialog = false
      this.loadingDialog = true
      let respone = await actionRequest(form_data)
      if (respone.status === 201 && respone.data) {
        this.$set(this.acts, this.dataDialog.index, respone.data)
        this.$toast.success('Success')
      } else {
        this.$toast.error('Error')
      }
      this.comment = ''
      this.loadingDialog = false
    },
    rejectItem(item) {
      this.editedIndex = this.desserts.indexOf(item)
      this.actionItem = Object.assign({}, item)
      this.$set(this.actionItem, 'action', 'Rejected')
      this.dialog = true
    },

    approveItem(item) {
      this.editedIndex = this.desserts.indexOf(item)
      this.actionItem = Object.assign({}, item)
      this.$set(this.actionItem, 'action', 'Approved')
      this.dialog = true
    },

    close() {
      this.dialog = false
      this.$nextTick(() => {
        this.actionItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
    },

    async actionRequest() {
      let form_data = new FormData()
      form_data.append('comment', this.actionItem.comment)
      form_data.append('request_off_id', this.actionItem.request_off.id)
      form_data.append('action', this.actionItem.action)
      this.loadingDialog = true
      let respone = await actionRequest(form_data)
      if (respone.status === 201 && respone.data) {
        this.$set(this.desserts, this.editedIndex, respone.data)
        this.$toast.success('Success')
      } else {
        this.$toast.error('Error')
      }
      this.comment = ''
      this.loadingDialog = false
      this.close()
    },
    async asyncDataActive() {
      this.loadingActive = true
      let response = await getManagementRequestOff()

      if (response && response.status === 200 && response.data.results) {
        this.pageCount = response.data.page_number
        this.desserts = response.data.results
      }
      this.loadingActive = false
    },
    async refreshDataActive() {
      await this.asyncDataActive()
      this.search = ''
      this.typeFilter = ''
      this.statusFilter = ''
      this.date = new Date().toISOString().substr(0, 10)
    },
    async refreshDataMyRequest() {
      await this.asyncDataMyRequest()
      this.searchRequest = ''
      this.typeFilterMyRequest = ''
      this.statusFilterMyRequest = ''
      this.dateMyRequest = new Date().toISOString().substr(0, 10)
    },
    rejected_reason(check, comments) {
      if (check === 'Rejected') {
        this.reasonDialog = true
        for (var index in comments) {
          if (comments[index]) {
            this.reasonReject = comments[index]
          }
        }
        if (!this.reasonReject) {
          this.reasonReject = 'No reason'
        }
      }
    },
    async withdraw(item) {
      if (!confirm('Are you sure you want to cancel request?')) {
        return
      }
      this.editedIndexRequest = this.dessertsRequest.indexOf(item)
      this.actionItemRequest = Object.assign({}, item)

      let form_data = new FormData()
      form_data.append('request_off_id', item.id)
      form_data.append('action', 'Cancel')
      this.loadingDialog = true
      let respone = await actionRequest(form_data)
      if (respone.status === 201 && respone.data) {
        this.$set(this.dessertsRequest, this.editedIndexRequest, respone.data)
        this.$toast.success('Success')
      } else {
        this.$toast.error('Error: Over time')
      }
      this.comment = ''
      this.loadingDialog = false
    },
    async filter() {
      let data = this.date.split('-')
      let response = ''
      this.loadingActive = true
      switch (this.typeFilter) {
        case 'day':
          response = await getManagementRequestOff(this.page, 10, data[0], data[1], data[2], this.statusFilter, this.search)
          break
        case 'month':
          response = await getManagementRequestOff(this.page, 10, data[0], data[1], '', this.statusFilter, this.search)
          break
        case 'year':
          response = await getManagementRequestOff(this.page, 10, data[0], '', '', this.statusFilter, this.search)
          break
        default:
          response = await getManagementRequestOff(this.page, 10, '', '', '', this.statusFilter, this.search)
      }
      if (response && response.status === 200 && response.data) {
        this.pageCount = response.data.page_number
        this.desserts = response.data.results
      } else {
        this.page = 1
        this.desserts = response
        this.pageCount = 0
      }
      this.loadingActive = false
    },
    async filterDate() {
      this.page = 1
      await this.filter()
    },
    async filterDateMyRequest() {
      this.loadingMyRequest = true
      let data = this.dateMyRequest.split('-')
      let response = ''
      switch (this.typeFilterMyRequest) {
        case 'day':
          response = await getMyRequest(data[0], data[1], data[2] + 1, this.statusFilterMyRequest)
          break
        case 'month':
          response = await getMyRequest(data[0], data[1], '', this.statusFilterMyRequest)
          break
        case 'year':
          response = await getMyRequest(data[0], '', '', this.statusFilterMyRequest)
          break
        default:
          response = await getMyRequest('', '', '', this.statusFilterMyRequest)
      }
      if (response && response.status === 200 && response.data) {
        this.pageCountRequest = Math.ceil(response.data.length / 10)
        this.dessertsRequest = response.data
      }
      this.loadingMyRequest = false
    }
  }
}
</script>
