<template>
  <div id="app" class="ml-5 mr-5">
    <template>
      <v-row justify="center">
        <v-dialog
          v-model="dialogCancel"
          persistent
          max-width="500"
        >
          <div class="fixed-top">
            <v-card class="mx-auto" width="500">
              <v-card-title class="headline">
                Are you want to delete the event?
              </v-card-title>
              <v-card-actions>
                <v-btn
                  dark
                  class="btn btn-width btn-secondary m-2"
                  text
                  @click="closeDialog"
                >
                  No
                </v-btn>
                <v-spacer></v-spacer>
                <v-btn
                  class="btn btn-width btn-info m-2"
                  text
                  @click="cancelEvent"
                >
                  Yes
                </v-btn>
              </v-card-actions>
            </v-card>
          </div>
        </v-dialog>
      </v-row>
    </template>
    <template>
      <v-row justify="center">
        <v-dialog
          v-model="dialogEventDetail"
          persistent
          max-width="800"
        >
          <v-card class="mx-auto" width="800">
            <v-card-title class="headline">
              Detail
            </v-card-title>
            <v-divider class="mt-0 pt-0"></v-divider>
            <v-card-text>
              <p class="text-dark font-weight-black"> Wishes: {{ eventOfUser.wishes }}</p>
              <p class="text-dark font-weight-black">Image:</p>
              <v-img contain max-width="800" :src="urlImage"></v-img>
            </v-card-text>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn
                dark
                class="btn btn-width btn-secondary m-2"
                text
                @click="closeEventDetail"
              >
                Close
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-row>
    </template>
    <v-app id="inspire">
      <v-card outlined class="m-4">
        <v-data-table
          :headers="headers"
          :items="desserts"
          hide-default-footer
          :page.sync="page"
          class="table-striped"
          :items-per-page="itemsPerPage"
          :search="search"
          @page-count="pageCount = $event"
        >
          <template v-slot:top>
            <v-toolbar flat color="white" class="m-3">
              <h2>Manage Events</h2>
              <v-spacer></v-spacer>
              <v-col cols="3">
                <v-text-field
                  v-model="search"
                  append-icon="mdi-magnify"
                  label="Search"
                  single-line
                  hide-details
                  placeholder="Enter name ..."
                ></v-text-field>
              </v-col>
              <v-col cols="2">
                <b-form-select @change="getUsers" v-model="selected" :options="options">
                  <template v-slot:first>
                    <b-form-select-option value="" disabled>Select month</b-form-select-option>
                  </template>
                </b-form-select>
              </v-col>
                <v-btn
                  v-if="desserts[0]"
                  dark
                  depressed
                  text
                  class="mb-2 mt-2 btn btn-info"
                  @click="dialog=true"
                >Set Events For All
                </v-btn>
              <v-dialog v-model="dialog" max-width="500px">
                <v-card>
                  <v-card-title>
                    <span class="headline">{{ formTitle }}</span>
                  </v-card-title>

                  <v-card-text>
                    <v-form
                      ref="form"
                      lazy-validation
                    >
                      <v-textarea
                        v-model="wishes"
                        label="Wishes"
                        rows="1"
                        required
                        :rules="[rules]"
                      ></v-textarea>
                      <v-file-input
                        v-model="image"
                        placeholder="Upload your image"
                        label="File input"
                        multiple
                        :rules="[rules]"
                        prepend-icon="mdi-paperclip"
                      >
                        <template v-slot:selection="{ text }">
                          <v-chip
                            small
                            label
                            color="primary"
                          >
                            {{ text }}
                          </v-chip>
                        </template>
                      </v-file-input>
                    </v-form>
                  </v-card-text>

                  <v-card-actions>
                    <v-btn dark class="btn btn-secondary font-weight-bold btn-width m-2" text @click="close">Cancel
                    </v-btn>
                    <v-spacer></v-spacer>
                    <v-btn v-show="eventOfUser.wishes" dark class="btn btn-info font-weight-bold btn-width m-2" text
                           @click="submitUpdate()">Update
                    </v-btn>
                    <v-btn v-show="!eventOfUser.wishes" dark class="btn btn-info font-weight-bold btn-width m-2" text @click="save()">Save</v-btn>
                  </v-card-actions>
                </v-card>
              </v-dialog>
            </v-toolbar>
          </template>
          <template v-slot:item.actions="{ item }">
            <v-btn small depressed v-if="item.event" type="submit" class="mr-2 btn-success " dark text @click="update(item)">
              Edit
            </v-btn>
            <v-btn small depressed v-if="item.event" type="submit"  color="red" dark @click="cancelItem(item)">
              Delete
            </v-btn>
            <v-btn small depressed v-else type="submit" dark color="#43c9d0"  @click="editItem(item)">
              Set Event
            </v-btn>
          </template>
          <template v-slot:item.detail="{ item }">
            <button v-show="item.event" class="btn btn-link" @click="showEvent(item)">Detail</button>
          </template>
          <template v-slot:no-data>
              {{msg}}
          </template>
        </v-data-table>
      </v-card>
      <v-pagination color="#43c9d0" v-model="page" :length="pageCount"></v-pagination>

    </v-app>
  </div>
</template>
<script>
import EventService from '../../services/event'
import { IMAGE_URL } from '../../.env.js'

export default {
  middleware: 'authen',
  data: () => ({
    dialog: false,
    dialogEventDetail: false,
    dialogCancel: false,
    rules: value => !!value || 'Required.',
    selected: new Date().getMonth() + 1,
    options: [
      {value: 1, text: 'January'},
      {value: 2, text: 'February'},
      {value: 3, text: 'March'},
      {value: 4, text: 'April'},
      {value: 5, text: 'May'},
      {value: 6, text: 'July'},
      {value: 8, text: 'August'},
      {value: 9, text: 'September'},
      {value: 10, text: 'October'},
      {value: 11, text: 'November'},
      {value: 12, text: 'December'},
    ],
    headers: [
      {
        text: 'Name',
        align: 'start',
        value: 'name'
      },
      {
        text: 'Birthday',
        value: 'birth_day'
      },
      {
        text: 'Personal Email',
        value: 'personal_email'
      },
      {
        text: 'Detail',
        align: 'start',
        sortable: false,
        value: 'detail'
      },
      {
        text: 'Actions',
        value: 'actions',
        align: 'center',
        sortable: false
      }
    ],
    search: '',
    page: 1,
    pageCount: 0,
    itemsPerPage: 10,
    desserts: [],
    editedIndex: -1,
    editedItem: {
      wishes: '',
      image: null
    },
    defaultItem: {
      wishes: '',
      image: null
    },
    events: [],
    image: null,
    wishes: '',
    eventOfUser: '',
    msg: ''
  }),

  computed: {
    formTitle() {
      return this.editedIndex === -1 ? 'Set event for all' : 'Set event for user'
    },
    urlImage() {
      return IMAGE_URL + this.eventOfUser.image
    },
    rules() {
      const rules = []
      const rule =
        v => (v || '').length <= 255 ||
          `A maximum of 255 characters is allowed`
      rules.push(rule)
      return rules
    }
  },

  watch: {
    dialog(val) {
      val || this.close()
    }
  },

  created() {
    this.getUsers()
    this.getEvents()
  },

  methods: {
    async getUsers() {
      const users = await EventService.getUsers(this.selected)
      if(users && users.data.msg){
        this.desserts = []
        this.pageCount = 1
        this.msg = users.data.msg
      }
      else {
        this.desserts = users.data
        this.pageCount = Math.ceil(this.desserts.length / 10)
      }
    },

    async getEvents() {
      const events = await EventService.getEvents()
      this.events = events.data
    },

    async createEvent(profile) {
      this.dialog = true
      const data = {
        image: this.image,
        wishes: this.wishes,
        profile: this.editedItem.a
      }
      const newEvent = await EventService.create(data)
      if (newEvent && newEvent.data && newEvent.data.msg) {
        return this.$toast.success(newEvent.data.msg, { position: 'top-right' })
      }
      return this.$toast.error(newEvent.data.error_msg, { position: 'top-right' })
    },
    showEvent(item) {
      this.eventOfUser = this.events.find(e => e.profile.id === item.id)
      this.dialogEventDetail = true
    },
    cancelItem(item) {
      this.editedIndex = this.desserts.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.dialogCancel = true
    },

    async cancelEvent() {
      if (this.editedIndex > -1) {
        let data = await EventService.cancelEvent(this.editedItem.event)
        if (data && data.data.msg) {
          this.$toast.error(data.data.msg)
        } else {
          this.$toast.success('Success')
          this.$set(this.editedItem, 'event', '')
          this.$set(this.desserts, this.editedIndex, this.editedItem)
          await this.getEvents()
        }
      }
      this.closeDialog()
    },

    update(item) {
      this.editedIndex = this.desserts.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.eventOfUser = this.events.find(e => e.profile.id === item.id)
      this.wishes = this.eventOfUser.wishes
      this.dialog = true
    },
    checkEvent(element) {
      const filterEvent = this.events.find(e => e.profile.id === element.id)
      if (filterEvent.length > 0) {
        return true
      }
      return false
    },
    async submitUpdate() {
      let formData = new FormData()
      formData.append('wishes', this.wishes)
      if (this.image[0]) {
        formData.append('image', this.image[0])
      }
      let data = await EventService.update(this.editedItem.id, formData)
      if (data && data.msg) {
        this.$toast.error(data.msg)
      } else {
        this.$toast.success('Success')
      }
      this.close()
      await this.getEvents()

    },
    closeDialog() {
      this.editedItem = Object.assign({}, this.defaultItem)
      this.dialogCancel = false
      this.editedIndex = -1
    },

    editItem(item) {
      this.editedIndex = this.desserts.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.dialog = true
    },

    close() {
      this.dialog = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.wishes = ''
        this.image = []
        this.editedIndex = -1
        this.eventOfUser = ''
      })
    },

    async save() {
      if (!this.wishes || !this.image.length) {
        return
      }
      let data = ''
      if (this.editedIndex > -1) {
        let formData = new FormData()
        formData.append('wishes', this.wishes)
        formData.append('image', this.image[0])
        formData.append('profile', this.editedItem.id)
        data = await EventService.create(formData)

      } else {
        let formData = new FormData()
        formData.append('wishes', this.wishes)
        formData.append('image', this.image[0])
        formData.append('is_send_to_all', 'all')
        formData.append('month', this.selected)
        data = await EventService.create(formData)
      }
      if (data && data.msg) {
        this.$toast.error(data.msg)
      } else {
        this.$toast.success('Success')
      }
      this.close()
      await this.getUsers()
      await this.getEvents()
    },
    closeEventDetail() {
      this.eventOfUser = ''
      this.dialogEventDetail = false
    },
  }
}
</script>
<style lang="scss" scoped>
@import "events";
</style>
