<template>
    <v-app>
        <div class="main-content">
            <div id="id_profile" class="main-profile row p-3">
                <div class="col-md-6 mx-auto">
                    <div
                        class="card-header text-white bg-info text-left py-4 d-flex justify-content-center align-items-center">
                        <h5><strong>
                            General profile
                        </strong></h5>
                        <v-spacer></v-spacer>
                        <div>
                            <router-link
                                v-show="admin === 'true' || user.id === userId"
                                class="text-white ml-3"
                                :to="{ path: '/editprofile/' + currentUserId, params: { profile: profile } }"
                                :profile="this.profile"
                                :class="{ 'd-none': check === false }"
                            >
                                <font-awesome-icon :icon="['fas', 'edit']" />
                            </router-link>
                        </div>
                    </div>
                    <form id="profile" class="bg-white p-4 text-info">
                        <div class="form-group row">
                            <label class="col-sm-5 col-form-label"></label>
                            <div class="col-sm-6 ml-2">
                                <img v-if="!profile.image" :src=defaultLogo alt="" @click="showImageModal(defaultLogo)"
                                     height="100px" />
                                <img :src="profile.image" alt="" @click="showImageModal(profile.image)" />
                            </div>
                        </div>
                        <div class="form-group row">
                            <label class="col-sm-3 mt-1">Name</label>
                            <div class="col-sm-8 ml-2">
                                <p class="text-dark">
                                    {{ profile.name }}
                                </p>
                            </div>
                        </div>
                        <div class="form-group row">
                            <label class="col-sm-3 mt-1">Email</label>
                            <div class="col-sm-8 ml-2">
                                <p class="text-dark">
                                    {{ user.email }}
                                </p>
                            </div>
                        </div>
                        <div class="form-group row">
                            <label class="col-sm-3 mt-1">Gender</label>
                            <div class="col-sm-8 ml-2">
                                <p class="text-dark" v-if="GENDERS[this.profile.gender] !== undefined">
                                    {{ GENDERS[this.profile.gender].text }}
                                </p>
                            </div>
                        </div>
                        <div v-show="admin === 'true' || user.id === userId" class="form-group row">
                            <label class="col-sm-3 mt-1">Join date</label>
                            <div class="col-sm-8 ml-2">
                                <p class="text-dark">
                                    {{ profile.join_date }}
                                </p>
                            </div>
                        </div>
                        <div v-show="admin === 'true' || user.id === userId" class="form-group row">
                            <label class="col-sm-3 mt-1">Personal email</label>
                            <div class="col-sm-8 ml-2">
                                <p class="text-dark">
                                    {{ profile.personal_email }}
                                </p>
                            </div>
                        </div>
                        <div class="form-group row">
                            <label class="col-sm-3 mt-1">Phone</label>
                            <div class="col-sm-8 ml-2">
                                <p class="text-dark">
                                    {{ profile.phone }}
                                </p>
                            </div>
                        </div>
                        <div class="form-group row">
                            <label class="col-sm-3 mt-1">Slack ID</label>
                            <div class="col-sm-8 ml-2">
                                <p class="text-dark">
                                    {{ profile.slack_id }}
                                </p>
                            </div>
                        </div>
                        <div class="form-group row">
                            <label class="col-sm-3 mt-1">Title</label>
                            <div class="col-sm-8 ml-2">
                                <p class="text-dark">
                                    {{ user.title }}
                                </p>
                            </div>
                        </div>
                        <div class="form-group row">
                            <label class="col-sm-3 mt-1">Birthday</label>
                            <div class="col-sm-8 ml-2">
                                <p class="text-dark" v-if="profile.birth_day">
                                    {{
                                        profile.birth_day
                                            .split('-')
                                            .reverse()
                                            .join('-')
                                    }}
                                </p>
                            </div>
                        </div>
                        <div v-if="check" class="form-group row">
                            <label class="col-sm-3 col-form-label mt-3">Line manager</label>
                            <div v-if="admin === 'true' && editLiner">
                                <input v-model="managerEmail" class="btn mr-2 border-dark" list="names"
                                       @keyup="filterUsers"
                                       @focus="filterUsers" />
                                <datalist id="names">
                                    <option v-for="user in listUsers" :value-data="user.name">
                                        {{ String(user.name).concat(` (${String(user.email)})`) }}
                                    </option>
                                </datalist>
                                <v-btn color="#43c9d0" dark depressed class="mr-4" @click="setLineManager()">
                                    Ok
                                </v-btn>
                                <v-btn dark class="mr-4 secondary" depressed v-on:click="() => editLiner = !editLiner">
                                    Cancel
                                </v-btn>
                            </div>
                            <div v-if="!editLiner" class="mx-3 d-inline-flex row">
                                <a :href="'/profile/' + managerId" title="Click to move to Profile page"
                                   class="col-sm-8">
                                    <p class="mt-1 text-dark">
                                        {{ profile.line_manager_email }}
                                    </p>
                                </a>
                                <span
                                    v-show="admin === 'true' && !editLiner && !editBonus"
                                    class="btn font-weight-normal ml-3 col-sm-1 mt-3"
                                    :class="{ 'd-none': profile.active === false }"
                                    v-on:click="() => editLiner = !editLiner"
                                    data-toggle="tooltip"
                                    title="Click set line manager"
                                >
                                  <font-awesome-icon :icon="['fas', 'edit']" class="text-info" />
                                </span>
                                <span
                                    v-show="admin === 'true' && !editLiner && !editBonus"
                                    class="btn font-weight-normal col-sm-1 mt-3"
                                    :class="{ 'd-none': profile.active === false }"
                                    @click="removeLineManager()"
                                    data-toggle="tooltip"
                                    title="Click remove line manager"
                                >
                                  <font-awesome-icon :icon="['fas', 'user-times']" class="text-danger" />
                                </span>
                            </div>
                        </div>
                        <div v-if="check" class="form-group row">
                            <label class="col-sm-3 col-form-label mt-3">Maximum level approve</label>
                            <div v-if="admin === 'true' && editLevel">
                                <input v-model="levelApprove" type="number" class="btn mr-2 border-dark" list="names"
                                />
                                <v-btn color="#43c9d0" dark depressed class="mr-4" @click="setLevelApprove()">
                                    Ok
                                </v-btn>
                                <v-btn dark class="mr-4 secondary" depressed v-on:click="editLevel = !editLevel">
                                    Cancel
                                </v-btn>
                            </div>
                            <div v-if="!editLevel" class="mx-3 d-inline-flex row">
                                <span class="col-sm-8">
                                  <p class="mt-1 text-dark">
                                    {{ profile.maximum_level_approved }}
                                  </p>
                                </span>
                                <span
                                    v-show="admin === 'true' && !editLevel"
                                    class="btn font-weight-normal ml-3 col-sm-1 mt-3"
                                    @click="editLevel = !editLevel"
                                    data-toggle="tooltip"
                                    title="Click set level approve"
                                >
                                  <font-awesome-icon :icon="['fas', 'edit']" class="text-info" />
                                </span>
                            </div>
                        </div>
                        <div v-if="check" class="form-group row">
                            <label class="col-sm-3 col-form-label">Bonus</label>
                            <div v-if="admin === 'true' && editBonus && user.id !== userId">
                                <input v-model="bonusNumber" class="btn mr-2 border-dark" type="number" />
                                <v-btn color="#43c9d0" dark depressed class="mr-4"
                                       @click="setBonus(bonusNumber, profile.id)">
                                    Add
                                </v-btn>
                                <v-btn v-show="admin === 'true' && editBonus" dark class="mr-4 secondary" depressed
                                       v-on:click="() => editBonus = !editBonus">
                                    Cancel
                                </v-btn>
                            </div>
                            <div v-if="!editBonus" class="mx-3 d-inline-flex">
                                <a :href="'/profile/' + managerId" title="Click to move to Profile page">
                                    <p v-if="bonusNumber!=0" class="mt-1 text-dark">
                                        {{ bonusNumber }}
                                    </p>
                                </a>
                            </div>
                        </div>
                        <div class="form-group row">
                            <label class="col-sm-10 col-form-label">
                                <div>
                  <span
                      v-show="admin === 'true' && !editBonus && currentUserId !== userId && !editLiner"
                      class="btn btn-info font-weight-normal col-sm-3 mr-4"
                      :class="{ 'd-none': profile.active === false }"
                      v-on:click="() => editBonus = !editBonus"
                  >
                  Add bonus
                </span>
                                </div>
                            </label>
                            <label class="col-sm-2 col-form-label">
                <span
                    v-show="admin === 'true' && user.active === true && user.admin !== true"
                    class="btn btn-danger font-weight-normal"
                    :class="{ 'd-none': profile.active === false }"
                    @click="showModal(profile.id, profile.name)"
                >
                  Deactive
                </span>
                                <span
                                    v-show="admin === 'true' && profile.active === false"
                                    class="btn btn-info font-weight-normal mt-3 float-right"
                                    :class="{ 'd-none': profile.active === true }"
                                    @click="active(user.id)"
                                >
                  Active
                </span>
                            </label>
                        </div>
                    </form>
                </div>
                <div class="col-md-6 mx-auto">
                    <Photo v-if="profile.id" :profileId="profile.id" />
                </div>
            </div>
            <b-modal ref="imagemodal" size="xl" hide-footer hide-header="">
                <div class="d-block text-center mx-auto px-auto">
                    <img class="modal_image" :src="photo" style="max-width: -webkit-fill-available" alt=".." />
                </div>
            </b-modal>
            <b-modal ref="mymodal" hide-footer hide-header="">
                <div class="d-block text-center text-danger">
                    <h3>Deactivate</h3>
                    <hr />
                </div>
                <div v-if="admin === 'true'" class="d-block text-center mb-4">
                    Do you want to deactivate user
                    <p class="text-danger d-inline">
                        {{ currentname }}
                    </p>
                </div>
                <div class="row">
                    <b-button class="mt-3 mx-auto col-4" variant="danger" block @click="del">
                        Confirm
                    </b-button>
                    <b-button class="mt-3 mx-auto col-4" variant="outline-dark" block @click="hideModal">
                        Cancel
                    </b-button>
                </div>
            </b-modal>
            <b-modal ref="imagemodal" size="xl" hide-footer hide-header="">
                <div class="d-block text-center mx-auto px-auto">
                    <img class="modal_image" :src="photo" style="max-width: -webkit-fill-available" />
                </div>
            </b-modal>
        </div>
    </v-app>
</template>

<script>
import axios from 'axios'
import Photo from '../../photos/_id/index.vue'
import logo from '../../../static/images/icon.png'
import { GENDERS } from '@/const/genders'
import UserStatisticService from '../../../services/profile/bonus'

export default {
    name: 'general',
    middleware: 'authen',
    components: {
        Photo
    },
    data: () => ({
        levelApprove: '',
        snackbar: {
            show: false,
            message: null,
            color: null
        },
        bonusNumber: 0,
        defaultLogo: logo,
        editLiner: false,
        editLevel: false,
        editBonus: false,
        user: {},
        profile: {},
        admin: localStorage.getItem('is_admin'),
        currentname: '',
        photo: '',
        userId: localStorage.getItem('user_id'),
        currentUserId: '',
        id: '',
        response: '',
        items: [
            {
                text: 'Homepage',
                to: {
                    name: 'Index'
                }
            },
            {
                text: 'Profile',
                active: true
            }
        ],
        managerEmail: '',
        managerId: '',

        row: {},
        listUsers: []
    }),
    computed: {
        check: function() {
            const id = localStorage.getItem('user_id')
            const paramId = this.$route.params.id
            if (id === paramId || this.admin === 'true') {
                return true
            }
            return false
        },
        GENDERS: function() {
            return GENDERS
        }
    },
    async created() {
        await this.asyncData(this.$route.params)
    },
    methods: {
        async setBonus(bonusNumber, profile) {
            let data = {
                'profile_id': profile,
                'bonus': bonusNumber
            }
            await UserStatisticService.create(data)
            this.editBonus = false
        },
        async filterUsers(event) {
            return axios.get(`/user?name=${event.target.value}`).then((res) => {
                this.listUsers = []
                res.data.results.forEach((data) => {
                    if (data.id !== this.user.id) {
                        this.listUsers.push({
                            name: data.profile.name,
                            email: data.email,
                            id: data.id
                        })
                    }
                })
            })
        },
        removeLineManager() {
            return axios
                .delete(`user/profile/${this.profile.id}/remove_line_manager`)
                .then(() => {
                    this.profile.line_manager_email = null
                    this.$toast.success('Remove line manager successfully!')
                    this.asyncData(this.$route.params)
                })
                .catch((err) => {
                    this.snackbar = {
                        message: err.response ? err.response.data.detail : 'Error occured',
                        color: 'error',
                        show: true
                    }
                })
                .finally(() => {
                    this.editLiner = false
                })
        },
        setLineManager() {
            const index = this.managerEmail.indexOf('(')
            this.managerEmail = this.managerEmail.substring(0, index - 1)
            return axios
                .post(`user/profile/${this.profile.id}/set_line_manager`, {
                    name: this.managerEmail
                })
                .then((res) => {
                    this.profile.line_manager_email = this.managerEmail
                    this.managerEmail = ''
                    this.managerId = res.data.manager_id
                    this.$toast.success('Set line manager successfully!')
                    this.editLiner = false
                })
                .catch((err) => {
                    console.log(err)
                    let message = err.response ? err.response.data.detail : 'Error occured'
                    this.$toast.error(message)
                })
        },
        setLevelApprove() {
            const index = this.managerEmail.indexOf('(')
            this.managerEmail = this.managerEmail.substring(0, index - 1)
            return axios
                .post(`user/profile/${this.profile.id}/set_level_approved`, {
                    level: this.levelApprove
                })
                .then((res) => {
                    debugger
                    this.profile.maximum_level_approved = res.data.level
                    this.$toast.success('Set maximum level approve successfully!')
                    this.editLevel = false
                })
                .catch((err) => {
                    let message = err.response ? err.response.data.detail : 'Error occured'
                    this.$toast.error(message)
                })
        },
        showModal(id, name) {
            this.currentid = id
            this.currentname = name
            this.$refs.mymodal.show()
        },
        hideModal() {
            this.$refs.mymodal.hide()
        },
        showImageModal(photo) {
            this.photo = photo
            this.$refs.imagemodal.show()
        },
        hideImageModal() {
            this.$refs.imagemodal.hide()
        },
        active: function(id) {
            axios
                .put(`user/${id}/activate`, '')
                .then((res) => {
                    this.user.active = true
                })
                .catch((e) => {
                })
        },
        del: function() {
            axios
                .delete(`user/${this.user.id}`)
                .then((res) => {
                    this.$refs.mymodal.hide()
                    this.profile.active = false
                })
                .catch((e) => {
                })
        },
        asyncData(params) {
            return axios
                .get(`/user/${params.id}`)
                .then((res) => {
                    this.user = res.data
                    this.profile = res.data.profile
                    this.managerId = res.data.profile.line_manager_user_id
                    this.currentUserId = res.data.profile.user
                })
                .catch((e) => {
                    error({
                        statusCode: 404,
                        message: 'Post not found'
                    })
                })
        }
    },
    head() {
        return {
            title: 'Profile'
        }
    }
}
</script>
