<template>
  <div>
    <div class="position-absolute sticky-top mt-2 mybcrumb">
      <b-breadcrumb :items="items"></b-breadcrumb>
    </div>
      <v-app class="ml-5 mr-5">
        <v-card outlined="true" class="m-4">
          <v-card-title>
            <v-row>
              <v-col cols="5" class="d-flex">
                <h2 class="mr-4">Leave management</h2>
                <date-picker v-model="date" type="year" placeholder="Select year"
                             @input="saveDate(date)"
                             :disabled-date="afterJoinDateInCompany" />
              </v-col>
              <v-spacer></v-spacer>
              <v-col cols="2">
                <div class="text-center rounded text-light color-bg">
                  <small>Remain leaves:</small>
                  <small class="badge bg-white text-danger ml-2">{{ leaveUser[0].current_days_off }}</small>
                </div>
              </v-col>
              <v-col cols="2">
                <div class="text-center rounded text-light color-bg">
                  <small>Annual leave last year:</small>
                  <small class="badge bg-white text-danger ml-2">{{ leaveUser[0].annual_leave_last_year }}</small>
                </div>
              </v-col>
              <v-col cols="2">
                <download-excel
                  class="bg-info text-center rounded text-light"
                  :data="leaveUser"
                  :fields="json_fields"
                  worksheet="My Worksheet"
                  name="leaveManagement.xls">
                  <small>Export Excel</small>
                </download-excel>
              </v-col>
            </v-row>
          </v-card-title>
          <v-data-table
            :headers="headers"
            :items="leaveUser"
            :items-per-page="12"
            :options.sync="options"
            class="table-striped"
            hide-default-footer
          >
            <template v-slot:item="props">
              <tr>
                <td>{{ props.item.month }}</td>
                <td>{{ props.item.working_days }}</td>
                <td>{{ props.item.leave_days_number }}
                  <a v-if="props.item.leave_days_number" class="btn text-info"
                     @click="getDetailData(props.item.month, 'leave_days')">
                    <font-awesome-icon :icon="['fas', 'info-circle']" />
                  </a>
                </td>
                <td>{{ props.item.OT_days_number }}
                  <a v-if="props.item.OT_days_number" class="btn text-info "
                     @click="getDetailData(props.item.month, 'OT_days')">
                    <font-awesome-icon :icon="['fas', 'info-circle']" />
                  </a>
                </td>
                <td>{{ props.item.night_days_number }}
                  <a v-if="props.item.night_days_number" class="btn text-info "
                     @click="getDetailData(props.item.month, 'night_days')">
                    <font-awesome-icon :icon="['fas', 'info-circle']" />
                  </a>
                </td>
                <td>{{ props.item.remote_days_number }}
                  <a v-if="props.item.remote_days_number" class="btn text-info "
                     @click="getDetailData(props.item.month, 'remote_days')">
                    <font-awesome-icon :icon="['fas', 'info-circle']" />
                  </a>
                </td>
                <td>{{ props.item.insurance_days_number }}
                  <a v-if="props.item.insurance_days_number" class="btn text-info "
                     @click="getDetailData(props.item.month, 'insurance_days')">
                    <font-awesome-icon :icon="['fas', 'info-circle']" />
                  </a>
                </td>
                <td>{{ props.item.unpaid_days_number }}
                  <a v-if="props.item.unpaid_days_number" class="btn text-info "
                     @click="getDetailData(props.item.month, 'unpaid_days')">
                    <font-awesome-icon :icon="['fas', 'info-circle']" />
                  </a>
                </td>
              </tr>
            </template>
          </v-data-table>
        </v-card>
      </v-app>
    <v-dialog
      v-model="dialog"
      width="800"
      class="color"
    >
      <v-card>
        <v-card-title class="headline color mb-6">Detail</v-card-title>
        <v-card-text>
          <v-data-table
            :headers="headersDetail"
            :items="detailLeaveUser"
            hide-default-footer
            class="elevation-1 "
          >
            <template v-slot:item="props">
              <tr :class="{'my-class': props.item.current > props.item.total}">
                <td>{{ detailLeaveUser.indexOf(props.item) + 1 }}</td>
                <td>{{ props.item.date }}</td>
                <td>{{ props.item.type }}</td>
                <td v-if=" props.item.lunch">Yes</td>
                <td v-else>No</td>
                <td>{{ props.item.type_off_title }}</td>
              </tr>
            </template>
          </v-data-table>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="#43c9d0"
            text
            @click="dialog = false"
          >
            Close
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
import UserStatisticService from '../../services/leavedayuserstatistic'
import DatePicker from 'vue2-datepicker'
import 'vue2-datepicker/index.css'

export default {
  name: 'LeaveDayStatistic',
  components: { DatePicker },
  data() {
    return {
      dialog: false,
      date: new Date(),
      leaveUser: [],
      detailLeaveUser: [],
      json_fields: {
        'Month': 'month',
        'Working days in month': 'working_days',
        'Remain leaves': 'current_days_off',
        'Number of leave days': 'leave_days_number',
        'Leave days in month': {
          field: 'leave_days',
          callback: (value) => {
            return this.getCallBack(value)
          }
        },
        'Number of OT days': 'OT_days_number',
        'OT days in month': {
          field: 'OT_days',
          callback: (value) => {
            return this.getCallBack(value)
          }
        },
        'Number of remote days': 'remote_days_number',
        'Remote days in month': {
          field: 'remote_days',
          callback: (value) => {
            return this.getCallBack(value)
          }
        },
        'Number of insurance days': 'insurance_days_number',
        'Insurance days in month': {
          field: 'insurance_days',
          callback: (value) => {
            return this.getCallBack(value)
          }
        },
        'Number of unpaid days': 'unpaid_days_number',
        'Unpaid days in month': {
          field: 'unpaid_days',
          callback: (value) => {
            return this.getCallBack(value)
          }
        }
      },
      items: [
        {
          text: 'Homepage',
          to: { name: 'Index' }
        },
        {
          text: 'Calendar',
          to: { name: 'calendar' }
        },
        {
          text: 'Leave Day Statistic',
          active: true
        }
      ],
      headers: [
        {
          text: 'Month',
          value: 'month'
        },
        {
          text: 'Working days in month',
          value: 'working_days'
        },
        {
          text: 'Leave days in month',
          value: 'leave_days'
        },
        {
          text: 'OT days in month',
          value: 'OT_days_number'
        },
        {
          text: 'Night days in month',
          value: 'night_days_number'
        },
        {
          text: 'Remote days in month',
          value: 'remote_days_number'
        },
        {
          text: 'Insurance leave days in month',
          value: 'insurance_days_number'
        },
        {
          text: 'Unpaid leave days in month',
          value: 'unpaid_days_number'
        }
      ],
      headersDetail: [
        {
          text: 'STT',
          value: 'stt'
        },
        {
          text: 'Date',
          value: 'date'
        },
        {
          text: 'Time',
          value: 'type'
        },
        {
          text: 'Lunch',
          value: 'lunch'
        },
        {
          text: 'Type of leave',
          value: 'date_off_number'
        }
      ]
    }
  },
  watch: {
    page() {
      this.getData()
    },
    month() {
      this.getData()
    }
  },
  created() {
    this.getData()
  },
  methods: {
    getCallBack(value) {
      const data = []
      for (let i = 0; i < value.length; i++) {
        let detailData = `${i + 1}. ${value[i].date} - ${value[i].type}`
        if (value[i].lunch) {
          detailData = detailData + ' - Have eat lunch'
        } else {
          detailData = detailData + ' - Don\'t have eat lunch'
        }
        data.push(detailData)
      }
      return data
    },
    afterJoinDateInCompany(date) {
      const max = new Date()
      const min = new Date(`${this.leaveUser[0].join_date.slice(0, 4) - 1}`)
      if (date <= max && date > min) {
        return false
      }
      return true
    },
    async saveDate(date) {
      const data = await UserStatisticService.get(date.getFullYear())
      this.leaveUser = data.data
    },
    async getData() {
      const data = await UserStatisticService.get(this.date.getFullYear())
      this.leaveUser = data.data
    },
    getDetailData(month, detail) {
      this.dialog = true
      this.detailLeaveUser = this.leaveUser.find(date => date.month === month)[detail]
    }
  },
  head() {
    return {
      title: 'Leave Day Statistic'
    }
  }
}
</script>
<style lang="scss">
@import './index.scss';
</style>
