import Vue from 'vue';
import Vuex from 'vuex';
import user from './modules/user';
import events from './modules/event';
import { countRequest } from '../services/managementLeave.service'

const cookieparser = require('cookieparser');

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    token: null,
    countNoti: 0,
  },
  mutations: {
    setToken(state, token) {
      state.token = token;
    },
    add(state) {
      state.countNoti += 1;
    },
    subtract(state) {
      state.countNoti -= 1;
    },
    setCountNoti(state, count) {
      state.countNoti = count.count;
    },
  },
  actions: {
    nuxtServerInit({ commit }, { req }) {
      let token = null;
      if (req.headers.cookie) {
        const parsed = cookieparser.parse(req.headers.cookie);
        try {
          token = JSON.parse(parsed.token);
        } catch (err) {
          // No valid cookie found
        }
      }
      commit('setToken', token);
    },
    async setCountNoti({ commit }) {
      const count = await countRequest()
      commit('setCountNoti', count)
    }
  },
  getters: {
    showNoti(state) {
      return state.countNoti;
    },
  },
  modules: {
    user: user,
    event: events,
  },
});
