import BaseService from '../base'
import axios from 'axios'
import { API_URL } from '@/.env'


class EducationService extends BaseService {
    get entity() {
        return 'education'
    }

    create(id, data) {
        const education = this.request({ auth: true }).post(`user/${this.entity}/${id}/add`, data)
        return education
    }

    get(id) {
        const education = this.request({ auth: true }).get(`user/${this.entity}/${id}`)
        return education
    }

    update(id, data) {
        return this.request({ auth: true }).put(`user/${this.entity}/${id}`, data)
    }

    delete(id, edu_id) {
        return axios({
            method: 'delete',
            headers: {
                Authorization: `${localStorage.getItem('token')}`
            },
            url: API_URL + `/user/${this.entity}/${id}`,
            data: {
                education_id: edu_id
            }
        });
    }

}

export default new EducationService()
