<template>
  <div id="app" class="ml-5 mr-5">
    <v-app>
      <v-card outlined class="m-4">
        <v-data-table
          :headers="headers"
          :items="desserts"
          sort-by="budget"
          class="table-striped"
          hide-default-footer
          :page.sync="page"
          :items-per-page="itemsPerPage"
          :search="search"
          @page-count="pageCount = $event"
        >
          <template v-slot:top>
            <v-toolbar flat color="white" class="m-3">
              <h2>Manage User Lunch</h2>
              <v-spacer></v-spacer>
              <v-col cols="3">
                <v-text-field
                  v-model="search"
                  append-icon="mdi-magnify"
                  label="Search"
                  placeholder="Search name or date"
                  single-line
                  hide-details
                ></v-text-field>
              </v-col>
              <v-dialog v-model="dialog" max-width="500px">
                <template v-slot:activator="{ on, attrs }">
                  <v-btn
                    color="#43c9d0"
                    dark
                    depressed
                    class="mb-2"
                    v-bind="attrs"
                    v-on="on"
                  >Create new portions
                  </v-btn>
                </template>
                <v-card>
                  <v-card-title>
                    <span class="headline">Lunch</span>
                  </v-card-title>
                  <v-card-text>
                    <v-form @submit.prevent="addEvent">
                      <v-container>
                        <v-row>
                          <flat-pickr
                            v-model="editedItem.date"
                            :config="{
                                altInput: true,
                                altFormat: 'd-m-Y',
                                dateFormat: 'Y-m-d',
                                locale: { firstDayOfWeek: 1 },
                            }"
                            class="form-control bg-white"
                            name="date"
                          />
                        </v-row>
                        <v-row>
                          <label class="container row font-weight-bold col-5 pr-0 justify-content-between">
                            <v-text-field v-if="editedItem.is_hidden" v-model="quantity" type="text"
                                          label="Quantity"></v-text-field>
                          </label>
                        </v-row>
                        <v-row>
                          <input v-model="editedItem.has_veggie" type="checkbox" class="mr-4" />
                          <label class="font-weight-bold">
                            <font-awesome-icon :icon="['fas', 'leaf']" class="text-success fa-fw" />
                            Has Veggie
                          </label>
                        </v-row>
                      </v-container>
                    </v-form>
                  </v-card-text>
                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn depressed color="danger" text @click="close">Cancel</v-btn>
                    <v-btn depressed color="#43c9d0" dark @click="save()">Save</v-btn>
                  </v-card-actions>
                </v-card>
              </v-dialog>
            </v-toolbar>
            <v-toolbar flat color="white" class="m-3">
              <div class="d-flex w-75">
                <flat-pickr
                  v-model="startday"
                  :config="{
                altInput: true,
                altFormat: 'd-m-Y',
                dateFormat: 'Y-m-d',
                locale: { firstDayOfWeek: 1 },
            }"
                  class="form-control bg-white"
                  name="date"
                  required
                  @on-change="doSomethingOnChange"
                />
                <div class="w-100 m-auto ml-3 text-white rounded pt-1 pb-1 text-center color-bg">Normal Lunch :
                  {{ countHasNoVeggie }}
                </div>
                <div class="w-100 m-auto ml-3 text-white rounded pt-1 pb-1 text-center color-bg">Veggie Lunch :
                  {{ countHasVeggie }}
                </div>
                <div v-if="providerOnDate" class="w-100 m-auto ml-3 text-white rounded pt-1 pb-1 text-center color-bg">
                  Provider : {{ providerOnDate.name }} - {{ providerOnDate.phone }}
                </div>
                <div v-else class="w-100 m-auto ml-3 text-white rounded pt-1 pb-1 text-center color-bg">Please choose
                  provider lunch!
                </div>
              </div>
              <v-spacer></v-spacer>
              <v-dialog v-model="dialogProvider" max-width="400px">
                <template v-slot:activator="{ on, attrs }">
                  <v-btn
                    color="#43c9d0"
                    dark
                    depressed
                    v-bind="attrs"
                    v-on="on"
                  >Lunch Provider Profile
                  </v-btn>
                </template>
                <v-card v-if="providerOnDate">
                  <v-card-title class="bg-info text-center">
                    <span class="headline text-light">Information Provider Lunch</span>
                  </v-card-title>
                  <v-card-text>
                    <v-container>
                      <v-row>
                        <v-col cols="6"><strong>Name:</strong></v-col>
                        <v-col cols="6"><p>{{ providerOnDate.name }}</p></v-col>
                        <v-col cols="6"><strong>Phone:</strong></v-col>
                        <v-col cols="6"><p>{{ providerOnDate.phone }}</p></v-col>
                        <v-col cols="6"><strong>Address:</strong></v-col>
                        <v-col cols="6"><p>{{ providerOnDate.address }}</p></v-col>
                        <v-col cols="6"><strong>Has veggie:</strong></v-col>
                        <v-col cols="6" v-if="providerOnDate.has_vegetarian"><p>Have</p></v-col>
                        <v-col cols="6" v-else><p>Don't have:</p></v-col>
                        <v-col cols="6"><strong>Link</strong></v-col>
                        <v-col cols="6"><p>{{ providerOnDate.link }}</p></v-col>
                        <v-col cols="6"><strong>Budget:</strong></v-col>
                        <v-col cols="6"><p>{{ providerOnDate.budget }}</p></v-col>
                      </v-row>
                    </v-container>
                  </v-card-text>
                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="danger" depressed text @click="dialogProvider=false">Close</v-btn>
                  </v-card-actions>
                </v-card>
                <v-card v-else>
                  <v-card-title>Please choose provider lunch!</v-card-title>
                </v-card>
              </v-dialog>
            </v-toolbar>
          </template>
          <template v-slot:item.has_veggie="{ item }">
            <td v-if="item.has_veggie">Yes</td>
            <td v-else>No</td>
          </template>
          <template v-slot:item.actions="{ item }">
            <v-icon
              title="Edit this"
              small
              color="green darken-2"
              class="mr-2"
              @click="editItem(item)"
            >
              mdi-pencil
            </v-icon>
            <v-icon
              title="Remove this"
              small
              color="red darken-2"
              @click="deleteItem(item)"
            >
              mdi-delete
            </v-icon>
          </template>
          <template v-slot:item.lunch="{ item }">
            <v-icon
              title="Remove this"
              small
              @click="deleteItem(item)"
              color="teal darken-2"
            >
              mdi-plus
            </v-icon>
          </template>
          <template v-slot:item.budget="{ item }">
            <v-chip :color="getColor(item.budget)" dark>{{ item.budget }}</v-chip>
          </template>
        </v-data-table>
      </v-card>
      <div class="text-center pt-2">
        <v-pagination v-model="page" :length="pageCount" color="#43c9d0"></v-pagination>
      </div>
    </v-app>
  </div>
</template>
<script>
import flatPickr from 'vue-flatpickr-component'
import 'flatpickr/dist/flatpickr.css'
import UserLunchService from '../../services/user-lunch'
import LunchService from '../../services/lunch'

export default {
  name: 'test',
  middleware: 'authen',
  components: {
    flatPickr
  },
  data: () => ({
    dialog: false,
    page: 1,
    pageCount: 0,
    itemsPerPage: 10,
    search: '',
    quantity: 1,
    startday: new Date().toISOString().substr(0, 10),
    headers: [
      {
        text: 'Name',
        align: 'start',
        sortable: false,
        value: 'profile.name'
      },
      {
        text: 'Veggie',
        value: 'has_veggie'
      },
      {
        text: 'Date',
        value: 'date'
      },
      {
        text: 'Actions',
        value: 'actions',
        sortable: false
      }
    ],
    desserts: [],
    dialogProvider: false,
    providers: [],
    providerOnDate: '',
    editedIndex: -1,
    editedItem: {
      has_veggie: false,
      date: new Date().toISOString().substr(0, 10),
      is_hidden: true
    },
    defaultItem: {
      has_veggie: false,
      date: new Date().toISOString().substr(0, 10),
      is_hidden: true
    },
    dataArray: []
  }),
  computed: {
    formTitle() {
      return this.editedIndex === -1 ? 'Lunch' : 'Edit Lunch'
    },
    countHasVeggie() {
      return this.desserts.filter(e => e.has_veggie).length
    },
    countHasNoVeggie() {
      return this.desserts.filter(e => !e.has_veggie).length
    }
  },
  created() {
    this.getLunches()
  },
  watch: {
    dialog(val) {
      val || this.close()
    }
  },
  methods: {
    async getLunches() {
      const lunches = await LunchService.get()
      this.providers = lunches.data
      let date = new Date().toISOString().substr(0, 10)
      if (!this.providers.find(provider => provider.date === date)) {
        this.providerOnDate = null
        return
      }
      this.providerOnDate = this.providers.find(provider => provider.date === date).provider
    },
    async getUserLunches() {
      const usLunches = await UserLunchService.getPagi(this.startday)
      this.desserts = usLunches.data.results
      return usLunches
    },
    editItem(item) {
      this.editedIndex = this.desserts.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.$set(this.editedItem, 'is_hidden', false)
      this.dialog = true
    },
    makeRepeat(arr, times) {
      return [].concat(...Array.from({ length: times }, () => arr))
    },
    async addUserLunchByAdmin() {
      const data = [
        {
          date: this.editedItem.date,
          has_veggie: this.editedItem.has_veggie,
        }
      ]
      const list_data = this.makeRepeat(data, Number(this.quantity))
      this.dataArray = list_data
      await UserLunchService.adminCreate(list_data)
    },
    endDate() {
      if (this.editedItem.date === null) {
        return this.today
      }
      return this.editedItem.date
    },
    async deleteItem(item) {
      const index = this.desserts.indexOf(item)
      confirm('Are you sure you want to delete this item?') && this.desserts.splice(index, 1) &&
      await UserLunchService.delete(item.id)
    },
    close() {
      this.dialog = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
    },
    async save() {
      if (this.editedIndex > -1) {
        Object.assign(this.desserts[this.editedIndex], this.editedItem)
        const data = {
          date: this.editedItem.date,
          has_veggie: this.editedItem.has_veggie
        }
        const usLunch = await UserLunchService.updateByAdmin({
          id: this.editedItem.id,
          data
        })
        if (usLunch && usLunch.data.msg) {
          const content = usLunch.data.msg
          this.$toast.error(content, { position: 'top-right' })
        }
        if (usLunch && usLunch.data.date) {
          await this.getUserLunches()
          const content = 'Updated successfully!'
          this.$toast.success(content, { position: 'top-right' })
        }
      } else {
        await this.addUserLunchByAdmin()
        for (let i = 0; i < this.dataArray.length; i++) {
          this.desserts.push(this.dataArray[i])
        }
        await this.getUserLunches()
        this.startday = this.editedItem.date
        this.editedItem.date = new Date().toISOString().substr(0, 10)
      }
      this.close()
    },
    getColor(budget) {
      if (budget > 400) {
        return 'purple'
      } else if (budget > 200) {
        return 'orange'
      } else {
        return 'teal'
      }
    },
    async doSomethingOnChange() {
      await this.getUserLunches()
      if (!this.providers.find(provider => provider.date === this.startday)) {
        this.providerOnDate = null
        return
      }
      this.providerOnDate = this.providers.find(provider => provider.date === this.startday).provider
    },
    async prepareData() {
      await this.getUserLunches()
    }
  },
  async mounted() {
    await this.prepareData()
  }
}
</script>
<style lang="scss">
@import "manage";
</style>
