<template>
  <div class="my-2 ma-auto calendar-main-content">
    <div class="position-absolute sticky-top mt-2 mybcrumb">
      <b-breadcrumb :items="items"></b-breadcrumb>
    </div>
    <v-app class="mr-3 bg-light">
      <v-row class="m-4">
        <v-col cols="2">
          <v-row style="height: 60px;">
          </v-row>
          <v-row>
            <router-link to="/leavesday" class="btn btn-info float-right pt-2 text-light m-2 w-100">
              ADD LEAVE
            </router-link>
          </v-row>
          <v-row>
            <b-dropdown text="SET LUNCH" variant="info" class="m-2 w-100">
              <v-btn color="#43c9d0" depressed dark @click.stop="dialog = true" class="mb-2 w-100 ">
                Range
              </v-btn>
              <v-btn color="#43c9d0" depressed dark @click="setToday" class="mb-2 w-100">
                Today
              </v-btn>
              <v-btn dark v-if="!is_set_veggie" depressed color="#43c9d0" class="mb-2 w-100" @click="setVeggieLunch()">
                Veggie
              </v-btn>
            </b-dropdown>
          </v-row>
          <v-row>
            <b-dropdown text="CANCEL LUNCH" variant="danger" class="m-2 w-100">
              <v-btn v-if="is_set_veggie" color="#e02d1a" depressed dark @click="cancelSetVeggieLunch()"
                     class="mb-2 w-100">
                Veggie
              </v-btn>
              <v-btn dark color="#e02d1a" depressed @click="deleteMany()" class="mb-2 w-100">
                Set
              </v-btn>
            </b-dropdown>
          </v-row>
          <v-row>
            <v-list class="bg-light">
              <v-list-group :value="true">
                <template v-slot:activator>
                  <v-list-item-content>
                    <v-list-item-title>My Calendar</v-list-item-title>
                  </v-list-item-content>
                </template>
                <v-list-item>
                  <v-checkbox
                    v-model="lunch"
                    color="#F2CD5E"
                    label="My Lunch"
                    @click="checkBox"
                    class="m-0 p-0"
                  >My Lunch
                  </v-checkbox>
                </v-list-item>
                <v-list-item>
                  <v-checkbox
                    color="info"
                    v-model="leave"
                    label="My Leave"
                    @click="checkBox"
                    class="m-0 p-0"
                  ></v-checkbox>
                </v-list-item>
              </v-list-group>
            </v-list>
          </v-row>
          <v-row>
            <div class="m-3">
              <strong>Descriptions</strong>
            </div>
            <div class="text-center rounded text-light color-bg m-2 w-100">
              <small>Lunar Day</small>
            </div>
            <div class="text-center rounded text-dark color-bg-2 m-2 w-100">
              <small>Current Day</small>
            </div>
          </v-row>
        </v-col>
        <v-col cols="10">
          <FullCalendar ref="fullCalendar" :options="calendarOptions" />
        </v-col>
      </v-row>
      <v-dialog v-model="dialog" max-width="500">
        <v-card>
          <v-container>
            <v-form>
              <div class="d-flex align-items-center mt-6">
                <label class="mt-3 mr-3">From: </label>
                <flat-pickr
                  v-model="startDate"
                  :config="{
                         altInput: true,
                         altFormat: 'd-m-Y',
                         minDate: this.today,
                         dateFormat: 'Y-m-d',
                         locale: { firstDayOfWeek: 1 },
                         }"
                  class="bg-white"
                  placeholder="Select date"
                />

                <label class="mt-3 mr-3 ml-3">To: </label>
                <flat-pickr
                  v-model="endDate"
                  :config="{
                         altInput: true,
                         altFormat: 'd-m-Y',
                         minDate: this.startDate? this.startDate : this.today,
                         dateFormat: 'Y-m-d',
                         locale: { firstDayOfWeek: 1 },
                         }"
                  class="bg-white"
                  placeholder="Select date"
                />
              </div>
              <label class="container row font-weight-bold col-5 pr-0 justify-content-between ml-1">
                Has Veggie
                <font-awesome-icon :icon="['fas', 'leaf']" class="text-success fa-fw" />
                <input v-model="has_veggie" type="checkbox" class="form-check-input" />
                <span class="checkmark" />
              </label>
            </v-form>
            <v-row>
              <v-spacer></v-spacer>
              <v-btn dark color="#43c9d0" depressed @click="addEvent()" class="mr-auto">
                Create Lunch
              </v-btn>
            </v-row>
          </v-container>
        </v-card>
      </v-dialog>
      <v-dialog v-model="dialogDetail" max-width="300">
        <v-card v-if="selectedEvent.title === 'Lunch' || selectedEvent.title === 'Veggie Lunch'">
          <v-card-title class="headline text-center">
            {{ selectedEvent.title }}
            <v-spacer></v-spacer>
            <v-btn @click="deleteEvent(selectedEvent)" icon>
              <v-icon color="red">mdi-delete</v-icon>
            </v-btn>
          </v-card-title>
          <v-card-actions>
            <div class="d-flex align-items-center ml-4">
              <v-checkbox
                v-model="selectedEvent.has_veggie"
                color="#43c9d0"
              ></v-checkbox> Has veggie
              <font-awesome-icon :icon="['fas', 'leaf']" class="text-success fa-fw text-info ml-3" />
            </div>
          </v-card-actions>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn dark type="submit" depressed color="#43c9d0" @click="updateEvent(selectedEvent)">
              Update
            </v-btn>
          </v-card-actions>
        </v-card>
        <v-card v-else>
          <v-card-title class="headline">
            {{ selectedEvent.title }}
          </v-card-title>
          <v-card-text>
            {{ selectedEvent.start }} - {{ selectedEvent.type }}
          </v-card-text>
          <v-card-text v-if="selectedEvent.reason">
            Reason: {{ selectedEvent.reason }}
          </v-card-text>
          <v-card-text v-else>
            No reason
          </v-card-text>
          <v-card-text>Lunch: {{ selectedEvent.lunch }}</v-card-text>
        </v-card>
      </v-dialog>

    </v-app>
  </div>
</template>
<script>
import FullCalendar from '@fullcalendar/vue'
import dayGridPlugin from '@fullcalendar/daygrid'
import timeGridPlugin from '@fullcalendar/timegrid'
import interactionPlugin from '@fullcalendar/interaction'
import { StatisticUser } from '../../services'
import { VALID_TIME } from '@/.env'
import UserLunchService from '@/services/user-lunch'
import flatPickr from 'vue-flatpickr-component'
import 'flatpickr/dist/flatpickr.css'
import moment from 'moment'
import 'moment-lunar'

export default {
  name: 'fullcalendar',
  middleware: 'authen',
  components: {
    FullCalendar,
    flatPickr

  },
  data() {
    return {
      dialogAddLeave: false,
      lunch: true,
      leave: true,
      showDialog: false,
      calendarOptions: {
        themeSystem: 'bootstrap',
        plugins: [
          dayGridPlugin,
          timeGridPlugin,
          interactionPlugin // needed for dateClick
        ],
        headerToolbar: {
          left: 'prev,next today',
          center: 'title',
          right: 'dayGridMonth,timeGridWeek,timeGridDay'
        },
        contentHeight: 800,
        events: [],
        eventClick: this.handleEventClick
      },
      selectedEvent: {},
      title: '',
      content: '',
      eventsLunch: [],
      eventsLeave: [],
      items: [
        {
          text: 'Homepage',
          to: { name: 'Index' }
        },
        {
          text: 'User Calendar',
          active: true
        }
      ],
      today: new Date().toISOString().substr(0, 10),
      focus: new Date().toISOString().substr(0, 10),
      name: 'Lunch',
      details: null,
      start: null,
      is_set_veggie: false,
      has_veggie: false,
      end: null,
      color: '#20B2AA', // default event color
      currentlyEditing: null,
      selectedElement: null,
      selectedOpen: false,
      events: [],
      dialog: false,
      dialogDate: false,
      startDate: new Date().toISOString().substr(0, 10),
      endDate: null,
      dialogDetail: false,
      lunarDay: []
    }
  },
  head() {
    return {
      title: 'User Calendar'
    }
  },
  async mounted() {
    this.lunarDays()
    await this.getLeave()
    await this.getEvents()
  },
  methods: {
    lunarDays() {
      let calendarApi = this.$refs.fullCalendar.getApi()
      let dateCalendar = calendarApi.getDate()
      let year = dateCalendar.getFullYear()
      let month = dateCalendar.getMonth()
      for (let day = 0; day < 32; day++) {
        let lunarDate = moment().year(year).month(month).date(day).lunar().format('DD')
        if (lunarDate == 15 || lunarDate == 1) {
          let date = `${year}-${month + 1}-${day}`
          let nextDate = `${year}-${month + 1}-${day + 1}`
          if (month < 9) {
            date = `${year}-0${month + 1}-${day}`
            nextDate = `${year}-0${month + 1}-${day + 1}`
            if (day == 31) {
              nextDate = `${year}-0${month + 2}-01`
            }
            if (day < 10) {
              date = `${year}-0${month + 1}-0${day}`
              nextDate = `${year}-0${month + 1}-0${day + 1}`
            }
          } else {
            if (day == 31) {
              nextDate = `${year}-${month + 2}-01`
            }
            if (day < 10) {
              date = `${year}-${month + 1}-0${day}`
              nextDate = `${year}-${month + 1}-0${day + 1}`
            }
          }
          let dataDay = {
            start: date,
            end: nextDate,
            display: 'background',
            backgroundColor: '#45BCF3'
          }
          this.lunarDay.push(dataDay)
        }
      }
    },
    checkBox() {
      if (this.leave === false && this.lunch === true) {
        this.calendarOptions.events = this.eventsLunch.concat(this.lunarDay)
      } else if (this.leave === false && this.lunch === false) {
        this.calendarOptions.events = this.lunarDays
      } else if (this.leave === true && this.lunch === false) {
        this.calendarOptions.events = this.eventsLeave.concat(this.lunarDay)
      } else {
        this.calendarOptions.events = this.eventsLeave.concat(this.eventsLunch, this.lunarDay)
      }
    },
    async getLeave() {
      await StatisticUser.getStatisticUser()
        .then((res) => {
          this.eventsLeave = res
        })
        .catch((e) => {
          error({
            statusCode: 404,
            message: 'Post not found'
          })
        })
    },
    async getEvents() {
      this.eventsLunch = await this.$store.dispatch('event/getUserLunches')
      this.calendarOptions.events = this.eventsLeave.concat(this.eventsLunch, this.lunarDay)
      this.eventsLunch.map(e => {
        if (e.has_veggie === true) {
          this.is_set_veggie = true
        }
      })
    },
    handleEventClick(e) {
      this.selectedEvent = this.calendarOptions.events.find(item => item.id === e.event.id)
      this.dialogDetail = true
    },
    async deleteMany() {
      const data = {
        date: this.today
      }
      const userLunch = await UserLunchService.deleteMany(data)
      await this.getEvents()
      if (userLunch && userLunch.data.msg) {
        const content = userLunch.data.msg
        this.$toast.success(content, { position: 'top-right' })
        this.is_set_veggie = false

      }
    },
    async setVeggieLunch() {
      const date = new Date()
      const hours = date.getHours()
      if (hours > VALID_TIME) {
        return this.$toast.error('Out of time to set lunch', { position: 'top-right' })
      }
      const data = {
        date: this.today
      }
      const userLunch = await UserLunchService.setVeggieMonth(data)
      const contentRes = 'Not found lunar day'

      await this.getEvents()
      if (userLunch && userLunch.data.msg !== contentRes) {
        const content = userLunch.data.msg
        this.$toast.success(content, { position: 'top-right' })
        this.is_set_veggie = true
      }
      if (userLunch && userLunch.data.msg === contentRes) {
        this.$toast.error(contentRes, { position: 'top-right' })
      }
    },
    async cancelSetVeggieLunch() {
      const date = new Date()
      const hours = date.getHours()
      if (hours > VALID_TIME) {
        return this.$toast.error('Out of time to set lunch', { position: 'top-right' })
      }
      const data = {
        date: this.today
      }
      const userLunch = await UserLunchService.cancelSetVeggieMonth(data)
      await this.getEvents()
      if (userLunch && userLunch.data.msg) {
        const content = userLunch.data.msg
        this.$toast.success(content, { position: 'top-right' })
      }
      this.is_set_veggie = false
    },
    async setToday() {
      const date = new Date()
      const hours = date.getHours()
      if (hours > VALID_TIME) {
        return this.$toast.error('Out of time to set lunch', { position: 'top-right' })
      }
      this.focus = this.today
      const data = {
        name: this.name,
        has_veggie: this.has_veggie || false,
        date: this.today
      }
      const userLunch = await UserLunchService.create(data)
      await this.getEvents()
      if (userLunch && userLunch.data.date) {
        this.$toast.success('You have setted lunch for today', { position: 'top-right' })
      }
      if (userLunch && userLunch.data.error_msg) {
        this.$toast.error(userLunch.data.error_msg, { position: 'top-right' })
      }
    },
    async addEvent() {
      this.dialog = false
      const date = new Date()
      const hours = date.getHours()
      const today = moment()
      if (this.startDate > this.endDate) {
        return this.$toast.error('End day must be greater than start day', { position: 'top-right' })
      }
      if (this.startDate < this.today) {
        return this.$toast.error('Start day must be greater than today', { position: 'top-right' })
      }
      if (this.startDate === this.today && hours > VALID_TIME) {
        this.startDate = moment(today).add(1, 'days')
      }
      if (this.startDate && this.endDate) {
        const data = {
          name: this.name,
          has_veggie: this.has_veggie,
          list_dates: this.getDatesUnRange(this.startDate, this.endDate)
        }

        const userLunches = await UserLunchService.createMany(data)
        await this.getEvents()
        if (userLunches && userLunches.data.msg) {
          const content = userLunches.data.msg
          this.$toast.success(content, { position: 'top-right' })
        }
      } else {
        alert('You must enter start and end time')
      }
    },
    editEvent(ev) {
      this.currentlyEditing = ev.id
    },
    async updateEvent(ev) {
      const date = new Date()
      const hours = date.getHours()
      if (hours > VALID_TIME && ev.start <= this.today) {
        return this.$toast.error('Out of time to set lunch', { position: 'top-right' })
      }
      const data = {
        has_veggie: ev.has_veggie
      }
      await UserLunchService.update({
        data,
        id: ev.id
      })
      this.currentlyEditing = null
      await this.getEvents()
      this.dialogDetail = false
    },
    async deleteEvent(ev) {
      const date = new Date()
      const hours = date.getHours()
      if (hours > VALID_TIME && ev.start <= this.today) {
        return this.$toast.error('Out of time to set lunch', { position: 'top-right' })
      }
      const userLunch = await UserLunchService.delete(ev.id)
      await this.getEvents()
      if (userLunch && userLunch.data.msg) {
        const content = userLunch.data.msg
        this.$toast.success(content, { position: 'top-right' })
      }
      this.dialogDetail = false
    },
    getDatesUnRange(startDate, stopDate) {
      const dateArray = []
      let currentDate = moment(startDate)
      let endDate = moment(stopDate)
      while (currentDate <= endDate) {
        dateArray.push(moment(currentDate).format('YYYY-MM-DD'))
        currentDate = moment(currentDate).add(1, 'days')
      }
      return dateArray
    }
  }
}
</script>
<style lang="scss" scoped>
@import "./style.scss";
</style>

