import BaseService from '../base'

class UserService extends BaseService {
    get entity() {
        return 'user'
    }

    getAllProjectManager() {
        const pms = this.request({ auth: true }).get(`${this.entity}/get_project_managers`)
        return pms
    }

    getAllRoles() {
        const roles = this.request({ auth: true }).get(`${this.entity}/title`)
        return roles
    }

}

export default new UserService()
