<template>
  <div id="app" class="ml-5 mr-5">
    <v-app id="inspire">
      <v-card outlined class="m-4">
        <v-data-table
          :headers="headers"
          :items="desserts"
          sort-by="budget"
          hide-default-footer
          class="table-striped"
          :page.sync="page"
          :items-per-page="itemsPerPage"
          :search="search"
          @page-count="pageCount = $event"
        >
          <template v-slot:top>
            <v-toolbar flat color="white" class="m-3">
              <h2>Manage Lunch</h2>
              <v-spacer></v-spacer>
              <v-col cols="3">
                <v-text-field
                  v-model="search"
                  append-icon="mdi-magnify"
                  label="Search"
                  placeholder="Search name or date"
                  single-line
                  hide-details
                ></v-text-field>
              </v-col>
              <router-link to="/create-lunch" class="btn font-weight-bold text-left bg-info text-light">
                <font-awesome-icon class="fa-fw" :icon="['fas', 'utensils']" />
                CHOOSE PROVIDER LUNCH
              </router-link>
              <v-dialog v-model="dialog" max-width="500px">
                <v-card>
                  <v-card-title>
                    <span class="headline">{{ formTitle }}</span>
                  </v-card-title>
                  <v-card-text>
                    <v-container>
                      <v-row>
                        <v-col cols="12" sm="6" md="4">
                          <v-text-field v-model="editedItem.note" label="note"></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="6" md="4">
                          <v-text-field v-model="editedItem.date" label="date"></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="6" md="4">
                          <label class="container row font-weight-bold col-5 pr-0 justify-content-between">
                            <font-awesome-icon :icon="['fas', 'leaf']" class="text-success fa-fw" />
                            Veggie
                            <input v-model="editedItem.has_veggie" type="checkbox" class="form-check-input" />
                            <span id="veggiecheckmark" class="checkmark" />
                          </label>
                        </v-col>
                      </v-row>
                    </v-container>
                  </v-card-text>
                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn depressed color="#6c757d" text @click="close">Cancel</v-btn>
                    <v-btn depressed color="blue darken-1" text @click="save()">Save</v-btn>
                  </v-card-actions>
                </v-card>
              </v-dialog>
            </v-toolbar>
          </template>
          <template v-slot:item.has_veggie="{ item }">
            <td v-if="item.has_veggie">Yes</td>
            <td v-else>No</td>
          </template>
          <template v-slot:item.actions="{ item }">
            <v-icon
              title="Remove this"
              small
              color="green darken-2"
              class="mr-2"
              @click="editItem(item)"
            >
              mdi-pencil
            </v-icon>
            <v-icon
              title="Remove this"
              small
              color="red darken-2"
              @click="deleteItem(item)"
            >
              mdi-delete
            </v-icon>
          </template>
          <template v-slot:item.lunch="{ item }">
            <v-icon
              title="Remove this"
              small
              @click="deleteItem(item)"
              color="teal darken-2"
            >
              mdi-plus
            </v-icon>
          </template>
          <template v-slot:item.date="{ item }">
            <v-chip :color="getColor(item.date)" dark>{{ item.date }}</v-chip>
          </template>
        </v-data-table>
      </v-card>
      <div class="text-center pt-2">
        <v-pagination color="#43c9d0" v-model="page" :length="pageCount"></v-pagination>
      </div>
    </v-app>
  </div>
</template>
<script>
import LunchService from '../../services/lunch'

export default {
  middleware: 'authen',
  data: () => ({
    dialog: false,
    page: 1,
    pageCount: 0,
    itemsPerPage: 10,
    search: '',
    headers: [
      {
        text: 'Provider',
        value: 'provider.name',
      },
      {
        text: 'Note',
        value: 'note',
      },
      {
        text: 'Date',
        value: 'date',
      },
      {
        text: 'Has Veggie',
        value: 'has_veggie',
      },
      {
        text: 'Actions',
        value: 'actions',
        sortable: false,
      }
    ],
    desserts: [],
    editedIndex: -1,
    editedItem: {
      provider: '',
      note: '',
      date: ''
    },
    defaultItem: {
      provider: '',
      note: '',
      date: ''
    }
  }),

  computed: {
    formTitle() {
      return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
    }
  },

  watch: {
    dialog(val) {
      val || this.close()
    }
  },

  created() {
    this.getLunches()
  },

  methods: {
    async getLunches() {
      const lunches = await LunchService.get()
      this.desserts = lunches.data
    },

    editItem(item) {
      this.editedIndex = this.desserts.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.dialog = true
    },

    async deleteItem(item) {
      const index = this.desserts.indexOf(item)
      confirm('Are you sure you want to delete this item?') && this.desserts.splice(index, 1) &&
      await LunchService.delete(item.id)
    },

    close() {
      this.dialog = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
    },

    async save() {
      if (this.editedIndex > -1) {
        Object.assign(this.desserts[this.editedIndex], this.editedItem)
        const data = {
          date: this.editedItem.date,
          note: this.editedItem.note,
          has_veggie: this.editedItem.has_veggie
        }
        const lunch = await LunchService.update({
          id: this.editedItem.id,
          data
        })
        if (lunch && lunch.data.msg) {
          const content = lunch.data.msg
          this.$toast.error(content, { position: 'top-right' })
        }
        if (lunch && lunch.data.note) {
          const content = 'Updated successfully!'
          this.$toast.success(content, { position: 'top-right' })
        }
      } else {
        this.desserts.push(this.editedItem)
      }
      this.close()
    },
    getColor(date) {
      return 'teal'
    }
  }
}
</script>
<style lang="scss" scoped>
