<template>
  <div>
    <div class="position-absolute sticky-top mt-2 mybcrumb">
      <b-breadcrumb :items="items"></b-breadcrumb>
    </div>
    <v-app class="ml-5 mr-5">
      <v-card outlined="true" class="m-4">
        <v-card-title>
          <v-row>
            <v-col cols="4"><h2>Leave management in month</h2></v-col>
            <v-spacer></v-spacer>
            <v-col cols="3">
              <v-text-field
                append-icon="mdi-magnify"
                label="Search"
                single-line
                hide-details
                v-model="search"
                placeholder="Search name or email"
                dense
                v-on:keyup.enter="searchWithNameOrEmail()">
              </v-text-field>
            </v-col>
            <v-col cols="2">
              <date-picker v-model="date" type="month" placeholder="Select date" />
            </v-col>
            <v-col cols="2">
              <download-excel
                class="bg-info text-center rounded text-light"
                :fetch="fetchDataForExportExcel"
                :fields="json_fields_detail"
                worksheet="My Worksheet"
                :name="year.toString()+ '-' + month.toString() + '.xls'">
                <small>Export Excel</small>
              </download-excel>
            </v-col>
          </v-row>
        </v-card-title>
        <v-data-table
          :headers="headers"
          :items="data"
          :items-per-page="count"
          class="table-striped"
          hide-default-footer
        >
          <template v-slot:item="props">
            <tr>
              <td class="specialClass">{{ props.item.name }}</td>
              <td class="specialClass">{{ props.item.email }}</td>
              <td>{{ props.item.working_days }}</td>
              <td>{{ props.item.leave_days_number }}
                <a v-if="props.item.leave_days_number" class="btn text-info"
                   @click="getDetailData(props.item.id, 'leave_days')">
                  <font-awesome-icon :icon="['fas', 'info-circle']" />
                </a>
              </td>
              <td>{{ props.item.OT_days_number }}
                <a v-if="props.item.OT_days_number" class="btn text-info "
                   @click="getDetailData(props.item.id, 'OT_days')">
                  <font-awesome-icon :icon="['fas', 'info-circle']" />
                </a>
              </td>
              <td>{{ props.item.night_days_number }}
                <a v-if="props.item.night_days_number" class="btn text-info "
                   @click="getDetailData(props.item.id, 'night_days')">
                  <font-awesome-icon :icon="['fas', 'info-circle']" />
                </a>
              </td>
              <td>{{ props.item.remote_days_number }}
                <a v-if="props.item.remote_days_number" class="btn text-info "
                   @click="getDetailData(props.item.id, 'remote_days')">
                  <font-awesome-icon :icon="['fas', 'info-circle']" />
                </a>
              </td>
              <td>{{ props.item.insurance_days_number }}
                <a v-if="props.item.insurance_days_number" class="btn text-info "
                   @click="getDetailData(props.item.id, 'insurance_days')">
                  <font-awesome-icon :icon="['fas', 'info-circle']" />
                </a>
              </td>
              <td>{{ props.item.unpaid_days_number }}
                <a v-if="props.item.unpaid_days_number" class="btn text-info "
                   @click="getDetailData(props.item.id, 'unpaid_days')">
                  <font-awesome-icon :icon="['fas', 'info-circle']" />
                </a>
              </td>
              <td>{{ props.item.current_days_off }}</td>
              <td>{{ props.item.annual_leave_last_year }}</td>
            </tr>
          </template>
        </v-data-table>
      </v-card>
      <v-pagination v-if="dialogPagination" color="#43c9d0" v-model="page" :length="pageCount"></v-pagination>
    </v-app>
    <v-dialog
      v-model="dialog"
      width="800"
      class="color"
    >
      <v-card>
        <v-card-title class="headline color mb-6">Detail</v-card-title>
        <v-card-text>
          <v-data-table
            :headers="headersDetail"
            :items="detailData"
            hide-default-footer
            class="elevation-1 "
          >
            <template v-slot:item="props">
              <tr>
                <td>{{ detailData.indexOf(props.item) + 1 }}</td>
                <td>{{ props.item.date }}</td>
                <td>{{ props.item.type }}</td>
                <td v-if=" props.item.lunch">Yes</td>
                <td v-else>No</td>
                <td>{{ props.item.type_off_title }}</td>
              </tr>
            </template>
          </v-data-table>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="#43c9d0"
            text
            @click="dialog = false"
          >
            Close
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
import AdminStatisticService from '../../services/leavedaystatistic'
import DatePicker from 'vue2-datepicker'

export default {
  name: 'LeaveDayStatistic',
  components: { DatePicker },
  data() {
    return {
      date: new Date().toISOString().substr(0, 7),
      menu: false,
      count: 0,
      search: '',
      dialog: false,
      data: [],
      detailData: [],
      month: new Date().getMonth() + 1,
      year: new Date().getFullYear(),
      page: 1,
      pageCount: 0,
      dialogPagination: true,
      nightWorking: [],
      json_fields_detail: {
        'Name': 'name',
        'Night 1 (Date)': 'Ok',
        'Night 2 (Date)': 'Ok',
        'Team Lead/ Sr': 'Sr',
        'Jr': 'Jr',
        'Total (Night)': 'night_days_number'
      },
      json_fields: {
        'Name': 'name',
        'Email': 'email',
        'Working days in month': 'working_days',
        'Remain leaves': 'current_days_off',
        'Number of leave days': 'leave_days_number',
        'Leave days in month': {
          field: 'leave_days',
          callback: (value) => {
            return this.getCallBack(value)
          }
        },
        'Number of OT days': 'OT_days_number',
        'OT days in month': {
          field: 'OT_days',
          callback: (value) => {
            return this.getCallBack(value)
          }
        },
        'Number of night days': 'night_days_number',
        'Night days in month': {
          field: 'night_days',
          callback: (value) => {
            return this.getCallBack(value)
          }
        },
        'Number of remote days': 'remote_days_number',
        'Remote days in month': {
          field: 'remote_days',
          callback: (value) => {
            return this.getCallBack(value)
          }
        },
        'Number of insurance days': 'insurance_days_number',
        'Insurance days in month': {
          field: 'insurance_days',
          callback: (value) => {
            return this.getCallBack(value)
          }
        },
        'Number of unpaid days': 'unpaid_days_number',
        'Unpaid days in month': {
          field: 'unpaid_days',
          callback: (value) => {
            return this.getCallBack(value)
          }
        },
        'Remaining annual leave': 'current_days_off'
      },
      items: [
        {
          text: 'Homepage',
          to: { name: 'Index' }
        },
        {
          text: 'Calendar',
          to: { name: 'calendar' }
        },
        {
          text: 'Leave Day Statistic',
          active: true
        }
      ],
      headers: [
        {
          text: 'Name',
          value: 'name'
        },
        {
          text: 'Email',
          sortable: false,
          value: 'email'
        },
        {
          text: 'Working days',
          value: 'working_days'
        },
        {
          text: 'Leave days',
          value: 'leave_days'
        },
        {
          text: 'OT days',
          value: 'OT_days_number'
        },
        {
          text: 'Night days',
          value: 'night_days_number'
        },
        {
          text: 'Remote days',
          value: 'remote_days_number'
        },
        {
          text: 'Insurance leave days',
          value: 'insurance_days_number'
        },
        {
          text: 'Unpaid leave days',
          value: 'unpaid_days_number'
        },
        {
          text: 'Remaining annual leave',
          value: 'current_days_off'
        },
        {
          text: 'Annual leave last year',
          value: 'current_days_off'
        }
      ],
      headersDetail: [
        {
          text: 'STT',
          value: 'stt'
        },
        {
          text: 'Date',
          value: 'date'
        },
        {
          text: 'Time',
          value: 'type'
        },
        {
          text: 'Lunch',
          value: 'lunch'
        },
        {
          text: 'Type of leave',
          value: 'date_off_number'
        }
      ]
    }
  },
  watch: {
    page() {
      this.getData()
    },
    month() {
      this.getData()
    },
    date() {
      this.getData(this.date)
    }
  },
  created() {
    this.getData()
    this.getNightWorking()
  },
  methods: {
    getCallBack(value) {
      const data = []
      for (let i = 0; i < value.length; i++) {
        let detailData = `${i + 1}. ${value[i].date} - ${value[i].type}`
        if (value[i].lunch) {
          detailData = detailData + ' - Have eat lunch'
        } else {
          detailData = detailData + ' - Don\'t have eat lunch'
        }
        data.push(detailData)
      }
      return data
    },
    async getNightWorking(){
      const data = await AdminStatisticService.get(this.month, this.year, 1, this.count, '', '')
      let results = data.data.results
      for(let i = 0; i<results.length; i++){
        for(let j=0; j<results[i].night_days.length;j++){
          let data = results[i].night_days[j].date
        }
      }
    },
    async fetchDataForExportExcel() {
      const data = await AdminStatisticService.get(this.month, this.year, 1, this.count, '', '')
      return data.data.results
    },
    allowedYears(val) {
      if (parseInt(val.split('-')[0], 10) > new Date().getFullYear()) {
        return false
      }
      return true
    },
    validateEmail(email) {
      const re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
      return re.test(String(email).toLowerCase())
    },
    async searchWithNameOrEmail() {
      if (this.search === '') {
        return
      }
      let data = []
      this.dialogPagination = false
      if (this.validateEmail(this.search)) {
        data = await AdminStatisticService.get(this.month, this.year, 1, this.count, '', this.search)
      } else {
        data = await AdminStatisticService.get(this.month, this.year, 1, this.count, this.search, '')
      }
      this.data = data.data.results
    },
    async getData(date) {
      if (date) {
        this.month = parseInt(date.slice(5, 7))
        this.year = parseInt(date.slice(0, 4))
        this.page = 1
      }
      const data = await AdminStatisticService.get(this.month, this.year, this.page, 50, '', '')
      this.data = data.data.results
      this.pageCount = data.data.page_number
      this.count = data.data.count
    },
    getDetailData(id, detail) {
      this.dialog = true
      this.detailData = this.data.find(date => date.id === id)[detail]
    }
  },
  head() {
    return {
      title: 'Leave Day Statistic'
    }
  }
}
</script>
