<template>
  <div>
    <div class="position-absolute sticky-top mt-2 mybcrumb">
      <b-breadcrumb :items="items"></b-breadcrumb>
    </div>
    <div class="m-4">
      <div v-if="admin === 'true'" id="id_list_table" class="table-responsive p-3">
        <div class="float-right">
          <download-excel
            class="btn btn-info ml-3 px-2"
            :fetch="fetchDataForExportExcel"
            :fields="json_fields"
            worksheet="My Worksheet"
            title="Click to export excel"
            name="user.xls">
            <font-awesome-icon :icon="['fas', 'file-export']" />
            Export file
          </download-excel>
          <router-link to="/invite" class="btn btn-info pr-3 ml-3 mr-4">
            <font-awesome-icon :icon="['fas', 'user-plus']" />
            <span class="ml-3">Invite</span>
          </router-link>
        </div>
      </div>
      <div class="mr-4 ml-4">
        <FilterBox @getData="getData" @loadData="loadData" :page_size="this.page_size" ref="filter" />
        <table class="table table-bordered table-striped table-rounded table-fixed table-responsive">
          <thead>
          <tr>
            <th class="firstrow" scope="col">
            <span class="btn text-white text-uppercase font-weight-bold">
              Avatar
            </span>
            </th>
            <th scope="col">
              <button class="btn text-white text-uppercase font-weight-bold">
                Name
              </button>
            </th>
            <th scope="col">
              <button class="btn text-white text-uppercase font-weight-bold">
                Email
              </button>
            </th>
            <th scope="col">
              <button class="btn text-white text-uppercase font-weight-bold">
                Title
              </button>
            </th>
            <th scope="col">
            <span class="btn text-white text-uppercase font-weight-bold">
              Gender
            </span>
            </th>
            <th scope="col">
            <span class="btn text-white text-uppercase font-weight-bold">
              Birthday
            </span>
            </th>
            <th scope="col">
            <span class="btn text-white text-uppercase font-weight-bold">
              Phone
            </span>
            </th>
            <th scope="col">
            <span class="btn text-white text-uppercase font-weight-bold">
              Team
            </span>
            </th>
            <th scope="col">
            <span class="btn text-white text-uppercase font-weight-bold">
              Join date
            </span>
            </th>
          </tr>
          </thead>
          <tbody>
          <tr v-for="(row, index) in employeesData.rows" :key="index">
            <td scope="row" class="text-center">
              <router-link :to="'/profile/' + row.id">
                <img class="img-fluid" :src="row.profile.image" v-if="row.profile.image !== null" />
                <img class="img-fluid" src="../../static/images/icon.png" v-else />
              </router-link>
            </td>
            <td scope="row" :style="tablewidth">
              <router-link :to="'/profile/' + row.id" title="Click to move to Profile page">
                <strong>{{ row.profile.name }}</strong>
              </router-link>
            </td>
            <td scope="row">
              <a class="btn text-muted" :href="'mailto:'+row.profile.email" title="Click to send mail"> {{ row.email
                }}</a>
            </td>
            <td scope="row" :style="tablewidth">
              <strong>{{ row.title }}</strong>
            </td>
            <td scope="row" :style="tablewidth">
              <strong v-if="GENDERS[row.profile.gender] !== undefined">{{ GENDERS[row.profile.gender].text }}</strong>
            </td>
            <td scope="row" :style="tablewidth">
              <strong>{{ row.profile.birth_day }}</strong>
            </td>
            <td scope="row" class="text-center text-muted" :style="tableteamwidth">
              <strong>{{ row.profile.phone }}</strong>
            </td>
            <td scope="row" :style="tableteamwidth" class="px-4">
              <router-link
                v-for="team in row.profile.teams"
                :to="'/team/' + team.id"
                class="font-weight-bold text-center d-block w-100 py-2 badge my-2 badge-pill"
                style="font-size:0.9em"
                :key="team.index"
                :class="{
                'badge-light text-dark ': parseInt(team.id) % 9 === 0,
                'badge-primary text-white ': parseInt(team.id) % 9 === 1,
                'badge-danger text-white ': parseInt(team.id) % 9 === 2,
                'badge-secondary text-white ': parseInt(team.id) % 9 === 3,
                'badge-success text-white ': parseInt(team.id) % 9 === 4,
                'badge-dark text-white ': parseInt(team.id) % 9 === 5,
                'badge-info text-white ': parseInt(team.id) % 9 === 6,
                'badge-warning text-dark ': parseInt(team.id) % 9 === 7,
                'badge-success text-dark ': parseInt(team.id) % 9 === 8
              }"
              >
                {{ team.name ? team.name : 'No team' }}
              </router-link>
            </td>
            <td scope="row" :style="tablewidth">
              <strong>{{ row.profile.join_date }}</strong>
            </td>
          </tr>
          </tbody>
        </table>
        <nav aria-label="Page navigation example" class="mt-3">
          <ul class="pagination justify-content-center">
            <li v-for="page in employeesData.totalPage" :key="page.index" class="page-item"
                :class="{ 'active': page === employeesData.currentPage }">
              <a @click="getDataFilterOption(page, page_size)" class="page-link btn">{{ page }}</a>
            </li>
          </ul>
        </nav>
      </div>
    </div>
  </div>
</template>

<script>
import FilterBox from '@/pages/employeelist/filter'
import InviteBox from '@/pages/employeelist/inviteBox'
import { GENDERS } from '@/const/genders'

const Cookie = process.client ? require('js-cookie') : undefined
import GetUserService from '../../services/user/getUser'
import ExportExcelService from '@/services/user/exportExcel'
import DatePicker from 'vue2-datepicker'
import 'vue2-datepicker/index.css'
import Vue from 'vue'
import moment from 'moment'

export default {
  components: {
    FilterBox,
    DatePicker,
    InviteBox
  },
  name: 'id_list_table',
  middleware: 'authen',
  data() {
    return {
      json_fields: {
        'Name': {
          field: 'profile',
          callback: (value) => {
            return `${value.name}`
          }
        },
        'Email': 'email',
        'Birth day': {
          field: 'profile',
          callback: (value) => {
            return `${value.birth_day}`
          }
        },
        'Phone': {
          field: 'profile',
          callback: (value) => {
            return `${value.phone}`
          }
        },
        'Join date': {
          field: 'profile',
          callback: (value) => {
            return `${value.join_date}`
          }
        },
        'Team': {
          field: 'profile',
          callback: (value) => {
            return this.getCallBack(value.teams)
          }
        }
      },
      bus: new Vue(),
      admin: localStorage.getItem('is_admin'),
      items: [
        {
          text: 'Homepage',
          to: { name: 'Index' }
        },
        {
          text: 'Employee List',
          active: true
        }
      ],
      borderradius: {
        borderRadius: '1.25rem',
        textAlign: 'center'
      },
      tablewidth: {
        width: '250px'
      },
      tableteamwidth: {
        width: '220px'
      },
      color: {
        color: '#25c9d0',
        fontSize: '40px',
        marginLeft: '16px'
      },
      text: {
        fontSize: '20px'
      },
      pageInfo: {
        pageNumber: '',
        previous: '',
        next: ''
      },
      errors: '',
      subject: '',
      content: '',
      page_size: 8,
      employeesData: {
        searching: false,
        rows: [],
        currentPage: 0,
        totalPage: 10
      }
    }
  },
  head() {
    return {
      title: 'Employee List'
    }
  },
  async created() {
    await this.getData(1, this.page_size)
  },
  computed: {
    GENDERS: function() {
      return GENDERS
    }
  },
  methods: {
    getCallBack(value) {
      const data = value.map(e => e.name)
      return data
    },
    async fetchDataForExportExcel() {
      let data = null
      if (this.employeesData.searching) {
        const searchData = this.$refs.filter.getSearchData()
        const joindate = this.format_date(searchData.joinDate)
        data = await ExportExcelService.search(searchData.name, searchData.email,
          searchData.birthdayMonth, joindate,
          searchData.gender, searchData.title, searchData.team, searchData.active)
      } else {
        data = await ExportExcelService.get()
      }
      return data.data
    },
    getDataFilterOption(page = 1, page_size = this.page_size) {
      if (this.employeesData.searching) {
        this.$refs.filter.search(page)
      } else {
        this.getData(page, page_size)
      }
    },
    async getData(page = 1, page_size = this.page_size) {
      let responseData = await GetUserService.get(page_size, page)
      if (responseData && responseData.data) {
        this.loadData(responseData, false)
      }
    },
    loadData(responseData, isSearching) {
      this.employeesData.searching = isSearching
      this.employeesData.rows = responseData.data.results
      this.employeesData.totalPage = responseData.data.page_number
      this.employeesData.currentPage = responseData.data.current
    },
    format_date(value) {
      if (value) {
        return moment(String(value)).format('YYYY-MM')
      }
    }
  }
}
</script>
<style lang="scss" scoped>
@import "index";
</style>
