import BaseService from './base'

class AdminStatisticService extends BaseService {
    get entity() {
        return 'workday/admin'
    }

    get(month, year, page, size, name, email) {
        const res = this.request({ auth: true }).get(`${this.entity}?m=${month}&y=${year}&page_size=${size}&page=${page}&n=${name}&e=${email}`)
        return res
    }

    create_annual_leave(){
        const res = this.request({auth: true}).post(`workday/remain_leave`)
        return res
    }

    check_annual_leave_next_year(){
        const res = this.request({auth: true}).get(`workday/check_annual_leave`)
        return res
    }

}

export default new AdminStatisticService()
