import axios from 'axios'

export const TypeOffServices = {
    getTypeOff,
    addTypeOff,
    handleActiveTypeoff,
    editTypeOff
};

function getTypeOff() {
    return axios.get('/workday/admin/types')
}

function addTypeOff(data) {
    return axios.post('/workday/admin/type/create', data)
}

function handleActiveTypeoff(id) {
    return axios.patch(`/workday/admin/type/edit/${id}`)
}

function editTypeOff(id, data){
    return axios.put(`/workday/admin/type/edit/${id}`, data)
}
