import axios from 'axios'

export const HolidayServices = {
    getHolidays,
    addHoliday,
    removeHoliday,
};

function getHolidays() {
    return axios.get('/workday/holidays')
}

function addHoliday(data) {
    return axios.post('/workday/holiday/create', data)
}

function removeHoliday(id) {
    return axios.delete(`/workday/holiday/delete/${id}`)
}
