<template>
  <div class="my-2 ma-auto calendar-main-content">
    <div class="position-absolute sticky-top mt-2 mybcrumb"><b-breadcrumb :items="items"></b-breadcrumb></div>
    <v-container>
      <v-row>
        <v-spacer></v-spacer>
        <router-link to="/leavedayuserstatistic" class="btn btn-info float-right pt-2 text-uppercase font-weight-bold">
          <font-awesome-icon :icon="['fas', 'chart-bar']" />
          Statistic
        </router-link>
      </v-row>
    </v-container>
    <v-container>
      <FullCalendar :options="calendarOptions"/>
    </v-container>
    <b-modal ref="mymodal" hide-footer hide-header="" v-model="showDialog">
      <div
        class="d-block text-center text-red pt-3"
        :class ="{
                'bg-morning': selectedEvent.type === 'Morning',
                'bg-afternoon': selectedEvent.type === 'Afternoon',
                'bg-full': selectedEvent.type === 'All day',
            }">
        <h3>{{ selectedEvent.title }}</h3>
        <span>{{ selectedEvent.type }}</span>
      </div>
    </b-modal>
  </div>
</template>
<script>
import FullCalendar from '@fullcalendar/vue'
import dayGridPlugin from '@fullcalendar/daygrid'
import timeGridPlugin from '@fullcalendar/timegrid'
import interactionPlugin from '@fullcalendar/interaction'
import { StatisticUser } from '../../services';
import Swal from "sweetalert2";
export default {
  name: 'UserCalendar',
  middleware: 'authen',
  components: {
    FullCalendar
  },
  created: function () {
    StatisticUser.getStatisticUser()
      .then((res) => {
        this.calendarOptions.events= res
      })
      .catch((e) => {
        error({ statusCode: 404, message: 'Post not found' })
      })
  },
  data() {
    return {
      showDialog: false,
      calendarOptions: {
        plugins: [
          dayGridPlugin,
          timeGridPlugin,
          interactionPlugin // needed for dateClick
        ],
        headerToolbar: {
          left: 'prev,next today',
          center: 'title',
          right: 'dayGridMonth,timeGridWeek,timeGridDay'
        },
        contentHeight: 800,
        events: [],
        eventClick: this.handleEventClick
      },
      selectedEvent: {},
      title: '',
      content: '',
      events: [],
      items: [
        {
          text: 'Homepage',
          to: { name: 'Index' }
        },
        {
          text: 'User Calendar',
          active: true
        }
      ]
    }
  },
  head () {
    return {
      title: 'User Calendar',
    }
  },
  methods:{
    handleEventClick(e) {
      this.selectedEvent = this.calendarOptions.events.find(item=>item.id===e.event.id)
      Swal.fire({
        title: this.selectedEvent.title,
        html:`<span>${this.selectedEvent.start}(${this.selectedEvent.type})</span> </br>`+
          `<p class="mt-3">${this.selectedEvent.reason? this.selectedEvent.reason:'No reason'}</p>`+
          `<p class="mt-3">Lunch: ${this.selectedEvent.lunch}</p>`
      })
    },
    hideModal() {
      this.$refs.mymodal.hide()
    },
  }
}
</script>
<style lang="scss" scoped>
@import "./style.scss";
</style>
