<template>
  <div>
    <div class="position-absolute sticky-top mt-2 mybcrumb">
      <b-breadcrumb :items="items"></b-breadcrumb>
    </div>
    <div id="id_leaves" class="main-leaves row p-3">
      <div class="col-sm-9 mx-auto">
        <h5 class="card-header text-white bg-info text-left py-4">
          <strong>Create request</strong>
        </h5>
        <form id="leaves" class="bg-white px-4 text-info mb-2" method="POST" @submit.prevent="handleClicktoModal('on')">
          <div v-if="this.error !== ''" class="text-danger text-center my-2">
            {{ this.error }}
          </div>
          <br>
          <div class="form-group row my-3">
            <label for="input_user_name" class="col-md-3 col-form-label">Name</label>
            <div class="col-md-8 ml-2">
              <input id="id_input_user_name" v-model="user_name" type="text" class="form-control"
                     placeholder="user_name" disabled/>
            </div>
          </div>
          <div class="form-group row my-3">
            <label for="input_endday" class="col-md-3 col-form-label">Available leave days</label>
            <div class="col-md-8 ml-2">
              <input id="id_input_endday" v-model="leave_day_number" type="text" class="form-control"
                     placeholder="Leave days remaining" disabled/>
            </div>
          </div>
          <div class="form-group row my-3">
            <label for="input_title" class="col-md-3 col-form-label">Types<sup class="text-danger">
              *</sup></label>
            <div class="col-md-8 ml-2">
              <select id="id_selected_titles" v-model="type_leave" type="text" class="form-control" required>
                <option v-for="type_off in list_type_off" :key="type_off.index" :value="type_off.id"
                        v-bind:disabled="leave_day_number===0 && type_off.add_sub_day_off===ADD_SUP_DAY_OFF">
                  {{ type_off.type === INSURANCE ? type_off.title + '( Insurance pay)' : type_off.title }}
                </option>
              </select>
            </div>
          </div>
          <div class="form-group row my-3">
            <label for="input_reason" class="col-md-3 col-form-label">Days<sup class="text-danger">
              *</sup></label>
            <div class="col-md-8 ml-1 pl-3 pr-2">
              <div class="border rounded text-secondary">
                <div class="md-form col-md-12 row d-flex justify-content-between">
                  <label class="text-info mt-3 ml-3">From<sup class="text-danger"> *</sup></label>
                  <flat-pickr
                      v-model="startday"
                      :config="{
                         altInput: true,
                         altFormat: 'd-m-Y',
                         minDate: this.join_date,
                         dateFormat: 'Y-m-d',
                         locale: { firstDayOfWeek: 1 },
                         disable: this.disable()
                         }"
                      class="form-control bg-white col-md-4 mt-2"
                      placeholder="Select date"
                      name="date"
                      @input="get_day()"
                  />
                  <label class="text-info mt-3 ml-3">To<sup class="text-danger"> *</sup></label>
                  <flat-pickr
                      v-model="endday"
                      :config="{
                          altInput: true,
                          altFormat: 'd-m-Y',
                          dateFormat: 'Y-m-d',
                          minDate: this.startday ? this.startday : this.today,
                          locale: { firstDayOfWeek: 1 },
                          disable: this.disable()
                          }"
                      class="form-control bg-white col-md-4 mt-2"
                      placeholder="Select date"
                      name="date"
                      @input="get_day()"
                  />
                </div>
                <div class="col-md-12 mt-2 pt-2 pb-2 border-top rounded text-secondary">
                  <span v-if="days.length == 0">No days off yet</span>
                  <div v-else>
                    <table class="table">
                      <thead class="thead-light">
                      <tr>
                        <th scope="col" class="stt">No</th>
                        <th scope="col" class="date">Date</th>
                        <th scope="col" class="type">Type</th>
                        <th scope="col" class="lunch">Lunch</th>
                        <th scope="col" class="action"></th>
                      </tr>
                      </thead>
                      <tbody v-for="(item, index) in days" :key="item.Startday">
                      <tr>
                        <td>{{ index + 1 }}</td>
                        <td>{{ format_date(item.date) }}</td>
                        <td>
                          <input
                              v-bind:id="'all-' + index"
                              v-bind:name=index
                              type="radio"
                              value="All day"
                              checked
                              @click="change_type_off(item, $event)">
                          <label v-bind:for="'all-' + index" class="mr-3"><span></span>All day</label>
                          <input
                              v-bind:id="'mor-' + index"
                              v-bind:name=index
                              type="radio"
                              value="Morning"
                              @click="change_type_off(item, $event)">
                          <label v-bind:for="'mor-' + index" class="mr-3"><span></span>Morning</label>
                          <input
                              v-bind:id="'aft-' + index"
                              v-bind:name=index
                              type="radio"
                              value="Afternoon"
                              @click="change_type_off(item, $event)">
                          <label v-bind:for="'aft-' + index"><span></span>Afternoon</label>
                        </td>
                        <td>
                          <div class="ml-5">
                            <input type="checkbox" class="form-check-input" v-on:click="change_lunch(item)">
                          </div>
                        </td>
                        <td>
                          <font-awesome-icon v-on:click="remove_day(item)" class="fa-fw" :icon="['fas', 'trash']"/>
                        </td>
                      </tr>
                      </tbody>
                    </table>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="form-group row my-3">
            <label for="input_reason" class="col-md-3 col-form-label">Reason<sup class="text-danger">
              *</sup></label>
            <div class="col-md-8 ml-2">
              <textarea id="id_input_reason" v-model="reason" form="leaves" class="form-control"
                        placeholder="Tell me your reasons" required/>
            </div>
          </div>
          <div class="row my-3" role="group">
            <label class="col-md-3"></label>
            <div class="col-md-8 ml-2 d-flex justify-content-end">
              <input class="btn btn-info borderform mx-2" type="submit" value="Send"/>
            </div>
          </div>
        </form>
        <b-modal ref="certification_modal" hide-footer title="Leave" class="font-weight-bold ">
          <div class="d-block pl-2 pr-2 pt-3">
            <div class="justify-content-between">
              <div><strong>User:</strong> {{ this.user_name }}</div>
              <br>
              <div><strong>Type:</strong> {{
                  this.list_type_off.find(item => item.id === this.type_leave) ? this.list_type_off.find(item => item.id === this.type_leave).title : ''
                }}
              </div>
              <br>
              <table class="table bg-white table-bordered ">
                <thead>
                <tr>
                  <th>Day off</th>
                  <th>Type</th>
                  <th>Lunch</th>
                </tr>
                </thead>
                <tbody v-for="item in days" :key="item.Startday">
                <tr>
                  <td>{{ format_date(item.date) }}</td>
                  <td>{{ item.type }}</td>
                  <td>{{ item.lunch ? "Yes" : "No" }}</td>
                </tr>
                </tbody>
              </table>
              <div><strong>Reason:</strong> {{ this.reason }}</div>
              <br>
            </div>
          </div>
          <div id="user-lunch-modal-bottom">
            <div id="modify-button">
              <div class="row">
                <b-button class="mt-3 mx-auto col-4" variant="danger" @click="handleClicktoModal()">Cancel</b-button>
                <b-button class="mt-3 mx-auto col-4" variant="info" @click="submit()">Send</b-button>
              </div>
            </div>
          </div>
        </b-modal>
      </div>
    </div>
  </div>
</template>
<script>
import {requestOffService} from '../../services';
import {FunctionalCalendar} from 'vue-functional-calendar'
import moment from 'moment'
import 'vue-cal/dist/vuecal.css'
import flatPickr from 'vue-flatpickr-component'
import 'flatpickr/dist/flatpickr.css'
import {mapMutations, mapGetters} from 'vuex'

export default {
  name: 'id_leaves',
  middleware: 'authen',
  components: {
    flatPickr,
    FunctionalCalendar
  },
  computed: {
    ...mapGetters({
      showNoti: 'sidebar/showNoti'
    }),
  },
  data() {
    return {
      INSURANCE: 1,
      ADD_SUP_DAY_OFF: 1,
      IS_OT: 1,
      days: [],
      fullPage: false,
      selected: 'No',
      checkbox_value: false,
      user_name: '',
      today: new Date().toISOString().slice(0, 10),
      startday: null,
      check: false,
      endday: null,
      join_date: null,
      type_leave: '',
      virtual_title: '',
      insurance: '',
      list_type_off: [],
      type_insurances: [],
      reason: '',
      leave_day_number: '',
      annual_leave_last_year: 0,
      current_days_off: 0,
      month : 2020,
      error: '',
      items: [
        {
          text: 'Homepage',
          to: {name: 'index'}
        },
        {
          text: 'Create request',
          active: true
        }
      ],
    }
  },
  head() {
    return {
      title: 'Create request'
    }
  },
  created: function () {
    requestOffService.getRetrieveDateStatistic()
        .then((res) => {
          this.current_days_off = res.data.current_days_off
          this.annual_leave_last_year = res.data.annual_leave_last_year
          this.user_name = res.data.profile.name
          this.join_date= new Date(res.data.profile.join_date).toISOString()
          this.month = res.data.month
          this.get_total_leave_days()
        })
        .catch((e) => {
          error({statusCode: 404, message: 'Post not found'})
        })
    requestOffService.getTypeOff().then(res => {
      this.list_type_off = [...res.data.filter(item => item.type !== this.INSURANCE), ...res.data.filter(item => item.type === this.INSURANCE)]
    })
  },
  beforeUpdate() {
    if (this.virtual_title !== '' && this.virtual_title !== this.type_leave) {
      this.days = []
      this.startday = null
      this.endday = null
      this.virtual_title = ""
    }
  },
  methods: {
    disable(){
      if(this.type_leave!=='') {
        if (this.list_type_off.find(item => item.id === this.type_leave) === this.list_type_off.find(item => item.is_ot_days === 0 && item.id === this.type_leave)){
          return [this.datePicker]
        }
        else if (this.list_type_off.find(item => item.id === this.type_leave) === this.list_type_off.find(item => item.is_ot_days === 1 && item.id === this.type_leave)){
          return [this.datePickerOT]
        }
        else{
          return [this.datePickerEnable]
        }
      }else
          return [this.datePickerUnEnable]
    },
    change_type_off(item) {
      let index = this.days.indexOf(this.days.find(i => i.date === item.date))
      this.days = [...this.days.slice(0, index), {...item, "type": event.target.value}, ...this.days.slice(index + 1)]
    },
    change_lunch(item) {
      let index = this.days.indexOf(this.days.find(i => i.date === item.date))
      this.days = [...this.days.slice(0, index), {...item, "lunch": !item.lunch}, ...this.days.slice(index + 1)]
    },
    remove_day: function (item) {
      const index = this.days.indexOf(this.days.find(i => i.date === item.date))
      if (index > -1) {
        this.days = [...this.days.slice(0, index), ...this.days.slice(index + 1)]
      }
    },
    submit: function (e) {
      let list_dates = []
      this.days.forEach(item => list_dates.push({...item, 'date': item.date.toISOString().slice(0, 10)}))
      const data = {
        'type_id': this.type_leave,
        'reason': this.reason,
        'date': list_dates
      }
      requestOffService.createRequestOff(data)
          .then((res) => {
            requestOffService.getRetrieveDateStatistic()
                .then((res) => {
                  this.current_days_off = res.data.current_days_off
                  this.annual_leave_last_year = res.data.annual_leave_last_year
                  this.user_name = res.data.profile.name
                  this.join_date= new Date(res.data.profile.join_date).toISOString()
                  this.month = res.data.month
                 this.get_total_leave_days()
                })
            this.startday = ''
            this.endtday = ''
            this.reason = ''
            this.checkbox_value = false
            this.selected = 'No'
            this.type_leave = ''
            this.virtual_title = ''
            this.days = []
            this.$nextTick(() => {
              this.$refs['certification_modal'].hide()
              const content = 'Your leave day request was sent successfully'
              this.$toast.success(content, {position: 'top-right'})
            })
            this.$router.push('/leavemanage')

          })
          .catch(e => {
            let error = 'Your leave day request was sent error'
            if(e.response.data.error!=='')
              error = e.response.data.error
            this.$nextTick(() => {
              this.$refs['certification_modal'].hide()
              this.$toast.error(error, {position: "top-right"})
            })
          })

    },
    datePicker: function (date) {
      return (date.getDay() === 0 || date.getDay() === 6)
    },

    datePickerOT: function (date) {
      return (date.getDay() !== 0 && date.getDay() !== 6)
    },
    datePickerUnEnable: function (date){
      return true
    },
    datePickerEnable: function (date){
      return false
    },
    get_total_leave_days(){
      if(new Date().getMonth() > this.month - 1|| this.annual_leave_last_year === 0 ){
        this.leave_day_number = `${this.current_days_off} days`
      }
      else this.leave_day_number = `${this.current_days_off +this.annual_leave_last_year} days (${this.current_days_off} days this years + ${this.annual_leave_last_year} days remain last year)`
    },
    get_day() {
      this.days = []
      if(new Date(this.startday).getMonth() < this.month - 1&& this.annual_leave_last_year >0 ){
        this.leave_day_number = `${this.current_days_off +this.annual_leave_last_year} days (${this.current_days_off} days this years + ${this.annual_leave_last_year} days remain last year)`
      }else  this.leave_day_number = `${this.current_days_off} days`
      if (this.startday !== null && this.endday !== null) {
        this.virtual_title = this.type_leave
        let endday = new Date(this.endday)
        let start = new Date(this.startday)
        if (this.type_leave && this.list_type_off.find(item => item.id === this.type_leave) !== this.list_type_off.find(item => item.is_ot_days === this.IS_OT)) {
          for (; start <= endday;) {
            if (start.getDay() !== 0 && start.getDay() !== 6) {
              const date = new Date(start.getFullYear(), start.getMonth(), start.getDate(), 12, 0)
              this.days.push({"date": date, "type": 'All day', "lunch": false})
            }
            start.setDate(start.getDate() + 1)
          }
        } else {
          for (; start <= endday;) {
            if (start.getDay() === 0 || start.getDay() === 6) {
              const date = new Date(start.getFullYear(), start.getMonth(), start.getDate(), 12, 0)
              this.days.push({"date": date, "type": 'All day', "lunch": false})
            }
            start.setDate(start.getDate() + 1)
          }
        }
      }
    },
    get_number_for_days_off() {
      let number = 0
      this.days.forEach(item => {
        if (item.type === 'All day')
          number += 1
        else number += 0.5
      })
      return number
    },
    handleClicktoModal(key) {
      this.check = true
      this.error = ''
      if (key === 'on') {
        if (this.days.length === 0) {
          this.check = true
          this.error = 'No days off yet'
        } else {
          this.check = false
        }
      }
      if (!this.check) {
        this.$refs['certification_modal'].show()
      }
      this.$refs['certification_modal'].hide()
    },
    format_date(value) {
      if (value) {
        return moment(String(value)).format("MMM Do YY");
      }
    }
  }
}
</script>

<style lang="scss" scoped>
@import "./style.scss";
</style>
