import BaseService from '../base'


class ContactService extends BaseService {
    get entity() {
        return 'contact'
    }

    create(id, data) {
        const contact = this.request({ auth: true }).post(`user/${this.entity}/${id}/add`, data)
        return contact
    }

    get(id) {
        const contact = this.request({ auth: true }).get(`user/${this.entity}/${id}`)
        return contact
    }

    update(id, data) {
        const contact = this.request({ auth: true }).put(`user/${this.entity}/${id}`, data)
        return contact
    }

    delete(id) {
        return this.request({ auth: true }).delete(`user/${this.entity}/${id}`)
    }

}

export default new ContactService()
