import axios from 'axios'

export const requestOffService = {
    getTypeOff,
    getRetrieveDateStatistic,
    createRequestOff
};

function getTypeOff() {
    return axios.get('/workday/type')
}

function getRetrieveDateStatistic() {
    return axios.get(`/workday/retrieve_date`)
}

function createRequestOff(data) {
    return axios.post(`/workday/request/create`, data)
}
