<template>
  <div class="m-5">
    <h5 class="card-header text-white bg-info text-left py-4">
      <strong>Employee social insurance</strong>
      <span class="float-right">
        <font-awesome-icon v-if="userInsurance.social_insurance_code"
                           :icon="['fas', 'edit']"
                           v-on:click="toggleEditing"/>
        <font-awesome-icon v-else :icon="['fas', 'plus']"
                           v-on:click="toggleEditing"/>
      </span>
    </h5>
    <form class="bg-white p-4 text-info">
      <div v-if="userInsurance.social_insurance_code">
        <div class="row">
          <label class="col-sm-3">Social insurance code</label>
          <div class="col-sm-8 ml-2">
            <p class="text-dark">
              {{ userInsurance.social_insurance_code }}
            </p>
          </div>
        </div>
        <div class="row">
          <label class="col-sm-3">Start date</label>
          <div class="col-sm-8 ml-2">
            <p class="text-dark">
              {{ userInsurance.start_date }}
            </p>
          </div>
        </div>
      </div>
    </form>
    <b-modal
      ref="create_insurance_modal"
      @ok="saveInsurance()"
      :ok-disabled="this.$v.$invalid"
      ok-title="Confirm"
      ok-variant="info"
    >
      <slot>
        <h5 class="text-center">
          <b-badge class="p-3 w-100">
            Input your social insurance information
          </b-badge>
        </h5>
        <div class="col-sm-12 mx-auto row my-4">
          <form id="id_invite_form" class="bg-white p-4 text-info">
            <div
              class="form-group row"
              :class="{
                'form-group--error': formData.social_insurance_code.$error
              }"
            >
              <label class="col-sm-5 col-form-label"
                >Social insurance code<sup class="text-danger"> *</sup></label
              >
              <div class="col-sm-7 pt-2">
                <input
                  v-model="formData.social_insurance_code"
                  type="text"
                  class="form-control row"
                />
                <div
                  class="error"
                  v-if="!$v.formData.social_insurance_code.required"
                >
                  Field is required
                </div>
              </div>
            </div>
            <div class="form-group row">
              <label class="col-sm-5 col-form-label"
                >Start date<sup class="text-danger"> *</sup></label
              >
              <div class="col-sm-7 pt-2">
                <input
                  v-model="formData.start_date"
                  type="date"
                  class="form-control row"
                />
              </div>
            </div>
          </form>
        </div>
      </slot>
    </b-modal>
  </div>
</template>

<script>
import InsuranceService from '../../../services/profile/insurance.js'
import { required } from 'vuelidate/lib/validators'
import '../../../assets/scss/main.scss'
export default {
  data: () => ({
    userId: '',
    admin: localStorage.getItem('is_admin'),
    selected: '',
    formData: {
      social_insurance_code: '',
      issue_date: null
    },
    keysToDelete: ['user', 'id', 'create_at', 'delete_at'],
    userInsurance: {}
  }),
  validations: {
    formData: {
      social_insurance_code: {
        required
      }
    }
  },
  created(){
    this.userId = this.$route.params.id
    this.getData()
  },
  methods: {
    toggleEditing() {
      this.showForm()
    },
    showForm() {
      this.$refs.create_insurance_modal.show()
    },
    async getData() {
      this.userInsurance = (await InsuranceService.get(this.userId)).data
      this.formData = { ...this.userInsurance }
      this.keysToDelete.forEach((key) => delete this.formData[key])
    },
    async saveInsurance() {
      try {
        this.userInsurance = !this.userInsurance.social_insurance_code
          ? (await InsuranceService.create(this.userId, this.formData)).data
          : (await InsuranceService.update(this.userId, this.formData)).data
      } catch (error) {
        if (error.response && error.response.data)
          this.$emit('openSnack', error.response.data, 'error')
      }
    }
  },
}
</script>
