<template>
  <v-app>
    <v-card outlined >
      <v-data-table
        data-app
        fixed-header
        height="640px"
        :loading="loadingMyRequest"
        :page.sync="pageRequest"
        :items-per-page="itemsPerPageRequest"
        :headers="headersRequest"
        :items="dessertsRequest"
        hide-default-footer
        class="table-striped fixed-header"
        sort-by=""
      >
        <template v-slot:top>
          <v-card flat color="white">
            <v-card-title>
              <v-row>
                <v-col cols="12" sm="6" md="4">
                  <v-row>
                    <v-col cols="12">
                      <v-text-field
                        ref="searchRequest"
                        v-model="searchRequest"
                        append-icon="mdi-magnify"
                        @keyup.enter="filterDateMyRequest"
                        placeholder="Search with user, reason"
                        hide-details
                        dense
                      ></v-text-field>
                    </v-col>
                  </v-row>
                </v-col>
                <v-col cols="12" sm="6" md="7">
                  <v-row>
                    <v-col cols="12" sm="6" md="4">
                      <flat-pickr
                        v-model="dateMyRequest"
                        :config="{
                                            altInput: true,
                                            altFormat: 'Y-m-d',
                                            dateFormat: 'Y-m-d',
                                            locale: { firstDayOfWeek: 1 },
                                        }"
                        class="form-control bg-white"
                        name="date"
                        required
                      />
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <b-form-select
                        ref="typeFilterMyRequest"
                        v-model="typeFilterMyRequest"
                        :options="itemSelect"
                        text-field="state"
                        value-field="abbr"
                        @change="filterDateMyRequest()"
                        label="Select type filter"
                        outlined
                        dense
                      >
                        <template v-slot:first>
                          <b-form-select-option :value="null" disabled>-- Please select type filter --</b-form-select-option>
                        </template>
                      </b-form-select>
                    </v-col>
                    <v-col data-app cols="12" sm="6" md="4">
                      <b-form-select
                        ref="statusFilterMyRequest"
                        v-model="statusFilterMyRequest"
                        :options="itemStatus"
                        text-field="state"
                        value-field="abbr"
                        @change="filterDateMyRequest()"
                        label="Select status request"
                        outlined
                        dense
                      >
                        <template v-slot:first>
                          <b-form-select-option :value="null" disabled>-- Please select status request --</b-form-select-option>
                        </template>
                      </b-form-select>
                    </v-col>

                  </v-row>
                </v-col>
                <v-col cols="12" sm="6" md="1">
                  <v-row>
                    <v-col cols="12">
                      <v-btn
                        color="#43c9d0"
                        class="font-weight-bold"
                        depressed
                        dark
                        @click="refreshDataMyRequest()"
                      >
                        Refresh
                      </v-btn>
                    </v-col>
                  </v-row>
                </v-col>

              </v-row>
            </v-card-title>
            <v-spacer></v-spacer>
          </v-card>
        </template>
        <template v-slot:item="row">
          <tr>
            <td scope="row" align="center">
              {{ row.item.profile.name }}
            </td>
            <td scope="row" align="center">
              <div class="container-fluid">
                <div v-for="item in row.item.date_off">
                      {{ item.date }} ({{ item.type }})
                </div>
              </div>
            </td>
            <td scope="row" align="center">
              {{ row.item.type_off.title }}
              <span v-show="row.item.type_off.type === 1">( Insurance )</span>
            </td>
            <td scope="row" align="center">
              {{ row.item.reason }}
            </td>
            <td scope="row" align="center">
              <a
                @click="
                        rejected_reason(
                          row.item.status,
                          row.item.request_detail
                        )
                      "
                class="text-white font-weight-bold text-center d-block w-100 py-2 badge badge-pill"
                :class="{
                        'badge-danger':
                          row.item.status === 'Rejected'
                            ? true
                            : row.item.status === 'Cancel'
                            ? true
                            : false,
                        'badge-info': row.item.status === 'Approved',
                        'badge-secondary': row.item.status === 'Passed',
                        'badge-success':
                          row.item.status === 'Pending'
                            ? true
                            : row.item.status === 'Forwarded'
                            ? true
                            : false
                      }"
              >
                {{ row.item.status }}
              </a>
            </td>
            <td scope="row" align="center">
              <button
                v-show="
                        row.item.status !== 'Cancel' &&
                          row.item.status !== 'Rejected'
                      "
                title="Cancel"
                class="btn btn-danger py-2  btn-sm"
                @click.prevent="withdraw(row.item)"
              >
                <font-awesome-icon
                  class="text-white fa-fw"
                  :icon="['fas', 'undo']"
                />
              </button>
            </td>
          </tr>
        </template>
      </v-data-table>
    </v-card>
    <v-pagination
      v-model="pageRequest"
      color="#43c9d0"
      :length="pageCountRequest"
    ></v-pagination>
  </v-app>
</template>

<script>
import LoadingDialog from '../../components/LoadingDialog.vue'
import flatPickr from 'vue-flatpickr-component'
import {
  actionRequest, getManagementRequestOff,
  getMyRequest,
  getRequestByAdmin
} from '../../services/managementLeave.service'
import { mapGetters } from 'vuex'

export default {
  name: 'RequestAdminManagement',
  mounted() {

  },
  components: {
    LoadingDialog,
    flatPickr,
  },
  data() {
    return {
      loadingMyRequest: false,
      typeFilterMyRequest: null,
      statusFilterMyRequest: null,
      itemSelect: [
        {
          state: 'All',
          abbr: ''
        },
        {
          state: 'Day',
          abbr: 'day'
        },
        {
          state: 'Month',
          abbr: 'month'
        },
        {
          state: 'Year',
          abbr: 'year'
        }
      ],
      itemStatus: [
        {
          state: 'All',
          abbr: ''
        },
        {
          state: 'Pending',
          abbr: 'Pending'
        },
        {
          state: 'Forward',
          abbr: 'Forwarded'
        },
        {
          state: 'Rejected',
          abbr: 'Rejected'
        },
        {
          state: 'Canceled',
          abbr: 'Cancel'
        },
        {
          state: 'Approved',
          abbr: 'Approved'
        }
      ],
      dateMyRequest: new Date().toISOString().substr(0, 10),
      menuMyRequest: false,
      pageMyRequest: 1,
      desserts: [],
      editedIndex: -1,
      actionItem: {
        action: '',
        request_off: {
          id: '',
          profile: {
            name: ''
          }
        },
        Date: 0,
        Reason: 0,
        Status: 0,
        comment: ''
      },
      defaultItem: {
        action: '',
        request_off: {
          profile: {
            name: ''
          }
        },
        Date: 0,
        Reason: 0,
        Status: 0,
        comment: ''
      },
      pageRequest: 1,
      pageCountRequest: 0,
      dialogRequest: false,
      itemsPerPageRequest: 10,
      searchRequest: '',
      headersRequest: [
        {
          text: 'User',
          align: 'center',
          sortable: false,
          value: 'profile.name',

        },
        {
          text: 'Date',
          value: 'date_off',
          align: 'center',

        },
        {
          text: 'Type off',
          align: 'center',
          value: 'type_off.title',

        },
        {
          text: 'Reason',
          align: 'center',
          value: 'reason',

        },
        {
          text: 'Status',
          align: 'center',
          value: 'status',

        },
        {
          text: 'Actions',
          align: 'center',
          value: 'actions',
          sortable: false,

        }
      ],
      dessertsRequest: [],
      editedIndexRequest: -1,
      actionItemRequest: {
        action: '',
        id: '',
        profile: {
          name: ''
        },
        Date: 0,
        Reason: 0,
        Status: 0,
        comment: ''
      },
      defaultItemRequest: {
        action: '',
        request_off: {
          profile: {
            name: ''
          }
        },
        Date: 0,
        Reason: 0,
        Status: 0,
        comment: ''
      },

      reasonDialog: false,
      reasonReject: '',
      loadingDialog: false,
      myRequests: [],
      pageNumberRequest: 1,
      pageCurrentRequest: 1,
      items: [
        {
          text: 'Homepage',
          to: { name: 'Index' }
        },
        {
          text: 'Leave Management',
          active: true
        }
      ],
      is_admin: localStorage.getItem('is_admin'),
      profileId: localStorage.getItem('profile_id')
    }
  },
  async created() {
    await this.asyncDataActive()
  },
  watch: {
    date() {
      if (this.date && this.typeFilter) {
        this.filterDate()
      }
    },
    dateMyRequest() {
      if (this.dateMyRequest && this.typeFilterMyRequest) {
        this.filterDateMyRequest()
      }
    },
    pageRequest() {
      this.filterDateMyRequest()
    }
  },
  methods: {
    async asyncDataActive() {
      this.loadingActive = true
      let response = await getRequestByAdmin()

      if (response && response.status === 200 && response.data.results) {
        this.pageCountRequest = response.data.page_number
        this.dessertsRequest = response.data.results
      }
      this.loadingActive = false
    },


    close() {
      this.dialog = false
      this.$nextTick(() => {
        this.actionItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
    },

    async refreshDataMyRequest() {
      await this.asyncDataActive()
      this.searchRequest = ''
      this.typeFilterMyRequest = ''
      this.statusFilterMyRequest = ''
      this.dateMyRequest = new Date().toISOString().substr(0, 10)
    },
    rejected_reason(check, comments) {
      if (check === 'Rejected') {
        this.reasonDialog = true
        for (var index in comments) {
          if (comments[index]) {
            this.reasonReject = comments[index]
          }
        }
        if (!this.reasonReject) {
          this.reasonReject = 'No reason'
        }
      }
    },
    async withdraw(item) {
      if(!confirm('Are you sure you want to cancel request?')) {
        return
      }
      this.editedIndexRequest = this.dessertsRequest.indexOf(item)
      this.actionItemRequest = Object.assign({}, item)

      let form_data = new FormData()
      form_data.append('request_off_id', item.id)
      form_data.append('action', 'Cancel')
      this.loadingDialog = true
      let respone = await actionRequest(form_data)
      if (respone.status === 201 && respone.data) {
        this.$set(this.dessertsRequest, this.editedIndexRequest, respone.data)
        this.$toast.success('Success')
      } else {
        this.$toast.error('Error: ' + respone)
      }
      this.comment = ''
      this.loadingDialog = false
    },
    async filterDate() {
      this.page = 1
      await this.filter()
    },
    async filterDateMyRequest() {
      let data = this.dateMyRequest.split('-')
      let response = ''
      this.loadingMyRequest = true
      switch (this.typeFilterMyRequest) {
        case 'day':
          response = await getRequestByAdmin(this.pageRequest, 10, data[0], data[1], data[2], this.statusFilterMyRequest, this.searchRequest)
          break
        case 'month':
          response = await getRequestByAdmin(this.pageRequest, 10, data[0], data[1], '', this.statusFilterMyRequest, this.searchRequest)
          break
        case 'year':
          response = await getRequestByAdmin(this.pageRequest, 10, data[0], '', '', this.statusFilterMyRequest, this.searchRequest)
          break
        default:
          response = await getRequestByAdmin(this.pageRequest, 10, '', '', '', this.statusFilterMyRequest, this.searchRequest)
      }
      if (response && response.status === 200 && response.data) {
        this.pageCountRequest = response.data.page_number
        this.dessertsRequest = response.data.results
      } else {
        this.pageRequest = 1
        this.dessertsRequest = response
        this.pageCountRequest = 0
      }
      this.loadingMyRequest = false
    },
  }
}
</script>

